import InPatient from './InPatient'
import OutPatientConsultation from './OutPatientConsultation'
import OutPatientProcedure from './OutPatientProcedure'
import Emergency from './Emergency'
import Reimbursement from './Reimbursement'
import MedicineAllowance from './MedicineAllowance'

// Store
import { useDispatch } from 'react-redux'

import ClaimTypes from './../../Constant/ClaimTypes'

let dispatch = null
const AutoFillInitializer = () => {
  dispatch = useDispatch()
  return ''
}
const AutoFill = ({ loa, claim_type }) => {
  const type = ClaimTypes[claim_type].other_name

  // Work Around for Member Names
  const [last_name, others] = loa.member_info.member_name.split(',')
  const first_middle = others.trim().split(' ')
  let given_name = ''
  let middle_name = ''
  switch (first_middle.length) {
    case 0:
      break
    case 1:
      given_name = first_middle.join(' ')
      break
    case 2:
      given_name = first_middle[0]
      middle_name = first_middle[1]
      break
    case 3:
      middle_name = first_middle.pop()
      given_name = first_middle.join(' ')
      break
  }

  let common = {
    loa_number: loa.loa_no,
    member_account_number: loa.member_id,
    member_last_name: last_name,
    member_first_name: given_name,
    member_middle_name: middle_name
  }
  switch (type) {
    case 'InPatient':
      InPatient({ common, loa, dispatch })
      break

    case 'OutPatientProcedure':
      OutPatientProcedure({ common, loa, dispatch })
      break

    case 'OutPatientConsultation':
      OutPatientConsultation({ common, loa, dispatch })
      break

    case 'Emergency':
      Emergency({ common, loa, dispatch })
      break

    case 'Reimbursement':
      Reimbursement({ common, loa, dispatch })
      break
    case 'MedicineAllowance':
      MedicineAllowance({ common, loa, dispatch })
      break
  }
}

export { AutoFill, AutoFillInitializer }
