import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  message: Joi.string().trim()
    .label('Escalation Message')
    .messages({ 'string.empty': `Please enter reason of escalation.` })
    .required(),
  assignee: Joi.string().trim()
    .messages({ 'string.empty': `Please select a name from the Assigned To field.` })
    .label('Assignee')
    .required()
}).options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
