import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'

// Pages of Batch Encoder
import Dashboard from './Pages/Dashboard/Dashboard'
import BundlePage from './Pages/BundlePage/BundlePage'
import NewBundlePage from './Pages/NewBundlePage/NewBundlePage'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Store
import Store from './Store'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/'
  }
]

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  specificBundle: {
    text: data => data,
    to: data => `/bundles/${data}`
  },
  newBundle: {
    text: 'Create new bundle',
    to: '/bundles/create'
  }
}
function BatchEncoderRoutes () {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <React.Fragment>
      <Provider store={Store}>
        <Switch>
          <Route path='/' exact component={Dashboard} />
          <Route path='/bundles/:bundle_number/create' exact component={NewBundlePage} />
          <Route path='/bundles/:bundle_number' exact component={BundlePage} />
        </Switch>
      </Provider>
    </React.Fragment>
  )
}

export default BatchEncoderRoutes
