import React from 'react'
import Typography from '@material-ui/core/Typography'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Popover from '@material-ui/core/Popover'

// Components

// Styles
import classes from './DisapprovedLabel.module.scss'

export default function DisapprovedLabel(props) {
  // INIT
  const { reasons, ...other } = props

  // Breaking down props
  let { id } = props

  // VARIABLES
  const [anchorEl, setAnchorEl] = React.useState(null)

  // FUNCTIONS

  function generateMessage(results) {
    if (typeof results === 'string') {
      return '\u2022 ' + results
    } else if (typeof results === 'object') {
      let message = ''
      results.map((value, index) => {
        message += '\u2022 ' + value + '\n'
      })
      return message.substring(0, message.length - 1)
    }
  }

  const open = Boolean(anchorEl)
  id = open ? 'simple-popover' : id
  function handleClick(event) {
    setAnchorEl(event.currentTarget)
  }

  function handleClose() {
    setAnchorEl(null)
  }
  return (
    <React.Fragment>
      {reasons && reasons.length > 0 ? (
        <div className={`${classes[`root`]} ${reasons.length > 0 ? classes.disapproved : ''}`}>
          <div id={id} className={`${classes[`input-wrapper`]}`}>
            <Typography
              id={props.id}
              variant='h6'
              className={`${classes.label} ${classes['read-only-value']}`}
              {...other}
            >
              {props.value}
            </Typography>
            {reasons.length > 0 ? (
              <div id={`container-${id}`} className={classes['icon-container']}>
                <FontAwesomeIcon
                  id={`exc-circle-${id}`}
                  icon='exclamation-circle'
                  className={classes.icon}
                  onClick={handleClick} />
                <Popover
                  id={id}
                  open={open}
                  anchorEl={anchorEl}
                  onClose={handleClose}
                  anchorOrigin={{
                    vertical: 'center',
                    horizontal: 'right'
                  }}
                  transformOrigin={{
                    vertical: 'center',
                    horizontal: 'left'
                  }}
                >
                  <Typography style={{ whiteSpace: 'pre-line' }} className={classes.message}>
                    {generateMessage(reasons)}
                  </Typography>
                </Popover>
              </div>
            ) : (
                ''
              )}
          </div>
        </div>
      ) : (
          ''
        )}
    </React.Fragment>
  )
}
