let subscribers = {}

export default class ProtectedPageScrollEvent {
  static publish () {
    for (let key in subscribers) {
      if (subscribers[key]) subscribers[key]()
    }
  }

  static subscribe (name, cb) {
    setTimeout(() => (subscribers[name] = cb), 10)
  }

  static unsubscribe (name) {
    subscribers[name] = undefined
  }
}
