const initialValue = {
  diagnosis: {},
  procedure: {},
  hospital_item: {},
  professional_fee: {}
}

const errorReducer = (state = initialValue, action) => {
  switch (action.type) {
    case 'RESET_ERROR':
      return initialValue

    case 'SET_ERROR':
      return (() => {
        return {
          ...initialValue,
          ...state,
          ...action.payload
        }
      })()

    default:
      return state
  }
}

export default errorReducer
