export const initSuggestions = payload => {
  return {
    type: 'INIT_SUGGESTIONS',
    payload
  }
}

export const setSuggestionObject = payload => {
  return {
    type: 'SET_SUGGESTION_OBJECT',
    payload
  }
}

export const resetSuggestionObject = payload => {
  return {
    type: 'RESET_SUGGESTION_OBJECT',
    payload
  }
}
