function IndexedDbHelper () {}

/**
 * Deletes an IndexedDb database.
 *
 * @param {string} name database to be deleted
 * @param {function} callback function to be invoked
 */

IndexedDbHelper.deleteDb = function (name, callback) {
  var request = window.indexedDB.deleteDatabase(name)

  request.onerror = function (event) {
    // console.log("Error deleting database.");
    callback()
  }

  request.onsuccess = function (event) {
    // console.log("Database deleted successfully");
    callback()
  }
}

/**
 * Opens an IndexedDb database.
 *
 * @param {string} name IndexedDb database to create/open
 * @param {function} onUprade callback function to be invoked when database
 *            is to be created or when there is a need to upgrade
 * @param {function} onSuccess callback function to be invoked upon successfully opening database
 * @param {function} onError callback function to be invoked upon failure
 */
IndexedDbHelper.openDb = function (name, onUprade, onSuccess, onError) {
  let request = window.indexedDB.open(name)

  request.onerror = function (event) {
    console.error(event)
    if (onError) onError(event)
  }

  request.onsuccess = function (event) {
    let db = event.target.result
    if (onSuccess) onSuccess(db, event)
  }

  // Triggered when a new database is created or when the
  // version number of an existing database has been incremented
  request.onupgradeneeded = function (event) {
    // Save the IDBDatabase interface
    let db = event.target.result
    if (onUprade) onUprade(db, event)
  }
}

/**
 * Creates object store
 *
 * @param {object} db reference to database object
 * @param {string} name name of object store to be created
 * @param {string} keyPath unique key in object
 * @param {function} onComplete callback to be invoked upon object store creation
 */
IndexedDbHelper.createStoreOnDb = function (db, name, keyPath, onComplete) {
  // console.log('test', 'creating store ' + name);

  let store = db.createObjectStore(name, { keyPath: keyPath })
  if (onComplete) store.transaction.oncomplete = onComplete
  return store
}

/**
 * Creates index on an object store.
 *
 * @param {object} store reference to object store
 * @param {string} name name of index to be created
 * @param {boolean} isUnique whether index is unique or not
 */
IndexedDbHelper.createIndexOnStore = function (store, name, isUnique) {
  store.createObjectStore(name, { unique: isUnique })
}

/**
 * Opens an IndexedDb, opens an object store, and saves the given entries
 *
 * @param {string} dbName IndexedDb database to open
 * @param {string} storeName name of objectstore to save entries to
 * @param {array} entries to be saved to objectstore
 * @param {function} onSaveComplete callback function to be invoked upon save completion
 * @param {function} onLoopEnd callback function to be invoked upon finishing loop for entries
 */
IndexedDbHelper.openDbAndSave = function (dbName, storeName, entries, onSaveComplete, onLoopEnd) {
  IndexedDbHelper.openDb(
    dbName,

    function (db, event) {},

    function (db, event) {
      IndexedDbHelper.saveToStore(db, storeName, entries, onSaveComplete, onLoopEnd)
    },

    function (event) {
      console.error('error opening ' + dbName)
      console.error(event)
      if (onLoopEnd) onLoopEnd()
    }
  )
}

/**
 * Opens an IndexedDb, opens an object store, and saves the given entries
 *
 * @param {object} db IndexedDb database reference
 * @param {string} storeName name of objectstore to save entries to
 * @param {array} entries to be saved to objectstore
 * @param {function} onSaveComplete callback function to be invoked upon save completion
 * @param {function} onLoopEnd callback function to be invoked upon finishing loop for entries
 */
IndexedDbHelper.saveToStore = function (db, storeName, entries, onSaveComplete, onLoopEnd) {
  if (!entries || entries.length === 0) {
    if (onLoopEnd) onLoopEnd()

    return
  }

  let transaction = db.transaction([storeName], 'readwrite')

  transaction.oncomplete = function (event) {
    console.log('entries written to IndexedDb objectstore: ' + storeName + ' total: ' + entries.length)
    if (onSaveComplete) onSaveComplete()
  }

  let store = transaction.objectStore(storeName)

  entries.forEach(function (entry) {
    // console.log(entry)
    try {
      store.add(entry)
    } catch (ex) {}
  })

  if (onLoopEnd) onLoopEnd()
}

IndexedDbHelper.getAll = function (db, storeName, callback) {
  try {
    let objectStore = db.transaction(storeName, 'readonly').objectStore(storeName)
    objectStore.getAll().onsuccess = function (event) {
      callback(event.target.result)
    }
  } catch (ex) {
    let empty = []
    callback(empty)
  }
}

export { IndexedDbHelper }
