import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/be')

export default class BatchEncodingServices {
  static getBundles (params) {
    return axios({
      url: '/bundles',
      method: 'GET',
      params
    })
  }

  static getBundleCount (params) {
    return axios({
      url: `/bundles/count`,
      method: 'GET',
      params
    })
  }

  static getBundle ({ bundle_number }) {
    return axios({
      url: `/bundles/${bundle_number}`,
      method: 'GET'
    })
  }

  static createBundle () {
    return axios({
      url: `/bundles`,
      method: 'POST'
    })
  }

  static updateBundle ({ bundle_number, data }) {
    return axios({
      url: `/bundles/${bundle_number}`,
      method: 'PUT',
      data
    })
  }

  static submitBundle ({ bundle_number }) {
    return axios({
      url: `/bundles/${bundle_number}/submit`,
      method: 'PUT'
    })
  }

  static getBatches ({ bundle_number }) {
    return axios({
      url: `/bundles/${bundle_number}/batches`,
      method: 'GET'
    })
  }

  static updateBatch ({ bundle_number, batch_number, data }) {
    return axios({
      url: `/bundles/${bundle_number}/batches/${batch_number}`,
      method: 'PUT',
      data
    })
  }

  static createBatch ({ data, bundle_number }) {
    return axios({
      url: `/bundles/${bundle_number}/batches`,
      method: 'POST',
      data
    })
  }

  static deleteBatch ({ bundle_number, batch_number }) {
    return axios({
      url: `/bundles/${bundle_number}/batches/${batch_number}`,
      method: 'DELETE'
    })
  }

  static deleteMultipleBatch ({ bundle_number, data }) {
    return axios({
      url: `/bundles/${bundle_number}/batches`,
      method: 'DELETE',
      data
    })
  }

  static lookups ({ on, params }) {
    return axios({
      url: `/lookups/${on}`,
      method: 'GET',
      params: { limit: 10, ...params },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static isFacilityExists (params) {
    return axios({
      url: `/lookups/medical-partner/exists`,
      method: 'GET',
      params
    })
  }

  static isDoctorExists (params) {
    return axios({
      url: `/lookups/doctor-partner/exists`,
      method: 'GET',
      params
    })
  }
}
