const dropDownReducer = (state = {}, action) => {
  switch (action.type) {
    case 'INIT_DROP_DOWNS':
      return (() => {
        let s = {}
        for (let f of action.payload) {
          s[f] = []
        }

        return s
      })()

    case 'SET_DROP_DOWN':
      return { ...state, ...action.payload }

    default:
      return state
  }
}

export default dropDownReducer
