import React from 'react'
import Typography from '@material-ui/core/Typography'

// Components
import DatePicker from 'SharedComponents/DatePicker/DatePicker'

// Styles
import classes from './ContractRange.module.scss'

export default function ContractRange (props) {
  const { claim, error = {}, disableControls = false, onChange = () => {}, readOnly = false } = props

  return (
    <div className={classes['root']}>
      <div style={{ cursor: 'pointer' }} onClick={() => {}} className={classes['title']}>
        <Typography variant='subtitle1'>Contract Year:</Typography>
      </div>
      <div className={classes['input']}>
        <Typography variant='subtitle1' className={classes['label']}>
          From:
        </Typography>
        <DatePicker
          id='contract-date-from'
          required
          value={claim.contract_year_from}
          disabled={disableControls}
          onChange={event => onChange(event, 'contract_year_from')}
          error={error.contract_year_from}
          readOnly={readOnly}
        />
      </div>
      <div className={classes['input']}>
        <Typography variant='subtitle1' className={classes['label']}>
          To:
        </Typography>
        <DatePicker
          id='contract-date-to'
          required
          value={claim.contract_year_to}
          disabled={disableControls}
          onChange={event => onChange(event, 'contract_year_to')}
          error={error.contract_year_to}
          readOnly={readOnly}
        />
      </div>
    </div>
  )
}
