const initialState = {
  items: {},
  selected: []
}
const breadcrumbsReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SET_BREADCRUMBS_ITEMS':
      return {
        ...state,
        items: action.payload
      }

    case 'SET_SELECTED_BREADCRUMBS':
      return {
        ...state,
        selected: [
          { text: 'Home', to: '/' },
          ...action.payload.map(_item => {
            const [item, value] = _item.split(':')

            if (value) {
              const { text, to } = state.items[item]
              return {
                text: text(value),
                to: to(value)
              }
            } else {
              return state.items[item]
            }
          })
        ]
      }

    default:
      return state
  }
}

export default breadcrumbsReducer
