const initialState = {
  items: [],
  selected: {},
  batchNumber: '',
  show: false,
  action: '',
  saveControl: false
}
const batchReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SET_BATCH_ITEMS':
      return {
        ...state,
        items: action.payload
      }

    case 'SET_BATCH_ACTION':
      return {
        ...state,
        action: action.payload
      }

    case 'SET_SELECTED_BATCH':
      return {
        ...state,
        selected: { ...state.selected, ...action.payload }
      }

    case 'SET_SHOW_BATCH_FORM':
      return {
        ...state,
        show: action.payload
      }

    case 'SET_SAVE_BATCH_CONTROL':
      return {
        ...state,
        saveControl: action.payload
      }

    default:
      return state
  }
}

export default batchReducer
