import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Popover from '@material-ui/core/Popover'

// Components
import { Table, TableHeader, TableBody, TableRow, TableRowExpandable } from 'SharedComponents/CustomTable/CustomTable'
import { CurrencyFormatter } from 'Utilities/TextFormatter'
import HospitalItemForm from '../HospitalItemForm/HospitalItemForm'

// Styles
import classes from './HospitalItemList.module.scss'

// Store
import { useDispatch, useSelector } from 'react-redux'
import { setHospitalItem } from '../../Store/actions'

export default function HospitalItemList (props) {
  const {
    data,
    doValidate,
    permissions,
    id,
    showAmountPaidByMember = true,
    showPhilhealthRemarks = true,
    showAmountNotCovered = true,
    showAmountDueFromMember = true
  } = props
  const dispatch = useDispatch()
  const discrepancy = useSelector(state => state.discrepancy.hospital_item)
  const hospitalItem = useSelector(state => state.hospitalItem)
  const error = useSelector(state => state.error.hospital_item)

  const amountPerItem = i => {
    return (
      parseFloat(i.gross_amount) +
      parseFloat(i.vat_amount) -
      parseFloat(i.amount_not_covered) -
      parseFloat(i.philhealth_amount_paid) -
      parseFloat(i.amount_due_from_member) -
      parseFloat(i.amount_paid_by_member)
    )
  }

  const totalAmount = () => {
    let amount = 0
    for (let i of data) {
      amount += amountPerItem(i)
    }

    return CurrencyFormatter(amount)
  }

  // VARIABLES
  const [anchorEl, setAnchorEl] = React.useState(null)
  const [message, setMessage] = React.useState('')
  const open = Boolean(anchorEl)

  // FUNCTIONS
  const spreadResult = result => {
    if (result) {
      let message = ''
      let list = []
      if (typeof result === 'object') {
        if (Array.isArray(result)) {
          list = result.flat(Infinity)
        } else {
          for (const field in result) {
            if (result[field]) list.push(result[field].flat(Infinity))
          }
        }
      }
      const uniqOnly = Array.from(new Set(list))
      uniqOnly.map(m => m && (message += `\u2022 ${m} \n`))
      setMessage(message)
    }
  }

  function handleClickLineDiscrepancy ({ event, index }) {
    setAnchorEl(event.currentTarget)
    spreadResult(discrepancy[index]['line'])
  }

  function handleClose () {
    setAnchorEl(null)
  }

  const expand = args => {
    const { mode, index, details } = args
    dispatch(
      setHospitalItem({
        show: true,
        mode,
        index,
        details
      })
    )
  }

  const icon = () => {
    return (
      <Popover
        id={'simple-popover'}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'center',
          horizontal: 'right'
        }}
        transformOrigin={{
          vertical: 'center',
          horizontal: 'left'
        }}
      >
        <Typography style={{ whiteSpace: 'pre-line' }} className={classes.message}>
          {message}
        </Typography>
      </Popover>
    )
  }

  return (
    <Paper className={classes.root}>
      <div className={classes.table}>
        <div className={classes['table-header']}>
          <Typography variant='subtitle2' className={classes['table-description']}>
            Hospital Items
          </Typography>
          {permissions.create && (
            <Typography
              variant='body2'
              className={classes['add-link']}
              onClick={() => dispatch(setHospitalItem({ show: true, mode: 'add', index: 'add' }))}
            >
              + Add Items
            </Typography>
          )}
        </div>

        <Table>
          <TableHeader
            items={[
              { name: 'Item Name' },
              { name: 'Amount (PHP)', width: 100, align: 'right' },
              { name: '', width: 20, align: 'center' }
            ]}
          />
          <TableBody>
            {/* {console.group('Hospital List')} */}
            {/* {console.log('Discrepancy', discrepancy)} */}
            {/* {console.log('Errors', error)} */}
            {/* {console.log('Suggestions ', suggestions)} */}
            {/* {console.log('Data Collection ', data)} */}
            {/* {console.log('Status Collection ', hospitalItem)} */}
            {/* {console.groupEnd()} */}
            {data.map((i, index) => (
              <TableRow
                key={`${index} ${hospitalItem ? hospitalItem.show : ''}`}
                id={`hospitalItem-${index}`}
                className={`${classes['row']} ${discrepancy[index] && classes.discrepancy}`}
                expanded={hospitalItem.index === index ? hospitalItem.show : false}
              >
                <td id={`${id}-name-${index}`} onClick={() => expand({ mode: 'edit', index, details: i })}>
                  {i.name}
                </td>
                <td
                  id={`${id}-amount-${index}`}
                  className={`${classes.align} ${classes.right}`}
                  onClick={() => expand({ mode: 'edit', index, details: i })}
                >
                  {CurrencyFormatter(amountPerItem(i))}
                </td>
                <td id={`${id}-icon-${index}`} className={`${classes.align} ${classes.center}`}>
                  {discrepancy[index] && discrepancy[index]['line'] && (
                    <div className={classes['icon-container']}>
                      <FontAwesomeIcon
                        id={`${id}-exc-circle-${index}`}
                        icon='exclamation-circle'
                        className={classes.icon}
                        onClick={event => handleClickLineDiscrepancy({ event, index })}
                      />
                    </div>
                  )}
                </td>
                <TableRowExpandable>
                  <HospitalItemForm
                    index={index}
                    discrepancy={discrepancy[index]}
                    error={error.index === index ? error : {}}
                    doValidate={doValidate}
                    permissions={permissions}
                    showAmountPaidByMember={showAmountPaidByMember}
                    showPhilhealthRemarks={showPhilhealthRemarks}
                    showAmountNotCovered={showAmountNotCovered}
                    showAmountDueFromMember={showAmountDueFromMember}
                  />
                </TableRowExpandable>
              </TableRow>
            ))}
            {hospitalItem.mode === 'add' && (
              <TableRow
                key={`add ${hospitalItem ? hospitalItem.show : ''}`}
                id={`hospitalItem-add`}
                className={`${classes['row']}`}
                expanded={hospitalItem.mode === 'add' ? hospitalItem.show : false}
              >
                <td>{''}</td>
                <td className={`${classes.align} ${classes.right}`}>{''}</td>
                <td className={`${classes.align} ${classes.right}`}>{''}</td>
                <TableRowExpandable>
                  <HospitalItemForm
                    index={'add'}
                    error={error.index === 'add' ? error : {}}
                    doValidate={doValidate}
                    permissions={permissions}
                    showAmountPaidByMember={showAmountPaidByMember}
                    showPhilhealthRemarks={showPhilhealthRemarks}
                    showAmountNotCovered={showAmountNotCovered}
                    showAmountDueFromMember={showAmountDueFromMember}
                  />
                </TableRowExpandable>
              </TableRow>
            )}
          </TableBody>
        </Table>

        <div className={classes['table-total']}>
          <Typography variant='body2' className={classes.caption}>
            Running Total (PHP)
          </Typography>
          <Typography variant='body2' className={classes.amount}>
            {totalAmount(data)}
          </Typography>
        </div>
      </div>
      {message !== '' && icon()}
    </Paper>
  )
}
