import React from 'react'

// Components
import Table from 'SharedComponents/Table/Table'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import TextField from 'SharedComponents/TextField/CustomTextField'
import SearchTextComponent from 'SharedComponents/SearchTextComponent'
import Typography from '@material-ui/core/Typography'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'

// Utilities
import { compare, includesAny } from 'Utilities/ArrayCompare'

// Styles
import classes from './PartnerNetwork.module.scss'

const headCells = [
  { name: 'Code', sortField: 'id', search: true },
  { name: 'Name', sortField: 'name', search: true },
  { name: 'Location', sortField: 'address', search: true },
  // { name: 'Type', sortField: 'type', search: true },
  // { name: 'Groupings', sortField: 'groups', search: true },
  { name: '', sortField: 'affiliated_doctors', search: false }
]

const headCellsDoctors = [
  { name: 'Medical Partner ID', sortField: 'id', search: true },
  { name: 'Doctor Name', sortField: 'name', search: true },
  { name: 'Specialization', sortField: 'specialization', search: true },
  { name: 'Schedule', sortField: 'schedule', search: true },
  { name: 'Remarks', sortField: 'remarks', search: false }
]

export default function PartnerNetwork (props) {
  const { partnerService, doctorService, memberAccountNumber } = props
  const [partnerId, setPartnerId] = React.useState('')
  const [partnerName, setPartnerName] = React.useState('')
  const [partnerLocation, setPartnerLocation] = React.useState('')

  const [partners, setPartners] = React.useState({
    data: [],
    category: 'all',
    search: '',
    count: 0,
    page: 1,
    limit: 10,
    rowsPerPageOptions: [10, 15, 20],
    orderBy: 'name',
    order: true
  })

  const [doctors, setDoctors] = React.useState({
    show: false,
    facility_id: '',
    data: [],
    filtered: [],
    filters: {},
    count: 0,
    page: 1,
    limit: 10,
    rowsPerPageOptions: [10, 15, 20],
    orderBy: 'name',
    order: true
  })

  const [debounce, setDebounce] = React.useState(null)

  const fetchAll = () => {
    partnerService({
      member_account_no: memberAccountNumber,
      params: { limit: partners.limit, page: 1, sort: 'name|asc', search: '', category: 'all' }
    }).then(res => {
      const { data } = res.data
      setPartners(state => ({
        ...state,
        data: data.results,
        count: data.count,
        page: 1,
        orderBy: 'name',
        order: true
      }))
    })
  }

  const fetchDoctors = ({ facility_id, filters }) => {
    doctorService({
      member_account_no: memberAccountNumber,
      params: { limit: partners.limit, page: 1, sort: 'name|asc', search: '', category: 'all' },
      facility_id,
      filters
    }).then(res => {
      const { data } = res.data
      setDoctors(state => ({
        ...state,
        ...data,
        show: true,
        facility_id,
        data: data.results,
        filtered: data.results,
        filters
      }))
    })
  }

  const lodash = require('lodash')

  React.useEffect(() => {
    if (memberAccountNumber) fetchAll()
  }, [memberAccountNumber])

  React.useEffect(() => {
    let filtered = doctors.data
    for (const filter in doctors.filters) {
      filtered = filtered.filter(item => {
        if (typeof item[filter] !== 'object') {
          return includesAny(item[filter], doctors.filters[filter])
        } else {
          for (const field in item[filter]) {
            if (includesAny(item[filter][field], doctors.filters[filter])) return true
          }
        }
      })
    }
    filtered = lodash.sortBy(filtered, doctors.sortBy, doctors.sort ? 'asc' : 'desc')
    setDoctors(state => ({ ...state, filtered }))
  }, [doctors.filters])

  const queryColumn = (collection, { search, field }) => {
    if (collection === 'partners') {
      clearTimeout(debounce)
      setDebounce(
        setTimeout(() => {
          let searchObj = search
          let category = field
          if (field === 'id') {
            setPartnerId(search)
            if (partnerName || partnerLocation) {
              searchObj = {
                id: search,
                name: partnerName,
                address: partnerLocation
              }
              category = 'and'
            }
          }
          if (field === 'name') {
            setPartnerName(search)
            if (partnerId || partnerLocation) {
              searchObj = {
                id: partnerId,
                name: search,
                address: partnerLocation
              }
              category = 'and'
            }
          }
          if (field === 'address') {
            setPartnerLocation(search)
            if (partnerId || partnerName) {
              searchObj = {
                id: partnerId,
                name: partnerName,
                address: search
              }
              category = 'and'
            }
          }
          console.log(search, category)
          if (search.length >= 3 || (!search && category === 'and')) {
            partnerService({
              member_account_no: memberAccountNumber,
              params: {
                limit: partners.limit,
                page: partners.page,
                sort: `${partners.orderBy}|${partners.order ? 'asc' : 'desc'}`,
                search: searchObj,
                category: category
              }
            }).then(res => {
              const { data } = res.data
              setPartners(state => ({
                ...state,
                data: data.results,
                count: data.count,
                page: 1,
                orderBy: 'name',
                order: true
              }))
            })
          } else fetchAll()
        }, 500)
      )
    } else {
      setDoctors(state => ({ ...state, filters: { [field]: search } }))
    }
  }

  const handleOnSort = (collection, orderBy) => e => {
    if (collection === 'partners') {
      const _params = {
        ...partners,
        orderBy,
        order: !partners.order,
        sort: `${orderBy}|${partners.order ? 'asc' : 'desc'}`
      }
      partnerService({
        member_account_no: memberAccountNumber,
        params: _params
      }).then(res => {
        const { data } = res.data
        setPartners(state => ({
          ...state,
          ..._params,
          data: data.results,
          count: data.count
        }))
      })
    } else {
      const sorted = lodash.sortBy(doctors.filtered, doctors.sortBy, doctors.sort ? 'asc' : 'desc')
      setDoctors(state => ({ ...state, filtered: sorted }))
    }
  }

  const handleOnPaginate = (collection, params) => {
    if (collection === 'partners') {
      const _params = {
        category: partners.category,
        limit: partners.limit,
        page: partners.page,
        search: partners.search,
        sort: `${partners.orderBy}|${partners.order ? 'asc' : 'desc'}`,
        ...params
      }
      partnerService({
        member_account_no: memberAccountNumber,
        params: _params
      }).then(res => {
        const { data } = res.data
        setPartners(state => ({
          ...state,
          ..._params,
          data: data.results,
          count: data.count
        }))
      })
    } else {
    }
  }

  return (
    <div className={classes.root}>
      <div className={classes.header}>
        <div className={classes.search}>
          <SearchTextComponent
            id='search-partner'
            onChange={event => queryColumn('partners', { search: event.target.value, field: 'any' })}
            placeholder='Search for Partner Network Code, Name, and Location'
          />
        </div>
      </div>
      <div className={classes.table}>
        <Table
          TableContainerProps={{ style: { maxHeight: '500px', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {headCells.map(headCell => (
                <TableCell key={headCell.sortField}>
                  <TableSortLabel
                    active={partners.orderBy === headCell.sortField}
                    direction={partners.order ? 'asc' : 'desc'}
                    onClick={handleOnSort('partners', headCell.sortField)}
                  >
                    {headCell.name}
                  </TableSortLabel>
                  {headCell.search && (
                    <TextField
                      className={`${classes.search}`}
                      onChange={event =>
                        queryColumn('partners', { search: event.target.value, field: headCell.sortField })
                      }
                    />
                  )}
                </TableCell>
              ))}

              <TableCell align='center' />
            </TableRow>
          }
          body={partners.data.map((row, index) => (
            <TableRow key={`claim-${index}`} hover role='checkbox' tabIndex={-1}>
              <TableCell id={`pn-code-${index}`}>
                <div className={classes.cell}>
                  <span>{row.id}</span>
                </div>
              </TableCell>
              <TableCell id={`pn-name-${index}`}>
                <div className={classes.cell}>
                  <span>{row.name}</span>
                </div>
              </TableCell>
              <TableCell id={`pn-location-${index}`}>
                <div className={classes.cell}>
                  <span>
                    {[
                      row.address.unit_no,
                      row.address.floor,
                      row.address.building_no,
                      row.address.building_name,
                      row.address.street,
                      row.address.barangay,
                      row.address.district,
                      row.address.city,
                      row.address.region,
                      row.address.zip_code
                    ]
                      .filter(Boolean)
                      .join(' ')}
                  </span>
                </div>
              </TableCell>

              <TableCell id={`pn-view-link-${index}`}>
                <div className={classes.cell}>
                  <span>
                    <Typography
                      variant='caption'
                      className={`text-link `}
                      onClick={() => {
                        fetchDoctors({ facility_id: row.id, filters: [] })
                      }}
                    >
                      View Affiliated Doctors
                    </Typography>
                  </span>
                </div>
              </TableCell>
            </TableRow>
          ))}
          pagination
          count={partners.count}
          page={partners.page}
          limit={partners.limit}
          rowsPerPageOptions={partners.rowsPerPageOptions}
          onChange={args => handleOnPaginate('partners', args)}
        />
      </div>

      <Dialog
        fullWidth
        maxWidth='md'
        onClose={() => setDoctors(state => ({ ...state, show: false }))}
        aria-labelledby='customized-dialog-title'
        open={doctors.show}
        className={classes.doctors}
        PaperProps={{ style: { height: 'auto', display: 'flex' } }}
      >
        <DialogTitle disableTypography className={classes['header-container']}>
          <div className={classes.header}>
            <Typography variant='h6' className={classes.title}>
              Affiliated Doctors
            </Typography>
            <IconButton
              aria-label='close'
              className={classes.button}
              onClick={() => setDoctors(state => ({ ...state, show: false }))}
            >
              <CloseIcon />
            </IconButton>
          </div>
        </DialogTitle>

        <DialogContent dividers className={classes.content}>
          <div className={classes.table}>
            <Table
              TableContainerProps={{ style: { maxHeight: '500px', overflow: 'auto', position: 'relative' } }}
              TableProps={{ stickyHeader: true }}
              head={
                <TableRow>
                  {headCellsDoctors.map(cell => (
                    <TableCell key={cell.sortField}>
                      <TableSortLabel
                        active={doctors.orderBy === cell.sortField}
                        direction={doctors.order ? 'asc' : 'desc'}
                        onClick={handleOnSort('doctors', cell.sortField)}
                      >
                        {cell.name}
                      </TableSortLabel>
                      {cell.search && (
                        <TextField
                          className={`${classes.search}`}
                          onChange={event =>
                            queryColumn('doctors', { search: event.target.value, field: cell.sortField })
                          }
                        />
                      )}
                    </TableCell>
                  ))}

                  <TableCell align='center' />
                </TableRow>
              }
              body={
                doctors.filtered.length <= 0 ? (
                  <TableRow key={`claim-no-record`} hover role='checkbox' tabIndex={-1}>
                    <TableCell id={`no-record`} colSpan={5}>
                      <div className={`${classes.cell} ${classes.center}`}>
                        <span>No Records</span>
                      </div>
                    </TableCell>
                  </TableRow>
                ) : (
                  doctors.filtered.map((row, index) => (
                    <TableRow key={`claim-${index}`} hover role='checkbox' tabIndex={-1}>
                      <TableCell id={`doctor-id-${index}`}>
                        <div className={classes.cell}>
                          <span>{row.id}</span>
                        </div>
                      </TableCell>
                      <TableCell id={`doctor-name-${index}`}>
                        <div className={classes.cell}>
                          <span>{[row.name.first, row.name.middle, row.name.last].join(' ')}</span>
                        </div>
                      </TableCell>
                      <TableCell id={`doctor-specialization-${index}`}>
                        <div className={classes.cell}>
                          <span>
                            {[
                              row.specialization.main,
                              row.specialization.major,
                              row.specialization.sub,
                              row.specialization.other
                            ]
                              .filter(Boolean)
                              .join(', ')}
                          </span>
                        </div>
                      </TableCell>
                      <TableCell id={`doctor-schedule-${index}`}>
                        <div className={classes.cell}>
                          <span>{row.schedule}</span>
                        </div>
                      </TableCell>
                      <TableCell id={`doctor-remarks-${index}`}>
                        <div className={classes.cell}>
                          <span>{row.remarks}</span>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))
                )
              }
              pagination
              count={doctors.count}
              page={doctors.page}
              limit={doctors.limit}
              rowsPerPageOptions={doctors.rowsPerPageOptions}
              onChange={args => handleOnPaginate('doctors', args)}
            />
          </div>
        </DialogContent>
      </Dialog>
    </div>
  )
}
