function ClaimsSupervisorRoutes () {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <Switch>
      <Route path='/' exact component={Dashboard} />
      <Provider store={Store}>
        <Route
          path='/claims/:claim_number/analyze'
          exact
          render={props => (
            <ClaimsViewer
              breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'analyze']}
              {...props}
              permissions={forAdjudicationPermissions}
              ApiService={ClaimsVerifierSupervisorServices.ClaimsAdjudicationServices}
            />
          )}
        />

        <Route
          path='/claims/reconciled/:claim_number'
          exact
          render={props => (
            <ClaimsViewer
              breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'reconciled']}
              {...props}
              permissions={forReconciledPermissions}
              ApiService={ClaimsVerifierSupervisorServices.ReconciledServices}
            />
          )}
        />

        <Route
          path='/claims/for-approval/:claim_number'
          exact
          render={props => (
            <ClaimsViewer
              breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'forApproval']}
              {...props}
              permissions={forApprovalPermissions}
              ApiService={ClaimsVerifierSupervisorServices.ForApprovalServices}
            />
          )}
        />
      </Provider>
    </Switch>
  )
}

export default ClaimsSupervisorRoutes
