import React from 'react'

import ValueField from '../ValueField/ValueField'
import { Typography } from '@material-ui/core'

import { DigitFormatter, UpperWord } from 'Utilities/TextFormatter'
import { SafeAccess } from 'Utilities/SafeAccess'

import { UnitLimit } from '../../Utilities/FieldFormatter'

import classes from './Header.module.scss'

const populateClient = args => {
  const { showAll, benefitType, categoryNode } = args

  const metadata = SafeAccess(categoryNode, ['metadata'], {})

  let limits = []
  let index = -1
  let PECIndex = -1
  let timebasedindex = 0
  let timebased

  switch (benefitType) {
    case 'APE':
      return <React.Fragment />

    case 'CC':
      return <React.Fragment />

    case 'DNT':
      return <React.Fragment />

    case 'EC':
      // this one have sub form function, need further study
      return <React.Fragment />

    case 'EM':
      return <React.Fragment />

    case 'IP':
      return <React.Fragment />

    case 'MT':
      return <React.Fragment />

    case 'OP':
      return <React.Fragment />

    case 'OM':
      return <React.Fragment />

    case 'NT':
      return <React.Fragment />

    case 'PPE':
      return <React.Fragment />

    case 'PV':
      return <React.Fragment />

    case 'RBC':
      if (categoryNode) {
        limits = categoryNode['limits'] || {}
        index = limits.findIndex(item => ['ABL', 'MBL'].includes(item.type))
        PECIndex = limits.findIndex(item => ['PEC'].includes(item.type))
        if (limits[PECIndex] && limits[PECIndex]['time_based_limit']) {
          timebased = limits[PECIndex]['time_based_limit']
          timebasedindex = limits[PECIndex]['time_based_limit'].length
        }
      }
      let voluntaryValue = ''
      let involuntaryValue = ''

      const modeOfPayment = {
        annual: 'Annual',
        semiannual: 'Semi-Annual',
        quarterly: 'Quarterly',
        monthly: 'Monthly'
      }

      if (metadata['voluntary_upgrading_type']) {
        if (metadata['voluntary_upgrading_type'] === 'paid_by_member') {
          voluntaryValue = 'Room and Board difference is paid by the Member'
        } else if (
          metadata['voluntary_upgrading_type'] === 'first_hours_free' &&
          metadata['voluntary_upgrading_amount']
        ) {
          voluntaryValue = metadata['voluntary_upgrading_amount'] + ' hrs'
        } else if (metadata['voluntary_upgrading_type'] === 'rate' && metadata['voluntary_upgrading_amount']) {
          voluntaryValue = metadata['voluntary_upgrading_amount'] + ' %'
        }
      }
      if (metadata['involuntary_upgrading_type']) {
        if (metadata['involuntary_upgrading_type'] === 'paid_by_member') {
          involuntaryValue = 'Room and Board difference is paid by the Member'
        } else if (
          metadata['involuntary_upgrading_type'] === 'first_hours_free' &&
          metadata['involuntary_upgrading_amount']
        ) {
          involuntaryValue = metadata['involuntary_upgrading_amount'] + ' hrs'
        }
      }

      return (
        <React.Fragment>
          <div className={classes.groups}>
            <ValueField
              label={'Limit Type'}
              value={`${index > -1 && limits[index]['type'] ? limits[index]['type'] : ''}`}
            />
            <ValueField
              label={'Limit'}
              value={`${
                index > -1 && limits[index]['limit'] && limits[index]['limit_unit']
                  ? `${DigitFormatter(limits[index]['limit'])} ${UnitLimit(limits[index]['limit_unit'])}`
                  : ''
              }`}
            />
            <ValueField
              label={'PEC Limit Type'}
              value={`${PECIndex > -1 && limits[PECIndex]['type'] ? limits[PECIndex]['type'] : ''}`}
            />
            {PECIndex === -1 ||
            limits[PECIndex]['limit_unit'] === 'PHP' ||
            limits[PECIndex]['limit_unit'] === 'Percentage' ? (
                <ValueField
                  label={'PEC Limit'}
                  value={`${
                    PECIndex > -1 && limits[PECIndex]['limit'] && limits[PECIndex]['limit_unit']
                      ? `${DigitFormatter(limits[PECIndex]['limit'])} ${UnitLimit(limits[PECIndex]['limit_unit'])}`
                      : ''
                  }`}
                />
              ) : (
                <React.Fragment>
                  <Typography style={{ height: 26, paddingBottom: 6 }} htmlFor='limit_unit'>
                  Specify Limit
                  </Typography>
                  <Typography style={{ height: 26, paddingBottom: 5 }} htmlFor='limit_unit'>
                  After n month
                  </Typography>
                  {timebasedindex > 0 ? (
                    timebased.map((col, index) => (
                      <ValueField
                        label={''}
                        value={
                          limits[PECIndex]['time_based_limit'] && limits[PECIndex]['time_based_limit'][index]
                            ? limits[PECIndex]['time_based_limit'][index]['number_month'] &&
                            limits[PECIndex]['time_based_limit'][index]['number_month'] !== '' &&
                            limits[PECIndex]['time_based_limit'][index]['limit'] &&
                            limits[PECIndex]['time_based_limit'][index]['limit'] !== ''
                              ? // number of month
                              (limits[PECIndex]['time_based_limit'][index]['number_month'] &&
                              limits[PECIndex]['time_based_limit'][index]['number_month'] !== ''
                                ? limits[PECIndex]['time_based_limit'][index]['number_month'].toString()
                                : '') +
                              ' ' +
                              // day_based
                              (limits[PECIndex]['time_based_limit'][index]['day_based']
                                ? limits[PECIndex]['time_based_limit'][index]['day_based'].toString()
                                : 'Months') +
                              ' : ' +
                              // limit
                              (limits[PECIndex]['time_based_limit'][index]['limit'] &&
                              limits[PECIndex]['time_based_limit'][index]['limit'] !== ''
                                ? limits[PECIndex]['time_based_limit'][index]['limit'].toString()
                                : '') +
                              ' ' +
                              // limit type
                              (limits[PECIndex]['time_based_limit'][index]['limit_type']
                                ? limits[PECIndex]['time_based_limit'][index]['limit_type'] === 'PHP'
                                  ? 'PHP'
                                  : '%'
                                : '%')
                              : ''
                            : ''
                        }
                      />
                    ))
                  ) : (
                    <ValueField label={''} value={``} />
                  )}
                </React.Fragment>
              )}
            <ValueField label={'Mode of Payment'} value={modeOfPayment[metadata['mode_of_payment']]} />
          </div>
        </React.Fragment>
      )

    case 'SD':
      return <React.Fragment />

    case 'SH':
      return <React.Fragment />
  }
}

const populateMember = args => {}

export default function Header (props) {
  const { contractType } = props
  const [elemFields, setElemFields] = React.useState([])

  React.useEffect(() => {
    if (contractType === 'client') {
      setElemFields(populateClient(props))
    } else if (contractType === 'member') {
      setElemFields(populateMember(props))
    }
  }, [props])

  return (
    <div className={classes.root}>
      <div className={classes.wrapper}>{elemFields}</div>
    </div>
  )
}
