import React from 'react'
import Typography from '@material-ui/core/Typography'
import Breadcrumbs from '@material-ui/core/Breadcrumbs'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { setBreadcrumbsItems, setSelectedBreadcrumbs } from 'Pages/Protected/Store/actions'

// CSS Classes
import classes from './BreadcrumbsComponent.module.scss'

let dispatch = null

const BreadcrumbsComponent = () => {
  dispatch = useDispatch()
  const items = useSelector(state => state.breadcrumbs.selected)
  return (
    <Breadcrumbs aria-label='breadcrumb' className={classes.root}>
      {items.map((item, index) => {
        if (index !== items.length - 1) {
          return (
            <Link to={item.to} className={classes.item} key={index}>
              <Typography variant='body2'>{item.text}</Typography>
            </Link>
          )
        } else {
          return (
            <Typography color='textPrimary' variant='body2' key={index}>
              {item.text}
            </Typography>
          )
        }
      })}
    </Breadcrumbs>
  )
}

const BreadcrumbsService = {
  setItems: items => dispatch(setBreadcrumbsItems(items)),
  setSelectedItems: items => dispatch(setSelectedBreadcrumbs(items))
}

export { BreadcrumbsComponent, BreadcrumbsService }
