import React from 'react'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import TaskTray from 'SharedComponents/TaskTrayComponent/TaskTrayComponent'
import TicketList from 'SharedComponents/TicketListComponent/TicketListComponent'
import ViewList from '../../Components/ViewList/ViewList'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { DateFormatter } from 'Utilities/TextFormatter'

import PaymentRequestServices from 'Services/Api/PaymentRequestServices'
//
// Styles
import classes from './Dashboard.module.scss'
export default function Dashboard () {
  const [claims, setClaims] = React.useState({
    paymentRequests: { count: 0, data: [] },
    override: { count: 0, data: [] }
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'claim.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    PageTitleService('Dashboard')
    BreadcrumbsService.setSelectedItems(['dashboard'])

    //* For Payment Request
    Promise.all([
      PaymentRequestServices.getClaims({
        category: 'payment-request',
        limit: 10,
        sort: 'claim.date.due|desc',
        range: 'this-week'
      }),

      PaymentRequestServices.getClaimCount({ category: 'payment-request' })
    ]).then(values => {
      const [batchResponse, batchCountResponse] = values
      const paymentRequestClaims = batchResponse.data.data.results
      const paymentRequestClaimCount = batchCountResponse.data.data
      setClaims(prevState => ({
        ...prevState,
        paymentRequests: { data: paymentRequestClaims, count: paymentRequestClaimCount }
      }))
    })

    // //* Override
    // Promise.all([
    //   PaymentRequestServices.getClaims({ category: 'override', range: 'this-week' }),
    //   PaymentRequestServices.getClaimCount({ category: 'override' })
    // ]).then(values => {
    //   const [batchResponse, batchCountResponse] = values
    //   const myBatches = batchResponse.data.data
    //   const myBatchCount = batchCountResponse.data.data
    //   setBatches(prevState => ({ ...prevState, my: { data: myBatches, count: myBatchCount } }))
    // })
  }, [])

  const handleOnShowViewList = category => () => {
    const title = 'For Payment Request'

    setViewList({ ...initialValue, show: true, title, category })
    PaymentRequestServices.getClaims({ category, limit: 10, sort: 'claim.date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleFilterChange = category => value => {
    let _category = category === 'payment-request' ? 'paymentRequests' : ''

    PaymentRequestServices.getClaims({ category, range: value, limit: 10, sort: 'claim.date.due|desc' }).then(
      ({ data: res }) => {
        const { data } = res
        setClaims(prevState => ({ ...prevState, [_category]: { ...prevState[_category], data: data.results } }))
      }
    )
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    PaymentRequestServices.getClaims(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  return (
    <div className={classes.root}>
      <Grid container className={classes['tiles']} spacing={3} direction='row'>
        <Grid item className={classes['task-tray']}>
          <TaskTray sizes={{ xs: 12 }}>
            <DashboardTile
              count={{ color: 3, value: claims.paymentRequests.count, id: 'for-pr-count' }}
              title='For Payment Request'
              id='for-payment-request'
              viewList={
                <Typography
                  variant='caption'
                  className='bold text-link'
                  onClick={handleOnShowViewList('payment-request')}
                  id='for-pr-view-list'
                >
                  View List
                </Typography>
              }
              onFilterChange={handleFilterChange('payment-request')}
            >
              <Table size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell id='for-pr-claim-no'>Claim No.</TableCell>
                    <TableCell id='for-pr-soa-no'>Bundle/SOA No.</TableCell>
                    <TableCell id='for-pr-due-date'>Due Date</TableCell>
                    <TableCell id='for-pr-facility-name'>Facility Name</TableCell>
                    <TableCell />
                  </TableRow>
                </TableHead>
                <TableBody>
                  {claims.paymentRequests.data.map((item, index) => (
                    <TableRow key={index}>
                      <TableCell id={`for-pr-claim-no-${index}`}>{item.claim_number}</TableCell>
                      <TableCell id={`for-pr-soa-no-${index}`}>{item.soa.number || item.bundle_number}</TableCell>
                      <TableCell id={`for-pr-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                      <TableCell id={`for-pr-facility-name-${index}`}>{item.facility.name}</TableCell>
                      <TableCell>
                        <Link to={`/payment-request/${item.bundle_number}`}>
                          <Typography variant='caption' className={`text-link underline`} id={`request-payment-${index}`}>
                            Request Payment
                          </Typography>
                        </Link>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </DashboardTile>

            <DashboardTile
              count={{ color: 5, value: claims.override.count, id: 'override-claim-count' }}
              title='Override Claim'
              id='override-claim'
              viewAllLink={{ to: '/claims?type=override', caption: 'View List' }}
            >
              <Table size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell>Claim No.</TableCell>
                    <TableCell>Bundle/SOA No.</TableCell>
                    <TableCell>Due Date</TableCell>
                    <TableCell>Facility Name</TableCell>
                    <TableCell />
                  </TableRow>
                </TableHead>
                <TableBody>
                  {claims.override.data.map((item, index) => (
                    <TableRow key={index}>
                      <TableCell>{item.soa_no}</TableCell>
                      <TableCell>{item.claim_no}</TableCell>
                      <TableCell>{item.due_date}</TableCell>
                      <TableCell>{item.facility_name}</TableCell>
                      <TableCell>
                        <Typography variant='caption' className={`text-link underline`}>
                          Request Payment
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </DashboardTile>
          </TaskTray>
        </Grid>

        <Grid item className={classes['ticket-list']}>
          <TicketList count={0} title='Open Tickets' viewAllLink={{ to: '/', caption: 'View all' }}>
            <Table size='small'>
              <TableHead>
                <TableRow>
                  <TableCell>Ticket #</TableCell>
                  <TableCell>Date</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {[].map((item, index) => (
                  <TableRow key={index}>
                    <TableCell>2334324343</TableCell>
                    <TableCell>08/12/2019</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TicketList>
        </Grid>
      </Grid>

      <ViewList viewList={viewList} setViewList={setViewList} onViewListChange={handleOnViewListChange} />
    </div>
  )
}
