import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import BaseDetails from '../BaseDetails/BaseDetails'
import DiagnosisList from '../DiagnosisList/DiagnosisList'
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import DatePicker from 'SharedComponents/DatePicker/DatePicker'
import DropDown from 'SharedComponents/DropDown/DropDown'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import ProcedureList from '../ProcedureList/ProcedureList'
import HospitalItemList from '../HospitalItemList/HospitalItemList'
import ProfessionalFeeList from '../ProfessionalFeeList/ProfessionalFeeList'
import ClaimFooter from '../ClaimFooter/ClaimFooter'
import DiscrepancyBase from '../DiscrepancyBase/DiscrepancyBase'
import DisapprovedLabel from '../DisapprovedLabel/DisapprovedLabel'
import RightPanelTabs from '../RightPanelTabs/RightPanelTabs'
import ReimbursementInfoComponent from '../../../Components/ReimbursementInfoComponent/ReimbursementInfoComponent'

// Services
import ClaimsAdjudicationServices from 'Services/Api/ClaimsAdjudicationServices'

// Utilities
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './ClaimReimbursement.module.scss'

// Controller
// import ConfigController from 'Controllers/ConfigController2'

// Validator
import MainValidator from './../../Validators/Claim'
import DiagnosisListValidator from '../DiagnosisList/DiagnosisListValidator'
import HospitalItemValidator from '../HospitalItemForm/HospitalItemValidator'
import ProfessionalFeeValidator from '../ProfessionalFeeForm/ProfessionalFeeValidator'
import ProcedureValidator from '../ProcedureForm/ProcedureValidator'

// Store
import { useSelector, useDispatch } from 'react-redux'
import {
  setClaimFields,
  setClaimBaseDetails,
  setClaimRoom,
  setDropDown,
  initDropDowns,
  initSuggestions,
  setProcedure,
  setHospitalItem,
  setProfessionalFee,
  resetError,
  setError,
  setRejectStatus
} from '../../Store/actions'

import AdditionalFields from './AdditionalFields'

// Libraries
import moment from 'moment'

export default function ClaimReimbursement (props) {
  const dispatch = useDispatch()
  // Events
  const {
    onEscalate,
    onViewAnalysisSheet,
    onViewLetterOfAdvice,
    onChangeDoctor = () => {},
    onReject,
    onReassign,
    onApprove,
    onSave,
    onCancel,
    onChangeFacility
  } = props
  // Variables
  const { claim, permissions, _type } = props
  // Services
  const { getClaimDetails, getUsers, userManagementService = () => {}, onChangePro } = props

  const suggestions = useSelector(state => state.suggestions)
  const dropDown = useSelector(state => state.dropDown)
  const discrepancy = useSelector(state => state.discrepancy)
  const error = useSelector(state => state.error)
  const escalations = useSelector(state => state.escalations)
  const recon = useSelector(state => state.recon)

  const [hasError, setHasError] = React.useState(false)
  //
  const [batchNumber, setBatchNumber] = React.useState(null)
  // const [openDialog, setOpenDialog] = React.useState(false)
  const [allowedFields, setAllowedFields] = React.useState({})
  const [initialAllowedFields, setInitialAllowedFields] = React.useState({})
  const [showReimbursementModal, setShowReimbursementModal] = React.useState(false)
  //

  const [grandTotal, setGrandTotal] = React.useState(0)

  React.useEffect(() => {
    console.log('fdsfd')
    dispatch(initSuggestions())
    dispatch(initDropDowns(['rooms']))
    dispatch(initDropDowns(['reason']))
    dispatch(initDropDowns(['reason-charging']))

    getReason()

    const _allowedFields = {
      approval_code: '',
      loa_number: '',
      reimbursement_type: '',
      member_account_number: '',
      member_last_name: '',
      member_first_name: '',
      member_middle_name: '',
      facility_id: '',
      facility_name: '',
      date_from: new Date().toISOString(),
      medicine_category: '',
      reimbursement_category: '',
      official_receipt_number: ''
    }

    const _lineItemFields = {
      diagnosis: [],
      procedures: [],
      hospital_items: [],
      professional_fees: [],
      reimbursement_info: []
    }

    setTimeout(() => {
      setAllowedFields({ ..._allowedFields, ...AdditionalFields(undefined) })
      setInitialAllowedFields(_allowedFields)
      dispatch(setClaimFields({ ..._allowedFields, ..._lineItemFields }))
    }, 10)

    getClaimDetails(data => {
      getRooms({ value: data.facility.id, by: 'id' })

      let _claim = {
        approval_code: data.approval_code,
        loa_number: data.loa_number,
        reimbursement_type: data.reimbursement_details.type,
        member_account_number: data.member.account_number,
        member_last_name: data.member.last_name,
        member_first_name: data.member.first_name,
        member_middle_name: data.member.middle_name,
        facility_id: data.facility.id,
        facility_name: data.facility.name,
        date_from: data.date.from,
        medicine_category: data.medicine_category,
        reimbursement_category: data.reimbursement_details.category,
        official_receipt_number: data.reimbursement_details.official_receipt_number,
        diagnosis: data.diagnosis || [],
        procedures: data.procedures || [],
        professional_fees: data.professional_fees || [],
        hospital_items: data.hospital_items || [],
        reimbursement_info: data.reimbursement_info || [],
        batch_number: data.batch_number
      }

      if (data.reimbursement_details.type === 'InPatient') {
        _claim = {
          ..._claim,
          date_to: data.date.to,
          room_type: data.room.type,
          room_rate: data.room.rate || 0,
          pro_id: data.pro.id,
          pro_name: data.pro.name
        }
      } else {
        _claim = {
          ..._claim,
          doctor_id: data.doctor.id,
          doctor_name: data.doctor.name,
          department: data.department
        }
      }

      dispatch(setClaimFields(_claim))
      setAllowedFields({
        ..._allowedFields,
        ...AdditionalFields(data.reimbursement_details.type === 'InPatient' ? 'InPatient' : '')
      })

      setBatchNumber(data.batch_number)

      // ConfigController.getUserManagementData().then(res => {
      //   dispatch(
      //     setClaimBaseDetails({
      //       value: new Date().toISOString(),
      //       field: 'approved_date'
      //     })
      //   )
      //   dispatch(
      //     setClaimBaseDetails({
      //       value: res.user_id,
      //       field: 'approved_id'
      //     })
      //   )
      //   dispatch(
      //     setClaimBaseDetails({
      //       value: `${res.first_name} ${res.last_name}`,
      //       field: 'approved_name'
      //     })
      //   )
      // })
    })
  }, [])

  const totalAmount = (currentValue, i) => {
    const total =
      parseFloat(i.gross_amount) +
      parseFloat(i.vat_amount) -
      (parseFloat(i.amount_not_covered) +
        parseFloat(i.philhealth_amount_paid) +
        parseFloat(i.amount_due_from_member) +
        parseFloat(i.amount_paid_by_member))
    return currentValue + total
  }

  //* Room
  const getRooms = ({ value, by = 'id' }) => {
    ClaimsAdjudicationServices.lookups({
      on: 'medical-partner',
      params: { query: value, by }
    }).then(res => {
      const [data] = res.data.data

      let rooms = []
      if (data) {
        rooms = (data.rooms || []).map(item => ({
          text: item.type,
          value: item.type,
          rate: item.rate
        }))
      }

      dispatch(setDropDown({ rooms }))
    })
  }

  React.useEffect(() => {
    let sum = 0
    if (claim) {
      if (claim.procedures) sum = claim.procedures.reduce(totalAmount, sum)
      if (claim.hospital_items) sum = claim.hospital_items.reduce(totalAmount, sum)
      if (claim.professional_fees) sum = claim.professional_fees.reduce(totalAmount, sum)
      dispatch(setRejectStatus(!(sum === 0)))
    }
    setGrandTotal(sum)
  }, [claim])

  const getReason = () => {
    ClaimsAdjudicationServices.getDropdown({ field: 'reason' }).then(res => {
      const { data } = res.data
      const reason = data.map(item => ({
        text: item.reason,
        value: item.reason,
        code: item.code
      }))
      dispatch(setDropDown({ reason }))
    })
    ClaimsAdjudicationServices.getDropdown({ field: 'reason-charging' }).then(res => {
      const { data } = res.data
      const reason_charging = data.map(item => ({
        text: item.reason_charging,
        value: item.reason_charging,
        code: item.code
      }))
      dispatch(setDropDown({ reason_charging }))
    })
  }

  const handleOnChangeFacility = args => {
    const rooms = (onChangeFacility(args) || []).map(item => ({
      text: item.type,
      value: item.type,
      rate: item.rate
    }))
    for (let i of rooms) {
      if (i.value === claim.room_type) {
        console.log(i)
        dispatch(setClaimRoom({ room_type: i.value, room_rate: i.rate }))
      }
    }
    dispatch(setDropDown({ rooms }))
  }

  const handleOnChangeRoomType = e => {
    const { value } = e.target
    for (let i of dropDown.rooms) {
      if (i.value === value) {
        return dispatch(setClaimRoom({ room_type: i.value, room_rate: i.rate }))
      }
    }
  }

  const doValidateBase = () => {
    const data = {}

    for (let f in allowedFields) data[f] = claim[f]

    const { error = {} } = MainValidator(Object.keys(allowedFields))({ ...data })
    console.log(error)
    return error
  }

  const doValidateProcedure = ({ details }) => {
    const { error } = ProcedureValidator({
      description: details.description,
      code: details.code,
      gross_amount: details.gross_amount,
      vat_amount: details.vat_amount,
      amount_paid_by_member: details.amount_paid_by_member,
      philhealth_amount_paid: details.philhealth_amount_paid,
      philhealth_remarks: details.philhealth_remarks,
      amount_not_covered: details.amount_not_covered,
      reason: details.reason,
      amount_due_from_member: details.amount_due_from_member,
      reason_for_charging_member: details.reason_for_charging_member
    })
    return error
  }

  const doValidateHospitalItem = ({ details }) => {
    const { error } = HospitalItemValidator({
      name: details.name,
      item_code: details.item_code,
      gross_amount: details.gross_amount,
      vat_amount: details.vat_amount,
      amount_paid_by_member: details.amount_paid_by_member,
      philhealth_amount_paid: details.philhealth_amount_paid,
      philhealth_remarks: details.philhealth_remarks,
      amount_not_covered: details.amount_not_covered,
      reason: details.reason,
      amount_due_from_member: details.amount_due_from_member,
      reason_for_charging_member: details.reason_for_charging_member
    })
    return error
  }

  const doValidateProfessionalFee = ({ details }) => {
    const { error } = ProfessionalFeeValidator({
      doctor_id: details.doctor_id,
      doctor_name: details.doctor_name,
      specialization: details.specialization,
      gross_amount: details.gross_amount,
      vat_amount: details.vat_amount,
      amount_paid_by_member: details.amount_paid_by_member,
      philhealth_amount_paid: details.philhealth_amount_paid,
      philhealth_remarks: details.philhealth_remarks,
      amount_not_covered: details.amount_not_covered,
      reason: details.reason,
      amount_due_from_member: details.amount_due_from_member,
      reason_for_charging_member: details.reason_for_charging_member
    })
    return error
  }

  const doValidateAll = () => {
    dispatch(resetError())

    const base_errors = doValidateBase()
    const diagnosis = DiagnosisListValidator(claim.diagnosis)
    let procedure = {}
    let hospital_item = {}
    let professional_fee = {}

    for (const [index, value] of claim.procedures.entries()) {
      const error = doValidateProcedure({ index, details: value })
      if (error) {
        procedure = { index, ...error }
        // AUTO OPEN CLAIM
        dispatch(
          setProcedure({
            show: true,
            mode: 'edit',
            index,
            details: value
          })
        )
        break
      }
    }

    for (const [index, value] of claim.hospital_items.entries()) {
      const error = doValidateHospitalItem({ index, details: value })
      if (error) {
        hospital_item = { index, ...error }
        // AUTO OPEN CLAIM
        dispatch(
          setHospitalItem({
            show: true,
            mode: 'edit',
            index,
            details: value
          })
        )
        break
      }
    }

    for (const [index, value] of claim.professional_fees.entries()) {
      const error = doValidateProfessionalFee({ index, details: value })
      if (error) {
        professional_fee = { index, ...error }
        // AUTO OPEN CLAIM
        dispatch(
          setProfessionalFee({
            show: true,
            mode: 'edit',
            index,
            details: value
          })
        )
        break
      }
    }
    dispatch(
      setError({
        ...base_errors,
        diagnosis,
        procedure,
        hospital_item,
        professional_fee
      })
    )

    const invalid =
      Object.keys(base_errors).length > 0 ||
      diagnosis.error ||
      procedure.index >= 0 ||
      hospital_item.index >= 0 ||
      professional_fee.index >= 0

    setHasError(invalid)

    return invalid
  }

  React.useEffect(() => {
    if (hasError) {
      console.group('Validation Failed')
      for (const value in error) {
        console.log(value, error[value])
      }
      console.groupEnd()
    }
  }, [hasError])

  const onChangeReimbursementType = ({ value, field }) => {
    setAllowedFields({ ...initialAllowedFields, ...AdditionalFields(value) })

    if (value === 'InPatient') {
      dispatch(
        setClaimFields({
          ...claim,
          date_to: moment(claim.date_from)
            .endOf('day')
            .toISOString()
        })
      )
    }

    dispatch(setClaimBaseDetails({ value, field }))
  }

  return (
    <div className={classes.root}>
      {claim && (
        <React.Fragment>
          <DisapprovedLabel
            id='dp-failed-adjudication'
            value='Failed Adjudication'
            reasons={discrepancy.disapproved ? discrepancy.disapproved : []}
          />

          <Paper className={classes['first-container']}>
            <BaseDetails
              claim={claim}
              onChange={({ value, field }) => dispatch(setClaimBaseDetails({ value, field }))}
              onChangeReimbursementType={onChangeReimbursementType}
              permissions={permissions.base}
              showReimbursement
              requiredFields={{ reimbursement_type: true }}
            />
            <div className={classes.groups}>
              <div className={classes['input']}>
                <DiscrepancyBase
                  id='dp-medical-partner-id'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.facility_id
                  }}
                >
                  <AutoComplete
                    id='medical-partner-id'
                    required
                    label='Medical Partner ID'
                    value={claim.facility_id}
                    suggestions={suggestions.facility.id}
                    onChange={(value, isSuggestionClick) =>
                      handleOnChangeFacility({
                        field: 'facility_id',
                        value,
                        isSuggestionClick
                      })
                    }
                    error={error.facility_id}
                    readOnly={!permissions.base.update}
                  />
                </DiscrepancyBase>
              </div>

              <div className={classes['input']}>
                <DiscrepancyBase
                  id='dp-medical-partner-facility-name'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.facility_name
                  }}
                >
                  <AutoComplete
                    id='medical-partner-facility-name'
                    required
                    label='Medical Partner (Facility) Name'
                    value={claim.facility_name}
                    suggestions={suggestions.facility.name}
                    onChange={(value, isSuggestionClick) =>
                      handleOnChangeFacility({
                        field: 'facility_name',
                        value,
                        isSuggestionClick
                      })
                    }
                    error={error.facility_name}
                    readOnly={!permissions.base.update}
                  />
                </DiscrepancyBase>
              </div>

              {AdditionalFields.exist(allowedFields, 'doctor_id') && (
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-medical-doctor-id'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.doctor_id
                    }}
                  >
                    <AutoComplete
                      id='dp-medical-partner-id'
                      required
                      label='Medical Partner ID'
                      value={claim.doctor_id}
                      suggestions={suggestions.doctor.id}
                      onChange={(value, isSuggestionClick) =>
                        onChangeDoctor({
                          field: 'doctor_id',
                          value,
                          isSuggestionClick
                        })
                      }
                      error={error.doctor_id}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'doctor_name') && (
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-medical-doctor-name'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.doctor_name
                    }}
                  >
                    <AutoComplete
                      id='doctor-name'
                      required
                      label='Medical Partner (Doctor) Name'
                      value={claim.doctor_name}
                      suggestions={suggestions.doctor.full_name}
                      onChange={(value, isSuggestionClick) =>
                        onChangeDoctor({
                          field: 'doctor_name',
                          value,
                          isSuggestionClick
                        })
                      }
                      error={error.doctor_name}
                      readOnly={!permissions.base.update}
                    />
                  </DiscrepancyBase>
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'date_to') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <DiscrepancyBase
                      id='dp-admission-date-from'
                      discrepancy={{
                        message: 'Failed',
                        result: discrepancy.date_from
                      }}
                    >
                      <DatePicker
                        id='admission-date-from'
                        required
                        label='Admission Date (From)'
                        value={claim.date_from}
                        onChange={event =>
                          dispatch(
                            setClaimBaseDetails({
                              value: moment(event.toISOString())
                                .startOf('day')
                                .toISOString(),
                              field: 'date_from'
                            })
                          )
                        }
                        error={error.date_from}
                        readOnly={!permissions.base.update}
                      />
                    </DiscrepancyBase>
                  </div>

                  <div className={classes['input']}>
                    <DiscrepancyBase
                      id='admission-date-to'
                      discrepancy={{
                        message: 'Failed',
                        result: discrepancy.date_to
                      }}
                    >
                      <DatePicker
                        id='admission-date-to'
                        required
                        label='Admission Date (To)'
                        value={claim.date_to}
                        onChange={event =>
                          dispatch(
                            setClaimBaseDetails({
                              value: event.toISOString(),
                              field: 'date_to'
                            })
                          )
                        }
                        error={error.date_to}
                        readOnly={!permissions.base.update}
                      />
                    </DiscrepancyBase>
                  </div>
                </React.Fragment>
              )}
            </div>

            <div className={classes.groups}>
              {AdditionalFields.exist(allowedFields, 'room_type') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <DiscrepancyBase
                      id='dp-room-type'
                      discrepancy={{
                        message: 'Failed',
                        result: discrepancy.room_type
                      }}
                    >
                      <DropDown
                        id='room-type'
                        label='Room Type'
                        items={dropDown.rooms}
                        value={claim.room_type}
                        onChange={handleOnChangeRoomType}
                        error={error.room_type}
                        readOnly={!permissions.base.update}
                      />
                    </DiscrepancyBase>
                  </div>

                  <div className={classes['input']}>
                    <DiscrepancyBase
                      id='dp-room-rate'
                      discrepancy={{
                        message: 'Failed',
                        result: discrepancy.room_rate
                      }}
                    >
                      <TextFieldCurrency
                        id='room-rate'
                        label='Room Rate'
                        value={claim.room_rate}
                        onChange={event => {
                          dispatch(
                            setClaimBaseDetails({
                              value: event.target.value,
                              field: 'room_rate'
                            })
                          )
                        }}
                        error={error.room_rate}
                        readOnly={!permissions.base.update}
                      />
                    </DiscrepancyBase>
                  </div>

                  <div className={classes['input']}>
                    <AutoComplete
                      id='pro-id'
                      label='PRO ID'
                      required
                      value={claim.pro_id}
                      suggestions={suggestions.pro.id}
                      onChange={(value, isSuggestionClick) =>
                        onChangePro({
                          field: 'pro_id',
                          value,
                          isSuggestionClick
                        })
                      }
                      error={error.pro_id}
                      readOnly={!permissions.base.update}
                    />
                  </div>

                  <div className={classes['input']}>
                    <AutoComplete
                      id='pro-name'
                      label='PRO Name'
                      required
                      value={claim.pro_name}
                      suggestions={suggestions.pro.name}
                      onChange={(value, isSuggestionClick) =>
                        onChangePro({
                          field: 'pro_name',
                          value,
                          isSuggestionClick
                        })
                      }
                      error={error.pro_name}
                      readOnly={!permissions.base.update}
                    />
                  </div>
                </React.Fragment>
              )}

              {!AdditionalFields.exist(allowedFields, 'room_type') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <DatePicker
                      id='date-of-service'
                      required
                      label='Date of Service'
                      value={claim.date_from}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.toISOString(),
                            field: 'date_from'
                          })
                        )
                      }
                      error={error.date_from}
                      readOnly={!permissions.base.update}
                    />
                  </div>

                  <div className={classes['input']}>
                    <TextField
                      id='department'
                      label='Department'
                      value={claim.department}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'department'
                          })
                        )
                      }
                      error={error.pro_id}
                      readOnly={!permissions.base.update}
                    />
                  </div>

                  <div className={classes['input']}>
                    <TextField
                      id='medicine-category'
                      required
                      label='Medicine Category'
                      value={claim.medicine_category}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'medicine_category'
                          })
                        )
                      }
                      readOnly={!permissions.base.update}
                      error={error.medicine_category}
                    />
                  </div>

                  <div className={classes['input']}>
                    <TextField
                      id='reimbursement-category'
                      label='Reimbursement Category'
                      value={claim.reimbursement_category}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'reimbursement_category'
                          })
                        )
                      }
                      readOnly={!permissions.base.update}
                      error={error.reimbursement_category}
                    />
                  </div>
                </React.Fragment>
              )}
            </div>

            <div className={classes.groups}>
              {AdditionalFields.exist(allowedFields, 'room_type') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <TextField
                      id='medicine-category'
                      label='Medicine Category'
                      value={claim.medicine_category}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'medicine_category'
                          })
                        )
                      }
                      readOnly={!permissions.base.update}
                      error={error.medicine_category}
                    />
                  </div>

                  <div className={classes['input']}>
                    <TextField
                      id='reimbursement-category'
                      label='Reimbursement Category'
                      value={claim.reimbursement_category}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'reimbursement_category'
                          })
                        )
                      }
                      readOnly={!permissions.base.update}
                      error={error.reimbursement_category}
                    />
                  </div>
                </React.Fragment>
              )}

              <div className={classes['input']}>
                <TextField
                  id='official-receipt-number'
                  label='Official Receipt Number'
                  value={claim.official_receipt_number}
                  required
                  onChange={event =>
                    dispatch(
                      setClaimBaseDetails({
                        value: event.target.value,
                        field: 'official_receipt_number'
                      })
                    )
                  }
                  readOnly={!permissions.base.update}
                  error={error.official_receipt_number}
                />
              </div>
            </div>

            <div className={classes['child-container']}>
              <div className={classes['left-side']}>
                {permissions.diagnosis.read && (
                  <DiagnosisList
                    id='diagnosis-list'
                    permissions={permissions.diagnosis}
                    items={claim.diagnosis}
                    error={error.diagnosis}
                  />
                )}
              </div>
            </div>
          </Paper>

          <div className={classes['tables-container']}>
            <div className={classes['left-side']} id='left-side-panel'>
              {permissions.procedures.read && (
                <ProcedureList
                  id='procedure'
                  data={claim.procedures}
                  doValidate={doValidateProcedure}
                  permissions={permissions.procedures}
                />
              )}
              {permissions.hospital_items.read && (
                <HospitalItemList
                  id='hospital-item'
                  data={claim.hospital_items}
                  doValidate={doValidateHospitalItem}
                  permissions={permissions.hospital_items}
                />
              )}
              {permissions.professional_fees.read && (
                <ProfessionalFeeList
                  id='professional-fee'
                  data={claim.professional_fees}
                  doValidate={doValidateProfessionalFee}
                  permissions={permissions.professional_fees}
                />
              )}
            </div>
            <div className={classes['right-side']} id='right-side-panel'>
              <div className={classes['wrapper']}>
                <div className={classes['sticky']}>
                  <RightPanelTabs escalations={escalations} recon={recon} {...props} />
                </div>
              </div>
            </div>
          </div>
          <div className={classes['grand-total']}>
            <Typography variant='subtitle2'>TOTAL PHP {CurrencyFormatter(grandTotal)}</Typography>
          </div>
          <ClaimFooter
            getUsers={getUsers}
            doValidate={doValidateAll}
            doEscalate={onEscalate}
            doReassign={onReassign}
            doViewAnalysisSheet={onViewAnalysisSheet}
            doViewLetterOfAdvice={onViewLetterOfAdvice}
            doReject={() => setShowReimbursementModal(true)}
            doApprove={onApprove}
            doSave={onSave}
            doCancel={onCancel}
            permissions={permissions.footer}
            grandTotal={grandTotal}
            claimType={_type}
            userManagementService={userManagementService}
            isReimbursement
          />
        </React.Fragment>
      )}

      <ReimbursementInfoComponent
        claim={{ ...claim, batch_number: batchNumber }}
        show={showReimbursementModal}
        setShowModal={setShowReimbursementModal}
        handleOnSave={reimbursement_info =>
          dispatch(
            setClaimBaseDetails({
              value: reimbursement_info,
              field: 'reimbursement_info'
            })
          )
        }
        doReject={() => onReject({ goBack: true })}
      />
    </div>
  )
}
// doReject={onReject}
