import React from 'react'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import ClaimsEncodingServices from 'Services/Api/ClaimsEncodingServices'
import CustomerCareServices from 'Services/Api/CustomerCareServices'

// Store
import { useSelector, useDispatch } from 'react-redux'
import {
  resetClaim,
  setClaimType,
  setSuggestionObject,
  resetSuggestionObject,
  setClaimFacility,
  setClaimDoctor,
  addSuggestionToArray,
  resetGrandTotal,
  setClaimPro
} from '../../Store/actions'

import GetClaimComponent from './GetClaimComponent'

export default function ClaimPage (props) {
  const dispatch = useDispatch()
  const claim = useSelector(state => state.claim)
  const claim_type = useSelector(state => state.claimType)
  const suggestions = useSelector(state => state.suggestions)

  const getBatchDetails = ({ _batchNumber }) => {
    ClaimsEncodingServices.getBatch({ batch_number: _batchNumber }).then(res => {
      const { data } = res.data
      dispatch(setClaimType(data.claim.type))
      LoadingService.loading(false)
    })
  }

  React.useEffect(() => {
    dispatch(resetClaim())
    LoadingService.loading(true)
    const { _batchNumber, _claimNumber } = props.match.params

    let pageTitle = ''
    let breadCrumbs = ['dashboard', 'batches', `specificBatch:${_batchNumber}`]
    if (props.isAdd) {
      pageTitle = 'Add new claim'
      breadCrumbs = [...breadCrumbs, 'addClaim']
    } else {
      pageTitle = `Claim No. ${_claimNumber}`
      breadCrumbs = [...breadCrumbs, 'updateClaim']
    }

    PageTitleService(pageTitle)
    BreadcrumbsService.setSelectedItems(breadCrumbs)
    getBatchDetails({ _batchNumber })

    return () => {
      dispatch(setClaimType(''))
      dispatch(resetClaim())
      dispatch(resetGrandTotal())
    }
  }, [props.match.params._batchNumber])

  const [debounce, setDebounce] = React.useState({
    facility: null,
    doctor: null,
    icd10: null,
    cpt: null,
    hospital_items: null,
    pro: null
  })

  const getClaimDetails = callback => {
    const { _batchNumber, _claimNumber } = props.match.params
    ClaimsEncodingServices.getClaim({
      batch_number: _batchNumber,
      claim_number: _claimNumber
    }).then(res => {
      const { data } = res.data

      // Set Diagnosis Suggestions
      const { diagnosis = [] } = data
      diagnosis.map(i => dispatch(addSuggestionToArray({ field: 'icd10' })))

      callback(data)
    })
  }

  //* Facility
  const handleOnChangeFacility = ({ value, field, isSuggestionClick }) => {
    dispatch(setClaimFacility({ [field]: value }))
    if (isSuggestionClick) {
      for (let i of suggestions.facility.data) {
        if (value === i.id || value === i.name) {
          dispatch(setClaimFacility({ facility_id: i.id, facility_name: i.name }))
          dispatch(resetSuggestionObject({ field: 'facility' }))
          return i.rooms
        }
      }
    } else {
      dispatch(resetSuggestionObject({ field: 'facility' }))
      const { 1: by } = field.split('_')
      getFacilitySuggestions({ value, by })
    }
  }

  const getFacilitySuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.facility)
    setDebounce(state => ({
      ...state,
      facility: setTimeout(() => {
        if (value) {
          ClaimsEncodingServices.lookups({
            on: 'medical-partner',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])
            dispatch(
              setSuggestionObject({
                field: 'facility',
                suggestion: { [by]: _items, data }
              })
            )
          })
        }
      }, 500)
    }))
  }

  //* Doctor
  const handleOnChangeDoctor = ({ value, field, isSuggestionClick }) => {
    dispatch(setClaimDoctor({ [field]: value }))
    if (isSuggestionClick) {
      for (let i of suggestions.doctor.data) {
        if (value === i.id || value === i.full_name) {
          dispatch(setClaimDoctor({ doctor_id: i.id, doctor_name: i.full_name }))
          dispatch(resetSuggestionObject({ field: 'doctor' }))
          return
        }
      }
    } else {
      dispatch(resetSuggestionObject({ field: 'doctor' }))
      const { 1: by } = field.split('_')
      let _by = by === 'id' ? 'id' : 'full_name'
      getDoctorSuggestions({ value, by: _by })
    }
  }

  const getDoctorSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.doctor)
    setDebounce(state => ({
      ...state,
      doctor: setTimeout(() => {
        if (value) {
          ClaimsEncodingServices.lookups({
            on: 'doctor-partner',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])
            dispatch(
              setSuggestionObject({
                field: 'doctor',
                suggestion: { [by]: _items, data }
              })
            )
          })
        }
      }, 500)
    }))
  }

  // PRO
  const handleOnChangePro = ({ value, field, isSuggestionClick }) => {
    dispatch(setClaimPro({ [field]: value }))
    if (isSuggestionClick) {
      for (let i of suggestions.pro.data) {
        if (value === i.employee_id || value === i.name) {
          dispatch(setClaimPro({ pro_name: i.name, pro_id: i.employee_id }))
          dispatch(resetSuggestionObject({ field: 'pro' }))
          return
        }
      }
    } else {
      dispatch(resetSuggestionObject({ field: 'pro' }))
      getProSuggestions({ value, field })
    }
  }

  const getProSuggestions = ({ value, field }) => {
    if (value.trim().length < 3) return
    clearTimeout(debounce.pro)
    setDebounce(state => ({
      ...state,
      pro: setTimeout(() => {
        if (value) {
          // PRO User lookup
          CustomerCareServices.proUsersLookup({ value, field }).then(res => {
            // limit temporary - no limit in api seer
            const limit = []
            for (let i = 0; i < 10; i++) {
              if (!res.data.data[i]) break
              limit.push(res.data.data[i])
            }

            const data = limit.map(elem => {
              let full_name = `${elem.first_name} ${elem.last_name}`
              delete elem.first_name
              delete elem.last_name
              return { ...elem, name: full_name }
            })

            let _items = []

            if (field === 'pro_id') {
              for (let d of data) {
                _items.push(d[`employee_${field.split('_')[1]}`])
              }
            } else {
              for (let d of data) {
                _items.push(d['name'])
              }
            }

            dispatch(
              setSuggestionObject({
                field: 'pro',
                suggestion: { [`${field.split('_')[1]}`]: _items, data }
              })
            )
          })
        }
      }, 500)
    }))
  }

  // Anaesthesiologist
  const handleOnChangeAnaesthesiologist = ({ value, field, isSuggestionClick }) => {
    dispatch(setClaimDoctor({ [field]: value }))
    if (isSuggestionClick) {
      for (let i of suggestions.anesthesiologist.data) {
        if (value === i.id || value === i.full_name) {
          dispatch(setClaimDoctor({ anesthesiologist_id: i.id, anesthesiologist_name: i.full_name }))
          dispatch(resetSuggestionObject({ field: 'anesthesiologist' }))
          return
        }
      }
    } else {
      dispatch(resetSuggestionObject({ field: 'anesthesiologist' }))
      const { 1: by } = field.split('_')
      let _by = by === 'id' ? 'id' : 'full_name'
      getAnaesthesiologistSuggestions({ value, by: _by })
    }
  }

  const getAnaesthesiologistSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.anaesthesiologist)
    setDebounce(state => ({
      ...state,
      doctor: setTimeout(() => {
        if (value) {
          ClaimsEncodingServices.lookups({
            on: 'doctor-partner',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])
            dispatch(
              setSuggestionObject({
                field: 'anesthesiologist',
                suggestion: { [by]: _items, data }
              })
            )
          })
        }
      }, 500)
    }))
  }

  return (
    <React.Fragment>
      {claim_type && (
        <GetClaimComponent
          _type={claim_type}
          {...props}
          claim={claim}
          onChangeFacility={handleOnChangeFacility}
          getClaimDetails={getClaimDetails}
          onChangePro={handleOnChangePro}
          onChangeDoctor={handleOnChangeDoctor}
          onChangeAnaesthesiologist={handleOnChangeAnaesthesiologist}
        />
      )}
    </React.Fragment>
  )
}
