import React from 'react'
import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList/ViewList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const ClaimsForApprovalTile = props => {
  const { services } = props
  const [claims, setClaims] = React.useState({
    recon: { count: 0, data: [] }
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'latest.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    Promise.all([
      services.getItems({
        limit: 10,
        sort: 'latest.date.due|desc',
        range: 'this-week'
      }),
      services.getItemCount()
    ]).then(values => {
      const [claimResponse, claimCountResponse] = values
      const allClaims = claimResponse.data.data.results
      const allClaimsCount = claimCountResponse.data.data
      setClaims(prevState => ({ ...prevState, recon: { data: allClaims, count: allClaimsCount } }))
    })
  }, [])

  const handleOnReview = claim_number => {
    props.history.push(`/claims/reconciled/${claim_number}`)
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    services.getItems(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnClickItemViewList = ({ category, claim_number }) => {
    if (category === 'recon') handleOnReview(claim_number)
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='claims-verifier'
        count={{ color: 3, value: 0, id: 'claims-verifier-count' }}
        sizes={{ sm: 12 }}
        title='Claims Verifier'
        isFilterShown={false}
        viewList={
          <Typography id='cv-view-list' variant='caption' className='bold text-link'>
            View List
          </Typography>
        }
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              <TableCell id='cv-verifier-name'>Verifier Name</TableCell>
              <TableCell id='cv-total-batches'>Total Batches</TableCell>
              <TableCell id='cv-total-claims'>Total Claims</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {[].map((item, index) => (
              <TableRow key={item.claim_number}>
                <TableCell id={`cv-verifier-name-${index}`}>{item.verifier_name}</TableCell>
                <TableCell id={`cv-total-batches-${index}`}>{item.total_batches}</TableCell>
                <TableCell id={`cv-total-claims-${index}`}>{item.total_claims}</TableCell>
                <TableCell id={`cv-view-claim-${index}`}>
                  <Typography
                    id={`view-claim-${index}`}
                    variant='caption'
                    className={`text-link underline`}
                    onClick={() => handleOnReview(item.claim_number)}
                  >
                    View Claim
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        onItemClick={handleOnClickItemViewList}
      />
    </React.Fragment>
  )
}

ClaimsForApprovalTile.displayName = 'DashboardTileComponent'

export default ClaimsForApprovalTile
