import React from 'react'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import { Link } from 'react-router-dom'

// CSS Classes
import classes from './TicketListComponent.module.scss'

/**
 * Create ticket list component
 *
 * @param children child components 
 * @param count count value to be displayed; keys: color, value
 * @param title title string
 * @param viewAllLink view all link
 * @param isFilterShown flag for showing filter
 */
export default function TicketListComponent ({ children, count, title, viewAllLink, isFilterShown = true }) {
  const [filter, setFilter] = React.useState('all')
  const handleChange = event => {
    setFilter(event.target.value)
  }

  return (
    <Grid container className={classes.root}>
      <Typography variant='subtitle1' className={classes.title}>
        TICKET LIST
      </Typography>
      <Grid container className={classes.wrapper} spacing={3}>
        <Grid item xs={12}>
          <Paper className={classes.content}>
            <div className={classes.wrapper}>
              <div className={classes.header}>
                <div className={classes.count}>
                  <Typography variant='h5' className='color-7'>
                    {count}
                  </Typography>
                </div>
                { isFilterShown ? (
                  <div className={classes.filter}>
                    <span>
                      <FormControl>
                        <Select className={classes['select']} value={filter} onChange={handleChange}>
                          <MenuItem value={'all'}>
                            <Typography className={classes.input}>Assigned to All</Typography>
                          </MenuItem>
                        </Select>
                      </FormControl>
                    </span>
                  </div>
                ) : '' }
              </div>
              <Typography variant='subtitle2' className={classes.title}>
                {title}
              </Typography>
              <div className={classes.list}>{children}</div>
              <div className={classes.action}>
                <Link to={viewAllLink.to} className='text-link'>
                  <Typography variant='caption' className='bold'>
                    {viewAllLink.caption}
                  </Typography>
                </Link>
              </div>
            </div>
          </Paper>
        </Grid>
      </Grid>
    </Grid>
  )
}
