import React from 'react'

// Styles
import classes from './CustomTable.module.scss'

const Table = ({ children }) => {
  const _children = React.Children.toArray(children)

  const headers = []
  const body = []
  _children.filter((item, index) => {
    if (item.type.displayName === 'TableHeader') {
      headers.push(
        React.cloneElement(item, {
          key: index
        })
      )
    }

    if (item.type.displayName === 'TableBody') {
      body.push(
        React.cloneElement(item, {
          key: index
        })
      )
    }
  })

  return (
    <table className={classes.table}>
      {headers}
      {body}
    </table>
  )
}

const TableHeader = ({ items = [] }) => {
  return (
    <thead>
      <tr>
        {items.map((i, index) => (
          <th key={i.name} style={{ width: `${i.width}px`, textAlign: i.align }}>
            {i.name}
          </th>
        ))}
      </tr>
    </thead>
  )
}
TableHeader.displayName = 'TableHeader'

const TableBody = ({ children }) => {
  return <tbody>{children}</tbody>
}
TableBody.displayName = 'TableBody'

const TableRow = ({ children, className, onClick, expanded, id }) => {
  const _children = React.Children.toArray(children)
  // const [open, setOpen] = React.useState(expanded)
  const [expandable, setExpandable] = React.useState([])
  const [cells, setCells] = React.useState([])

  React.useEffect(() => {
    _children.filter((item, index) => {
      if (item.type === 'td') {
        setCells(state => [
          ...state,
          React.cloneElement(item, {
            key: index
          })
        ])
      }

      if (item.type) {
        if (item.type.displayName === 'TableRowExpandable') {
          setExpandable(state => [
            ...state,
            React.cloneElement(item, {
              key: index
            })
          ])
        }
      }
    })
  }, [])

  const handleOnClick = () => {
    // setOpen(!open)
    if (typeof onClick === 'function') onClick()
  }

  return (
    <React.Fragment>
      <tr id={id} onClick={handleOnClick} className={className}>
        {cells}
      </tr>
      <tr id={id} className={classes.expandable}>
        <td colSpan={cells.length}>
          <div className={`${classes.container} ${expanded ? classes.open : ''}`}>
            <div className={classes.content}>{expandable}</div>
          </div>
        </td>
      </tr>
    </React.Fragment>
  )
}
TableRow.displayName = 'TableRow'

const TableRowExpandable = ({ children }) => {
  return children
}
TableRowExpandable.displayName = 'TableRowExpandable'

export { Table, TableHeader, TableBody, TableRow, TableRowExpandable }
