import React from 'react'
import Typography from '@material-ui/core/Typography'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import startCase from 'lodash/startCase'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList/ViewList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const ReconTIle = props => {
  const { services } = props
  const [claims, setClaims] = React.useState({
    recon: { count: 0, data: [] }
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'latest.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  // url: ,

  React.useEffect(() => {
    Promise.all([
      services.getItems({
        limit: 10,
        sort: 'latest.date.due|desc',
        range: 'this-week'
      }),
      services.getItemCount()
    ]).then(values => {
      const [claimResponse, claimCountResponse] = values
      const allClaims = claimResponse.data.data.results
      const allClaimsCount = claimCountResponse.data.data
      setClaims(prevState => ({ ...prevState, recon: { data: allClaims, count: allClaimsCount } }))
    })
  }, [])

  const handleFilterChange = category => value => {
    services.getItems({ category, range: value, limit: 10, sort: 'latest.due.date|desc' }).then(({ data: res }) => {
      const { data } = res
      setClaims(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
    })
  }

  const handleOnReview = claim_number => {
    props.history.push(`/claims/reconciled/${claim_number}`)
  }

  const handleOnShowViewList = category => () => {
    let title = startCase(`${category} Claims`)
    if (category === 'recon') title = 'Reconciled Claims'

    setViewList({ ...initialValue, show: true, title, category })

    services.getItems({ category, limit: 10, sort: 'latest.date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    // TO BE EDITED, WAITING FOR CLAIMS SUPERVISOR API SERVICES TO BE UP
    // mock datas from med consultant
    services.getItems(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnClickItemViewList = ({ category, claim_number }) => {
    if (category === 'recon') handleOnReview(claim_number)
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='reconciled-claim'
        count={{ color: 3, value: claims.recon['count'], id: 'reconciled-claim-count' }}
        sizes={{ sm: 12 }}
        title='Reconciled Claims'
        viewList={
          <Typography
            id='rc-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList('recon')}
          >
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange('recon')}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              <TableCell id='rc-claim-no'>Claim No.</TableCell>
              <TableCell id='rc-due-date'>Due Date</TableCell>
              <TableCell id='rc-facility-name'>Facility Name</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {claims.recon.data.map((item, index) => (
              <TableRow key={item.claim_number}>
                <TableCell id={`rc-claim-no-${index}`}>{item.claim_number}</TableCell>
                <TableCell id={`rc-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                <TableCell id={`rc-facility-name-${index}`}>{item.facility.name}</TableCell>
                <TableCell id={`rc-view-claim-${index}`}>
                  <Typography
                    id={`view-claim-${index}`}
                    variant='caption'
                    className={`text-link underline`}
                    onClick={() => handleOnReview(item.claim_number)}
                  >
                    View Claim
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        onItemClick={handleOnClickItemViewList}
      />
    </React.Fragment>
  )
}

ReconTIle.displayName = 'DashboardTileComponent'

export default ReconTIle
