import { combineReducers } from 'redux'
import claimReducer from './claimReducer'
import claimTypeReducer from './claimTypeReducer'
import suggestionsReducer from './suggestionsReducer'
import dropDownReducer from './dropDownReducer'
import procedureReducer from './procedureReducer'
import hospitalItemReducer from './hospitalItemReducer'
import professionalFeeReducer from './professionalFeeReducer'
import discrepancyReducer from './discrepancyReducer'
import errorReducer from './errorReducer'
import escalationReducer from './escalationReducer'
import reassignReducer from './reassignReducer'
import rejectReducer from './rejectReducer'
import reconReducer from './reconReducer'
import utilizationReducer from './utilizationReducer'

const reducers = combineReducers({
  claim: claimReducer,
  claimType: claimTypeReducer,
  suggestions: suggestionsReducer,
  dropDown: dropDownReducer,
  procedure: procedureReducer,
  hospitalItem: hospitalItemReducer,
  professionalFee: professionalFeeReducer,
  discrepancy: discrepancyReducer,
  error: errorReducer,
  escalations: escalationReducer,
  reassign: reassignReducer,
  rejectStatus: rejectReducer,
  recon: reconReducer,
  title: utilizationReducer
})

export default reducers
