import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  facility_id: Joi.string()
    .trim()
    .label('Medical Partner ID')
    .messages({ 'string.empty': `"Medical Partner ID" is required.` })
    .required(),
  facility_name: Joi.string()
    .trim()
    .label('Medical Partner (Facility) Name')
    .messages({ 'string.empty': `"Medical Partner (Facility) Name" is required.` })
    .required(),
  date_from: Joi.date()
    .label('Admission Date (From)')
    .required()
    .max('now'),
  date_to: Joi.date()
    .label('Admission Date (To)')
    .required()
    .min(Joi.ref('date_from'))
    .messages({
      'date.min': `"Admission Date (From)" should be on or before "Admission Date (To)"`
    }),
  room_type: Joi.string()
    .trim()
    .label('Room Type')
    .messages({ 'string.empty': `"Room Type" is required.` })
    .required(),
  room_rate: Joi.number()
    .label('Room Rate')
    .messages({ 'string.empty': `"Room Rate" is required.` })
    .required(),
  pro_id: Joi.string()
    .trim()
    .label('PRO ID')
    .allow('', null),
  pro_name: Joi.string()
    .trim()
    .label('PRO Name')
    .allow('', null),
  claim_sub_type: Joi.string()
    .trim()
    .label('Claim Sub Type')
    .required()
    .messages({
      'string.empty': `"Claim Sub Type" is required.`
    }),
  claim_sub_sub_type: Joi.string()
    .trim()
    .label('Claim Sub-subType')
    .allow('', null),
  with_corporate_guarantee: Joi.string()
    .trim()
    .label('Corporate Guaranty')
    .allow('', null),
  loa_notes: Joi.string()
    .trim()
    .allow('', null)
}).options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
