import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

// Components
import SearchTextComponent from 'SharedComponents/SearchTextComponent'

// Styles
import classes from './ClaimListHeader.module.scss'

export default function ClaimListHeader (props) {
  const { onClickAdd, onClickProviderSchedule, count, status, type = '', inputChange = null, disabled = false } = props
  const handleChange = event => {
    if (typeof inputChange === 'function') {
      inputChange(event.target.value)
    }
  }

  return (
    <div className={classes.root}>
      <div className={classes.title}>
        <Typography variant='subtitle2'>Claims ({count})</Typography>
      </div>

      <div className={classes.wrapper}>
        <SearchTextComponent onChange={handleChange} />

        {status !== 'Completed' && (
          <React.Fragment>
            {(type === 'APE' || type === 'ECU' || type === 'PPE') && (
              <Button
                id={`${type.toLowerCase()}-schedule`}
                variant='contained'
                color='primary'
                className={`${classes.button} button-positive`}
                onClick={onClickProviderSchedule}
              >
                Provider's {type} Schedule
              </Button>
            )}

            <Button
              id='add-claim'
              variant='contained'
              color='primary'
              className={`${classes.button} button-positive`}
              onClick={onClickAdd}
              disabled={disabled}
            >
              Add Claim
            </Button>
          </React.Fragment>
        )}
      </div>
    </div>
  )
}
