import React from 'react'

// Components
import DiscrepancyBase from 'Pages/Protected/Claims/ClaimsViewer/Components/DiscrepancyBase/DiscrepancyBase'
import DropDown from 'SharedComponents/DropDown/DropDown'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Services
import CustomerCareService from 'Services/Api/CustomerCareService'
import { AutoFill, AutoFillInitializer } from '../../AutoFill'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { setClaimBaseDetails, resetSuggestionObject, setSuggestionObject } from '../../Store/actions'
// Styles
import classes from './BaseDetails.module.scss'

// Constants
// import ReimbursementTypes from 'Constant/reimbursementTypes'
// Constants
import ClaimTypes from './../../../Constant/ClaimTypes'

// On Init
// const reimbursementTypes = ReimbursementTypes.dropdown()

export default function BaseDetails (props) {
  const {
    onChange,
    claim,
    disableControls,
    onChangeReimbursementType = () => {},
    handleMemberAccountNumber,
    permissions,
    requiredFields = {
      approval_code: true,
      loa_number: true,
      reimbursement_type: true
    },
    showReimbursement = false,
    showApprovalCode = true,
    showLoaNumber = true,
    showCompany = false,
    isDental = false
  } = props

  const dispatch = useDispatch()

  const claim_type = useSelector(state => state.claimType)
  const discrepancy = useSelector(state => state.discrepancy)
  const errors = useSelector(state => state.error)

  const [memberAccountNumber, setMemberAccountNumber] = React.useState()

  const handleLoaDetails = event => {
    const { value } = event.target
    if (value.trim()) {
      CustomerCareService.getLoaDetails({ approval_code: value }).then(res => {
        const loa = res.data.data
        if (loa) {
          AutoFill({
            loa,
            claim_type
          })
        }
      })
    }
  }

  const handleOnBlurMemberAccountNumber = event => {
    if (!handleMemberAccountNumber) return

    const value = event.target.value.trim()

    if (value === memberAccountNumber) return

    dispatch(resetSuggestionObject({ field: 'contract' }))
    setMemberAccountNumber(value)
    dispatch(setClaimBaseDetails({ field: 'contract_year_from', value: null }))
    dispatch(setClaimBaseDetails({ field: 'contract_year_to', value: null }))

    if (!value) return

    LoadingService.loading(true)
    CustomerCareService.getContractList({ member_account_number: value }).then(({ data: res }) => {
      LoadingService.loading(false)

      if (!res) return

      const { latest_contract } = res
      const { coverage_start_date: from, coverage_end_date: to } = latest_contract.custom_metadata

      dispatch(
        setSuggestionObject({
          field: 'contract',
          suggestion: { years: [{ from, to }] }
        })
      )
    })
  }

  return (
    <React.Fragment>
      {permissions.read && (
        <React.Fragment>
          <div className={classes.groups}>
            {showApprovalCode && (
              <div className={classes['input']}>
                <DiscrepancyBase
                  id='dp-approval-code'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.approval_code
                  }}
                >
                  <TextField
                    id='approval-code'
                    required={!!requiredFields.approval_code}
                    label='Approval Code'
                    placeholder={claim.approval_code || 'Pending'}
                    value={claim.approval_code}
                    disabled={disableControls}
                    onChange={event => onChange({ field: 'approval_code', value: event.target.value })}
                    onBlur={handleLoaDetails}
                    error={errors.approval_code}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>
            )}

            {showLoaNumber && (
              <div className={classes['input']}>
                <DiscrepancyBase
                  id='dp-loa-number'
                  discrepancy={{
                    message: 'Failed',
                    result: discrepancy.loa_number
                  }}
                >
                  <TextField
                    id='loa-number'
                    required={!!requiredFields.loa_number}
                    label='LOA No.'
                    placeholder={claim.loa_number || 'Pending'}
                    value={claim.loa_number}
                    disabled={disableControls}
                    onChange={event => onChange({ field: 'loa_number', value: event.target.value })}
                    error={errors.loa_number}
                    readOnly={!permissions.update}
                  />
                </DiscrepancyBase>
              </div>
            )}

            {showReimbursement && (
              <div className={classes['input']}>
                <DropDown
                  id='reimbursement-type'
                  label='Reimbursement Type'
                  required={!!requiredFields.reimbursement_type}
                  items={ClaimTypes.Reimbursement.subType}
                  value={claim.reimbursement_type}
                  disabled={disableControls}
                  onChange={event =>
                    onChangeReimbursementType({ field: 'reimbursement_type', value: event.target.value })
                  }
                  error={errors.reimbursement_type}
                  readOnly={!permissions.update}
                />
              </div>
            )}
            {showCompany && (
              <React.Fragment>
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-customer-account-number'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.customer_account_number
                    }}
                  >
                    <TextField
                      id='company-account-number'
                      label='Customer (Company) Account Number'
                      value={claim.customer_account_number}
                      disabled={disableControls}
                      required
                      onChange={event => onChange({ field: 'customer_account_number', value: event.target.value })}
                      error={errors.customer_account_number}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
                <div className={classes['input']}>
                  <DiscrepancyBase
                    id='dp-company-name'
                    discrepancy={{
                      message: 'Failed',
                      result: discrepancy.company_name
                    }}
                  >
                    <TextField
                      id='company-name'
                      label='Company Name'
                      value={claim.company_name}
                      disabled={disableControls}
                      required
                      onChange={event => onChange({ field: 'company_name', value: event.target.value })}
                      error={errors.company_name}
                      readOnly={!permissions.update}
                    />
                  </DiscrepancyBase>
                </div>
              </React.Fragment>
            )}
          </div>

          <div className={classes.groups}>
            <div className={classes['input']}>
              <DiscrepancyBase
                id='dp-member-account-number'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.member_account_number
                }}
              >
                <TextField
                  id='member-account-number'
                  required
                  label='Member Account No.'
                  value={claim.member_account_number}
                  disabled={disableControls}
                  onChange={event =>
                    onChange({
                      field: 'member_account_number',
                      value: event.target.value
                    })
                  }
                  onBlur={handleOnBlurMemberAccountNumber}
                  error={errors.member_account_number}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>
            <div className={classes['input']}>
              <DiscrepancyBase
                id='dp-member-last-name'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.member_last_name
                }}
              >
                <TextField
                  id='member-last-name'
                  required
                  label='Member Last Name'
                  value={claim.member_last_name}
                  disabled={disableControls}
                  onChange={event => onChange({ field: 'member_last_name', value: event.target.value })}
                  error={errors.member_last_name}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>

            <div className={classes['input']}>
              <DiscrepancyBase
                id='dp-member-first-name'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.member_first_name
                }}
              >
                <TextField
                  id='member-first-name'
                  required
                  label='Member First Name'
                  value={claim.member_first_name}
                  disabled={disableControls}
                  onChange={event =>
                    onChange({
                      field: 'member_first_name',
                      value: event.target.value
                    })
                  }
                  error={errors.member_first_name}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>

            <div className={classes['input']}>
              <DiscrepancyBase
                id='dp-member-middle-name'
                discrepancy={{
                  message: 'Failed',
                  result: discrepancy.member_middle_name
                }}
              >
                <TextField
                  id='member-middle-name'
                  required={!!isDental}
                  label='Member Middle Name'
                  value={claim.member_middle_name}
                  disabled={disableControls}
                  onChange={event =>
                    onChange({
                      field: 'member_middle_name',
                      value: event.target.value
                    })
                  }
                  error={errors.member_middle_name}
                  readOnly={!permissions.update}
                />
              </DiscrepancyBase>
            </div>
          </div>
        </React.Fragment>
      )}
      <AutoFillInitializer />
    </React.Fragment>
  )
}
