import React from 'react'
import Typography from '@material-ui/core/Typography'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList/ViewList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const ClaimsForApprovalTile = props => {
  const { services } = props
  const [claims, setClaims] = React.useState({ count: 0, data: [] })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'latest.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  // url: ,

  React.useEffect(() => {
    Promise.all([
      services.getItems({
        limit: 10,
        sort: 'latest.date.due|desc',
        range: 'this-week'
      }),
      services.getItemCount()
    ]).then(values => {
      const [claimResponse, claimCountResponse] = values
      const claims = claimResponse.data.data.results
      const claimsCount = claimCountResponse.data.data
      setClaims({ data: claims, count: claimsCount })
    })
  }, [])

  const handleFilterChange = category => value => {
    services.getItems({ category, range: value, limit: 10, sort: 'latest.due.date|desc' }).then(({ data: res }) => {
      const { data } = res

      setClaims(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
    })
  }

  const handleOnItemClick = claim_number => {
    props.history.push(`/claims/for-approval/${claim_number}`)
  }

  const handleOnShowViewList = category => () => {
    let title = 'Claims For Approval'

    setViewList({ ...initialValue, show: true, title, category: 'for-approval' })

    services.getItems({ category, limit: 10, sort: 'latest.date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    // TO BE EDITED, WAITING FOR CLAIMS SUPERVISOR API SERVICES TO BE UP
    // mock datas from med consultant
    services.getItems(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnClickItemViewList = ({ category, claim_number }) => {
    handleOnItemClick(claim_number)
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='claims-for-approval'
        count={{ color: 3, value: claims['count'], id: 'claims-for-approval-count' }}
        sizes={{ sm: 12 }}
        title='Claims For Approval'
        isFilterShown={false}
        viewList={
          <Typography
            id='fa-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList('for-approval')}
          >
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange('for-approval')}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              <TableCell id='fa-claim-no'>Claim No.</TableCell>
              <TableCell id='fa-due-date'>Due Date</TableCell>
              <TableCell id='fa-facility-name'>Facility Name</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {claims.data.map((item, index) => (
              <TableRow key={item.claim_number}>
                <TableCell id={`fa-claim-no-${index}`}>{item.claim_number}</TableCell>
                <TableCell id={`fa-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                <TableCell id={`fa-facility-name-${index}`}>{item.facility.name}</TableCell>
                <TableCell id={`fa-view-claim-${index}`}>
                  <Typography
                    id={`view-claim-${index}`}
                    variant='caption'
                    className={`text-link underline`}
                    onClick={() => handleOnItemClick(item.claim_number)}
                  >
                    View Claim
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        onItemClick={handleOnClickItemViewList}
      />
    </React.Fragment>
  )
}

ClaimsForApprovalTile.displayName = 'DashboardTileComponent'

export default ClaimsForApprovalTile
