import ClaimsAdjudicationServices from './../ClaimsAdjudicationServices'
import instance from '../instance'
const axios = instance('/c/cvs')

export default class ForApprovalServices extends ClaimsAdjudicationServices {
  static getItems (params) {
    return axios({
      url: `/claims/for-approval`,
      method: 'GET',
      params
    })
  }

  static getItemCount (params) {
    return axios({
      url: `/claims/for-approval/count`,
      method: 'GET',
      params
    })
  }

  static getClaim ({ claim_number }) {
    return axios({
      url: `/claims/for-approval/${claim_number}`,
      method: 'GET'
    })
  }

  static approve ({ claim_number }) {
    return axios({
      url: `/claims/for-approval/${claim_number}/approve`,
      method: 'POST'
    })
  }

  static getComments ({ claim_number }) {
    return axios({
      url: `/claims/for-approval/${claim_number}/comments`,
      method: 'GET'
    })
  }

  static comment ({ claim_number, data }) {
    return axios({
      url: `/claims/for-approval/${claim_number}/comment`,
      method: 'POST',
      params: { claim_number },
      data
    })
  }

  static getUtilizations ({ member_account_no, type, icd_code }) {
    return axios({
      url: `/claims/for-approval/${member_account_no}/utilizations`,
      method: 'GET',
      params: { type, icd_code }
    })
  }

  static getPartners ({ member_account_no, params }) {
    return axios({
      url: `/claims/for-approval/${member_account_no}/partners`,
      method: 'GET',
      params,
      data: { member_account_no }
    })
  }

  static update ({ claim_number, body }) {
    return new Promise(resolve => resolve())
  }
}
