import React from 'react'
import { Switch, Route } from 'react-router-dom'

// Routes by role
import BatchEncoderRoutes from './BatchEncoder/Routes'
import ClaimsDetailsEncoderRoutes from './ClaimsDetailsEncoder/Routes'
import ClaimsAnalystRoutes from './ClaimsAnalyst/Routes'
import PaymentRequestRoutes from './PaymentRequest/Routes'
import MedicalConsultantRoutes from './MedicalConsultant/Routes'
import ClaimsReconciliationRoutes from './ClaimsReconciliation/Routes'
import ClaimsVerifierSupervisorRoutes from './ClaimsVerifierSupervisor/Routes'
import BatchEncodingServices from 'Services/Api/BatchEncodingServices'

export default function ClaimsModule ({ history }) {
  const [userRole, setUserRole] = React.useState('')
  const [isLoading, setIsLoading] = React.useState(true)

  React.useEffect(() => {
    document.title = 'HIMS - Claims'
    const role = localStorage.getItem('role')
    setUserRole(role)
    setIsLoading(false)
  }, [])

  const batchEncoderLogic = () => {
    if (localStorage.getItem('from_login')) {
      BatchEncodingServices.createBundle().then(res => {
        const { data } = res.data
        localStorage.removeItem('from_login')
        history.push(`/bundles/${data}/create`)
      })
    } else return <Route path='/' component={BatchEncoderRoutes} />
  }

  return (
    <React.Fragment>
      {!isLoading && (
        <Switch>
          {userRole === 'BATCH_ENCODER' && batchEncoderLogic()}

          {userRole === 'CLAIMS_ENCODER' && <Route path='/' component={ClaimsDetailsEncoderRoutes} />}
          {userRole === 'CLAIMS_DETAILS_ENCODER' && <Route path='/' component={ClaimsDetailsEncoderRoutes} />}

          {userRole === 'CLAIMS_VERIFIER' && <Route path='/' component={ClaimsAnalystRoutes} />}
          {userRole === 'CLAIMS_ANALYST' && <Route path='/' component={ClaimsAnalystRoutes} />}

          {userRole === 'PR_OFFICER' && <Route path='/' component={PaymentRequestRoutes} />}

          {userRole === 'MEDICAL_CONSULTANT' && <Route path='/' component={MedicalConsultantRoutes} />}

          {userRole === 'CLAIMS_RECON' && <Route path='/' component={ClaimsReconciliationRoutes} />}

          {userRole === 'CLAIMS_VERIFIER_SUPERVISOR' && <Route path='/' component={ClaimsVerifierSupervisorRoutes} />}
          {userRole === 'CLAIMS_SUPERVISOR' && <Route path='/' component={ClaimsVerifierSupervisorRoutes} />}
        </Switch>
      )}
    </React.Fragment>
  )
}
