import IndexedDb2 from 'Utilities/IndexedDb2'
import CPT_ICDServices from 'Services/Api/CPT_ICDServices'
const dbName = 'himsDb'
const stores = [
  { name: 'user_data', keyPath: ['key'] },
  { name: 'config', keyPath: ['key'] },
  { name: 'cpt', keyPath: ['key'] },
  { name: 'cpt_list', keyPath: ['_id', 'label'] },
  { name: 'icd10', keyPath: ['key'] },
  { name: 'icd10_list', keyPath: ['_id', 'full_code', 'full_description'] }
]

export default class ConfigController2 {
  static async getUserManagementData () {
    const db = await IndexedDb2.connect({ dbName, stores })

    if (!db) return

    let result = await IndexedDb2.getData({ db, storeName: 'user_data' })

    if (!result.length) return

    let user_data = {}

    for (let i of result) user_data[i.key] = i.value

    return user_data
  }

  static async setUserManagementData (data) {
    const db = await IndexedDb2.connect({ dbName, stores })

    if (!db) return

    await IndexedDb2.update({ db, storeName: 'user_data', data })
  }

  static async clearUserManagementData (data) {
    const db = await IndexedDb2.connect({ dbName, stores })

    if (!db) return

    await IndexedDb2.clear({ db, storeName: 'user_data' })
  }

  static async initializeBaseUrls () {
    const db = await IndexedDb2.connect({ dbName, stores })

    if (!db) return

    let result = await IndexedDb2.getData({ db, storeName: 'config' })

    if (!result.length) return

    let config = {}

    for (let i of result) config[i.key] = i.value

    if (config['PARTNER_URL']) {
      // window.localStorage.partnerUrl = config['PARTNER_URL']
    }

    if (config['CLIENT_URL']) {
      // window.localStorage.clientUrl = config['CLIENT_URL']
    }
  }

  static async updateCPT_ICD ({ cpt, icd10, IcdCptStatusSet }) {
    const db = await IndexedDb2.connect({ dbName, stores })

    if (!db) return

    let stored_cpt = await IndexedDb2.getData({ db, storeName: 'cpt' })
    let stored_icd10 = await IndexedDb2.getData({ db, storeName: 'icd10' })

    if (!stored_cpt.length || !stored_icd10.length) {
      await IndexedDb2.update({ db, storeName: 'cpt', data: cpt })
      await IndexedDb2.update({ db, storeName: 'icd10', data: icd10 })
      this.setCPT_ICD({ IcdCptStatusSet, cpt_count: cpt.count, icd10_count: icd10.count })
    }
  }

  static async setCPT_ICD ({ IcdCptStatusSet, cpt_count, icd10_count }) {
    const db = await IndexedDb2.connect({ dbName, stores })

    const data_limit = process.env.REACT_APP_LIMIT_ICD_CPT ? parseInt(process.env.REACT_APP_LIMIT_ICD_CPT) : -1

    if (!db) return

    let already_fetched = {}

    IcdCptStatusSet({
      show: true,
      cpt_fetched: 0,
      cpt_total: data_limit > cpt_count || data_limit === -1 ? cpt_count : data_limit,
      icd10_fetched: 0,
      icd10_total: data_limit > icd10_count || data_limit === -1 ? icd10_count : data_limit
    })

    const fetchMore = async (args = {}) => {
      let { type, collectrion_name, limit = 2000, skip = 0, mappings } = args

      const item_success_insert = () => {
        IcdCptStatusSet(p => {
          const name = `${collectrion_name}_fetched`
          return {
            ...p,
            [name]: p[name] + 1
          }
        })
      }
      CPT_ICDServices.getMany({ type, limit, skip }).then(async ({ data }) => {
        await IndexedDb2.add({ db, storeName: `${collectrion_name}_list`, data, mappings, item_success_insert })

        if (!already_fetched[type]) already_fetched[type] = 0

        already_fetched[type] += data.length

        if (data.length < limit) return

        if (data_limit === -1) {
          fetchMore({ ...args, skip: already_fetched[type] })
        } else if (already_fetched[type] < data_limit) {
          const remaining = data_limit - already_fetched[type]

          const _limit = remaining > 2000 ? 2000 : remaining
          fetchMore({ ...args, skip: already_fetched[type], limit: _limit })
        }
      })
    }

    const limit = data_limit > -1 && data_limit < 2000 ? data_limit : 2000

    fetchMore({ type: 'icd10-codes', collectrion_name: 'icd10', limit })
    fetchMore({ type: 'cpts', collectrion_name: 'cpt', mappings: [{ from: 'description', to: 'label' }], limit })
  }
}
