export default class IndexedDb2 {
  static async connect ({ dbName, stores = [], version = 1 }) {
    return new Promise((resolve, reject) => {
      let request = window.indexedDB.open(dbName /*, version */)

      request.onupgradeneeded = function (event) {
        const db = event.target.result

        for (const store of stores) {
          db.objectStoreNames.contains(store.name) || db.createObjectStore(store.name, { keyPath: store.keyPath })
        }
      }

      request.onsuccess = function (event) {
        const db = event.target.result
        resolve(db)
      }
      request.onerror = function (event) {
        console.log('[onerror]', request.error)
        reject(request.error)
      }
    })
  }

  static async update ({ db, storeName, mode = 'readwrite', data }) {
    const transaction = db.transaction(storeName, mode)
    const objectStore = transaction.objectStore(storeName)
    const { keyPath } = objectStore
    for (let key in data) objectStore.put({ [keyPath]: key, value: data[key] })
  }

  static async add ({ db, storeName, mode = 'readwrite', data, mappings = null, item_success_insert }) {
    let index = 0
    const _add = () => {
      if (index !== data.length) {
        setTimeout(() => {
          const transaction = db.transaction(storeName, mode)
          const objectStore = transaction.objectStore(storeName)

          const item = data[index]

          if (item) {
            const _item = mappings ? this.manipulateItem({ item, mappings }) : item
            objectStore.add(_item)
            if (item_success_insert) item_success_insert()
          }

          _add()
          index++
        }, 1)
      }
    }
    _add()
  }

  static async clear ({ db, storeName, mode = 'readwrite' }) {
    const transaction = db.transaction(storeName, mode)
    const objectStore = transaction.objectStore(storeName)

    return new Promise(resolve => {
      objectStore.clear().onsuccess = event => {
        resolve()
      }
    })
  }

  static manipulateItem ({ item, mappings }) {
    const additional_data = {}
    for (const mapping of mappings) {
      const { from, to } = mapping

      try {
        additional_data[to] = item[from]
      } catch (error) {
        console.log(error)
        console.log(item)
        additional_data[to] = Date.now()
      }
    }
    return { ...item, ...additional_data }
  }

  static async getData ({ db, storeName }) {
    const transaction = db.transaction(storeName, 'readonly')
    const objectStore = transaction.objectStore(storeName)

    return new Promise(resolve => {
      objectStore.getAll().onsuccess = function (event) {
        resolve(event.target.result)
      }
    })
  }
}
