import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import classes from './TextFieldSubmit.module.scss'
export default function TextFieldSubmit (props) {
  const { label = '', required, readOnly, error, onSubmit, ...other } = props

  return (
    <div className={classes.root}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {props.value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase className={classes.input} {...other} autoComplete='no' />
              <Button
                className={`button-positive ${classes.submit}`}
                onClick={onSubmit}
                variant='contained'
                color='primary'
                autoFocus
              >
                Send
              </Button>
            </Paper>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}
