import ClaimsAdjudicationServices from './../ClaimsAdjudicationServices'
import instance from './../instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/cvs')

export default class ReconciledServices extends ClaimsAdjudicationServices {
  static getItems (params) {
    return axios({
      url: `/claims/reconciled`,
      method: 'GET',
      params
    })
  }

  static getItemCount (params) {
    return axios({
      url: `/claims/reconciled/count`,
      method: 'GET',
      params
    })
  }

  static getClaim ({ claim_number }) {
    return axios({
      url: `/claims/reconciled/${claim_number}`,
      method: 'GET'
    })
  }

  static update ({ claim_number, body }) {
    return axios({
      url: `/claims/reconciled/${claim_number}`,
      method: 'PUT',
      data: body
    })
  }

  static approve ({ claim_number }) {
    return axios({
      url: `/claims/reconciled/${claim_number}/approve`,
      method: 'POST'
    })
  }

  static escalate ({ claim_number, data }) {
    return axios({
      url: `/claims/reconciled/${claim_number}/escalate`,
      method: 'POST',
      data
    })
  }

  static reject ({ claim_number }) {
    return axios({
      url: `/claims/reconciled/${claim_number}/reject`,
      method: 'POST'
    })
  }

  static lookups ({ on, params }) {
    return axios({
      url: `/lookups/${on}`,
      method: 'GET',
      params: { limit: 10, ...params },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }
  static getComments ({ claim_number }) {
    return axios({
      url: `/claims/reconciled/${claim_number}/comments`,
      method: 'GET'
    })
  }

  static comment ({ claim_number, data }) {
    return axios({
      url: `/claims/reconciled/${claim_number}/comment`,
      method: 'POST',
      params: { claim_number },
      data
    })
  }

  static getNotes ({ claim_number }) {
    return axios({
      url: `/claims/reconciled/${claim_number}/notes`,
      method: 'GET'
    })
  }

  static getUtilizations ({ member_account_no, type, icd_code }) {
    return axios({
      url: `/claims/reconciled/${member_account_no}/utilizations`,
      method: 'GET',
      params: { type, icd_code }
    })
  }

  static getPartners ({ member_account_no, params }) {
    return axios({
      url: `/claims/reconciled/${member_account_no}/partners`,
      method: 'GET',
      params,
      data: { member_account_no }
    })
  }

  // MOCK ENDPOINT
  static getUsers (params) {
    return axios({
      url: `/lookups/users`,
      method: 'GET',
      params,
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }
}
