import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import BaseDetails from '../BaseDetails/BaseDetails'
import DiagnosisList from '../DiagnosisList/DiagnosisList'
import ProcedureForm from '../ProcedureForm/ProcedureForm'
import ProfessionalFeeForm from '../ProfessionalFeeForm/ProfessionalFeeForm'
import TextField from 'SharedComponents/TextField/CustomTextField'
// import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import DatePicker from 'SharedComponents/DatePicker/DatePicker'
import DropDown from 'SharedComponents/DropDown/DropDown'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import ProcedureList from '../ProcedureList/ProcedureList'
import ProfessionalFeeList from '../ProfessionalFeeList/ProfessionalFeeList'
import ClaimFooter from '../ClaimFooter/ClaimFooter'

// Services
import ClaimsEncodingServices from 'Services/Api/ClaimsEncodingServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utilities
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './ClaimOutPatientProcedure.module.scss'

// Validator
import { SoftValidatorService, SoftValidatorAlertComponent } from '../BaseDetails/SoftValidator'
import DiagnosisListValidator from '../DiagnosisList/DiagnosisListValidator'
import MainValidator from './../../Validators/Claim'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { setClaimFields, setClaimBaseDetails, initSuggestions, setGrandTotal } from '../../Store/actions'

export default function ClaimOutPatientProcedure (props) {
  const dispatch = useDispatch()
  const { claim, onChangeFacility, onChangeDoctor, onChangeAnaesthesiologist, getClaimDetails } = props
  const suggestions = useSelector(state => state.suggestions)
  const grandTotal = useSelector(state => state.grandTotal)

  const [isAdd, setIsAdd] = React.useState(true)

  const [batchNumber, setBatchNumber] = React.useState(null)

  const [openDialog, setOpenDialog] = React.useState(false)

  const [allowedFields, setAllowedFields] = React.useState({})
  const [lineItemFields, setlineItemFields] = React.useState({})

  React.useEffect(() => {
    const { _batchNumber } = props.match.params
    setIsAdd(props.isAdd)
    dispatch(initSuggestions())

    const _allowedFields = {
      approval_code: '',
      loa_number: '',
      member_account_number: '',
      member_first_name: '',
      member_middle_name: '',
      member_last_name: '',
      facility_id: '',
      facility_name: '',
      doctor_id: '',
      doctor_name: '',
      date_from: new Date().toISOString(),
      date_to: new Date().toISOString(),
      department: '',
      anesthesiologist_id: '',
      anesthesiologist_name: '',
      claim_sub_type: '',
      claim_sub_sub_type: '',
      with_corporate_guarantee: '',
      diagnosis: [],
      loa_notes: ''
    }

    const _lineItemFields = {
      procedures: [],
      professional_fees: []
    }

    setTimeout(() => {
      setAllowedFields(_allowedFields)
      setlineItemFields(_lineItemFields)
      dispatch(setClaimFields({ ..._allowedFields, ..._lineItemFields }))
    }, 10)

    if (!props.isAdd) {
      getClaimDetails(data => {
        dispatch(
          setClaimFields({
            approval_code: data.approval_code,
            loa_number: data.loa_number,
            member_account_number: data.member.account_number,
            member_first_name: data.member.first_name,
            member_middle_name: data.member.middle_name,
            member_last_name: data.member.last_name,
            facility_id: data.facility.id,
            facility_name: data.facility.name,
            doctor_id: data.doctor.id,
            doctor_name: data.doctor.name,
            date_from: data.date.from,
            date_to: data.date.to,
            department: data.department,
            anesthesiologist_id: data.anesthesiologist.id,
            anesthesiologist_name: data.anesthesiologist.name,
            claim_sub_type: data.claim_sub.type,
            claim_sub_sub_type: data.claim_sub.sub_type,
            with_corporate_guarantee: data.with_corporate_guarantee,
            diagnosis: data.diagnosis,
            loa_notes: data.loa_notes,
            procedures: data.procedures,
            professional_fees: data.professional_fees
          })
        )
      })
    }

    setBatchNumber(_batchNumber)
  }, [])

  const totalAmount = (currentValue, i) => {
    const total =
      parseFloat(i.gross_amount) +
      parseFloat(i.vat_amount) -
      parseFloat(i.amount_paid_by_member) -
      parseFloat(i.philhealth_amount_paid)
    return currentValue + total
  }

  React.useEffect(() => {
    let sum = 0
    if (claim) {
      if (claim.procedures) sum = claim.procedures.reduce(totalAmount, sum)
      if (claim.hospital_items) sum = claim.hospital_items.reduce(totalAmount, sum)
      if (claim.professional_fees) sum = claim.professional_fees.reduce(totalAmount, sum)
    }

    dispatch(setGrandTotal(sum))
  }, [claim])

  const handleOnChangeFacility = args => {
    onChangeFacility(args)
  }

  const [errors, setErrors] = React.useState({})

  const doValidateBase = () => {
    const data = {}
    const { diagnosis, ...fields } = allowedFields
    for (let f in fields) data[f] = claim[f]

    const { error = {} } = MainValidator(Object.keys(fields))({ ...data })
    return error
  }

  const doValidateAll = () => {
    const base_errors = doValidateBase()
    const diagnosis = DiagnosisListValidator(claim.diagnosis)
    setErrors({ ...base_errors, diagnosis })
    return !!(Object.keys(base_errors).length || diagnosis.error)
  }

  const [disableControls, setDisableControls] = React.useState(false)
  const handleOnSave = async goBack => {
    if (doValidateAll()) return

    const proceed = await SoftValidatorService({
      fields: [{ 'Approval Code': claim.approval_code }, { 'LOA No.': claim.loa_number }]
    })

    if (!proceed) return

    setDisableControls(true)
    LoadingService.loading(true)

    const data = {}
    for (let f in { ...allowedFields, ...lineItemFields }) data[f] = claim[f]

    if (isAdd) {
      ClaimsEncodingServices.addClaim({ batch_number: batchNumber, data }).then(res => {
        LoadingService.loading(false)
        if (goBack) return props.history.push(`/batches/${batchNumber}`)
        props.history.push(`/`)
      })
    } else {
      const { _claimNumber } = props.match.params
      ClaimsEncodingServices.updateClaim({ batch_number: batchNumber, claim_number: _claimNumber, data }).then(res => {
        LoadingService.loading(false)
        if (goBack) return props.history.push(`/batches/${batchNumber}`)
        props.history.push(`/`)
      })
    }
  }

  return (
    <React.Fragment>
      {claim && (
        <div className={classes.root}>
          <Paper className={classes['first-container']}>
            <BaseDetails
              claim={claim}
              errors={errors}
              disableControls={disableControls}
              onChange={({ value, field }) => dispatch(setClaimBaseDetails({ value, field }))}
            />

            <div className={classes.groups}>
              <div className={classes['input']}>
                <AutoComplete
                  id='medical-partner-id'
                  required
                  label='Medical Partner ID'
                  value={claim.facility_id}
                  disabled={disableControls}
                  suggestions={suggestions.facility.id}
                  onChange={(value, isSuggestionClick) =>
                    handleOnChangeFacility({
                      field: 'facility_id',
                      value,
                      isSuggestionClick
                    })
                  }
                  error={errors.facility_id}
                />
              </div>

              <div className={classes['input']}>
                <AutoComplete
                  id='medical-partner-facility-name'
                  required
                  label='Medical Partner (Facility) Name'
                  value={claim.facility_name}
                  disabled={disableControls}
                  suggestions={suggestions.facility.name}
                  onChange={(value, isSuggestionClick) =>
                    handleOnChangeFacility({
                      field: 'facility_name',
                      value,
                      isSuggestionClick
                    })
                  }
                  error={errors.facility_name}
                />
              </div>

              <div className={classes['input']}>
                <AutoComplete
                  id='doctor-id'
                  label='Medical Partner ID'
                  value={claim.doctor_id}
                  disabled={disableControls}
                  suggestions={suggestions.doctor.id}
                  onChange={(value, isSuggestionClick) =>
                    onChangeDoctor({
                      field: 'doctor_id',
                      value,
                      isSuggestionClick
                    })
                  }
                  error={errors.doctor_id}
                />
              </div>

              <div className={classes['input']}>
                <AutoComplete
                  id='doctor-name'
                  label='Medical Partner (Doctor) Name'
                  value={claim.doctor_name}
                  disabled={disableControls}
                  suggestions={suggestions.doctor.full_name}
                  onChange={(value, isSuggestionClick) =>
                    onChangeDoctor({
                      field: 'doctor_name',
                      value,
                      isSuggestionClick
                    })
                  }
                  error={errors.doctor_name}
                />
              </div>
            </div>

            <div className={classes.groups}>
              <div className={classes['input']}>
                <DatePicker
                  id='date-of-service'
                  required
                  label='Date of Service'
                  value={claim.date_to}
                  disabled={disableControls}
                  onChange={event => {
                    dispatch(
                      setClaimBaseDetails({
                        value: event.toISOString(),
                        field: 'date_to'
                      })
                    )
                    dispatch(
                      setClaimBaseDetails({
                        value: event.toISOString(),
                        field: 'date_from'
                      })
                    )
                  }}
                  error={errors.date_to}
                />
              </div>

              <div className={classes['input']}>
                <TextField
                  id='department'
                  label='Department'
                  value={claim.department}
                  disabled={disableControls}
                  onChange={event =>
                    dispatch(
                      setClaimBaseDetails({
                        value: event.target.value,
                        field: 'department'
                      })
                    )
                  }
                  error={errors.department}
                />
              </div>

              <div className={classes['input']}>
                <AutoComplete
                  id='anesthesiologist-id'
                  label='Medical Partner ID (Anesthesiologist)'
                  value={claim.anesthesiologist_id}
                  disabled={disableControls}
                  suggestions={suggestions.anesthesiologist.id}
                  onChange={(value, isSuggestionClick) =>
                    onChangeAnaesthesiologist({
                      value,
                      field: 'anesthesiologist_id',
                      isSuggestionClick
                    })
                  }
                  error={errors.anesthesiologist_id}
                />
              </div>

              <div className={classes['input']}>
                <AutoComplete
                  id='anesthesiologist-name'
                  label='Medical Partner (Anesthesiologist) Name'
                  value={claim.anesthesiologist_name}
                  disabled={disableControls}
                  suggestions={suggestions.anesthesiologist.full_name}
                  onChange={(value, isSuggestionClick) =>
                    onChangeAnaesthesiologist({
                      value,
                      field: 'anesthesiologist_name',
                      isSuggestionClick
                    })
                  }
                  error={errors.anesthesiologist_name}
                />
              </div>
            </div>

            <div className={classes.groups}>
              <div className={classes['input']}>
                <DropDown
                  id='claims-sub-type'
                  required
                  label='Claim Sub Type'
                  items={[
                    {
                      text: 'Medical (diagnostic, lab)',
                      value: 'Medical (diagnostic, lab)'
                    },
                    { text: 'Surgical', value: 'Surgical' },
                    { text: 'Treatment', value: 'Treatment' },
                    { text: 'Therapy', value: 'Therapy' }
                  ]}
                  value={claim.claim_sub_type}
                  // disabled={disableControls}
                  onChange={event =>
                    dispatch(
                      setClaimBaseDetails({
                        value: event.target.value,
                        field: 'claim_sub_type'
                      })
                    )
                  }
                  error={errors.claim_sub_type}
                />
              </div>

              <div className={classes['input']}>
                <DropDown
                  id='claims-sub-sub-type'
                  label='Claim Sub-subType'
                  items={[
                    { text: 'Physical', value: 'Physical' },
                    { text: 'Speech', value: 'Speech' },
                    { text: 'Occupational', value: 'Occupational' }
                  ]}
                  value={claim.claim_sub_sub_type}
                  onChange={event =>
                    dispatch(
                      setClaimBaseDetails({
                        value: event.target.value,
                        field: 'claim_sub_sub_type'
                      })
                    )
                  }
                  error={errors.claim_sub_sub_type}
                />
              </div>

              <div className={classes['input']}>
                <TextField
                  id='with-corporate-guaranty'
                  label='With Corporate Guaranty?'
                  value={claim.with_corporate_guarantee}
                  onChange={event =>
                    dispatch(
                      setClaimBaseDetails({
                        value: event.target.value,
                        field: 'with_corporate_guarantee'
                      })
                    )
                  }
                  readOnly
                  error={errors.with_corporate_guarantee}
                />
              </div>
            </div>

            <div className={classes['child-container']}>
              <DiagnosisList items={claim.diagnosis} errors={errors.diagnosis} />

              <TextField
                constainerStyle={{ width: 'unset', flex: 1, marginTop: '20px' }}
                multiline
                rows={3}
                id='loa-notes'
                label='LOA Notes'
                value={claim.loa_notes}
                disabled={disableControls}
                readOnly
                onChange={event =>
                  dispatch(
                    setClaimBaseDetails({
                      value: event.target.value,
                      field: 'loa_notes'
                    })
                  )
                }
              />
            </div>
          </Paper>

          <div className={classes['tables-container']}>
            <div className={classes['left-side']} id='left-side-panel'>
              <ProcedureList data={claim.procedures} />
              <ProfessionalFeeList data={claim.professional_fees} />
            </div>

            <div className={classes['right-side']} id='right-side-panel'>
              <div className={classes['wrapper']}>
                <div className={classes['sticky']}>
                  <ProcedureForm />
                  <ProfessionalFeeForm />
                </div>
              </div>
            </div>
          </div>

          <div className={classes['grand-total']}>
            <Typography variant='subtitle2'>TOTAL PHP {CurrencyFormatter(grandTotal)}</Typography>
          </div>

          <ClaimFooter
            handleOnSave={handleOnSave}
            setOpenDialog={setOpenDialog}
            openDialog={openDialog}
            onOk={() => {
              setOpenDialog(false)
              props.history.push(`/batches/${batchNumber}`)
            }}
          />
          <SoftValidatorAlertComponent />
        </div>
      )}
    </React.Fragment>
  )
}
