import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import BaseDetails from '../BaseDetails/BaseDetails'
import DiagnosisList from '../DiagnosisList/DiagnosisList'
import ProcedureForm from '../ProcedureForm/ProcedureForm'
import HospitalItemForm from '../HospitalItemForm/HospitalItemForm'
import ProfessionalFeeForm from '../ProfessionalFeeForm/ProfessionalFeeForm'
import TextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import DatePicker from 'SharedComponents/DatePicker/DatePicker'
import DropDown from 'SharedComponents/DropDown/DropDown'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import ProcedureList from '../ProcedureList/ProcedureList'
import HospitalItemList from '../HospitalItemList/HospitalItemList'
import ProfessionalFeeList from '../ProfessionalFeeList/ProfessionalFeeList'
import ClaimFooter from '../ClaimFooter/ClaimFooter'
import ReimbursementInfoComponent from '../../../Components/ReimbursementInfoComponent/ReimbursementInfoComponent'
import ContractRange from 'SharedComponents/ContractRange/ContractRange'
import DentalProcedureForm from '../DentalProcedureForm/DentalProcedureForm'

// Services
import ClaimsEncodingServices from 'Services/Api/ClaimsEncodingServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utilities
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './ClaimReimbursement.module.scss'

// Validator
import MainValidator from './../../Validators/Claim'
import { SoftValidatorService, SoftValidatorAlertComponent } from '../BaseDetails/SoftValidator'
import DiagnosisListValidator from '../DiagnosisList/DiagnosisListValidator'

import AdditionalFields from './AdditionalFields'

// Store
import { useSelector, useDispatch } from 'react-redux'
import {
  setClaimFields,
  setClaimBaseDetails,
  setClaimRoom,
  setDropDown,
  initDropDowns,
  initSuggestions,
  setGrandTotal,
  setProcedure,
  setDentalProcedure,
  setProfessionalFee,
  setHospitalItem
} from '../../Store/actions'

// Libraries
import moment from 'moment'

export default function ClaimReimbursement (props) {
  const dispatch = useDispatch()
  const { claim, onChangeFacility, getClaimDetails, onChangeDoctor, onChangePro } = props
  const suggestions = useSelector(state => state.suggestions)
  const dropDown = useSelector(state => state.dropDown)
  const grandTotal = useSelector(state => state.grandTotal)

  const [isAdd, setIsAdd] = React.useState(true)

  const [batchNumber, setBatchNumber] = React.useState(null)

  const [openDialog, setOpenDialog] = React.useState(false)

  const [allowedFields, setAllowedFields] = React.useState({})
  const [initialAllowedFields, setInitialAllowedFields] = React.useState({})
  const [lineItemFields, setlineItemFields] = React.useState({})
  const [showReimbursementModal, setShowReimbursementModal] = React.useState(false)
  const [type, setType] = React.useState('')

  React.useEffect(() => {
    const { _batchNumber } = props.match.params
    setIsAdd(props.isAdd)
    dispatch(initSuggestions())
    dispatch(initDropDowns(['rooms']))

    const _allowedFields = {
      approval_code: '',
      loa_number: '',
      reimbursement_type: props.isAdd ? 'InPatient' : '',
      member_account_number: '',
      member_last_name: '',
      member_first_name: '',
      member_middle_name: '',
      facility_id: '',
      facility_name: '',
      date_from: new Date().toISOString(),
      medicine_category: '',
      reimbursement_category: '',
      official_receipt_number: '',
      diagnosis: []
    }

    const _lineItemFields = {
      procedures: [],
      hospital_items: [],
      professional_fees: [],
      reimbursement_info: []
    }

    setTimeout(() => {
      setAllowedFields({ ..._allowedFields, ...AdditionalFields(props.isAdd ? 'InPatient' : undefined) })
      setlineItemFields(_lineItemFields)
      setInitialAllowedFields(_allowedFields)

      const initial_values = { ..._allowedFields, ..._lineItemFields }

      if (props.isAdd) initial_values['date_to'] = new Date().toISOString()

      dispatch(setClaimFields(initial_values))

      // if (result['date_to'] !== undefined)
    }, 10)

    if (!props.isAdd) {
      getClaimDetails(data => {
        setType(data.reimbursement_details.type)
        ClaimsEncodingServices.lookups({
          on: 'medical-partner',
          params: { query: data.facility.id, by: 'id' }
        }).then(res => {
          const { data } = res.data
          const rooms = (data[0].rooms || []).map(room => ({
            text: room.type,
            value: room.type,
            rate: room.rate
          }))
          dispatch(setDropDown({ rooms }))
        })

        let _claim = {
          approval_code: data.approval_code,
          loa_number: data.loa_number,
          reimbursement_type: data.reimbursement_details.type,
          member_account_number: data.member.account_number,
          member_last_name: data.member.last_name,
          member_first_name: data.member.first_name,
          member_middle_name: data.member.middle_name,
          facility_id: data.facility.id,
          facility_name: data.facility.name,
          date_from: data.date.from,
          medicine_category: data.medicine_category,
          reimbursement_category: data.reimbursement_details.category,
          official_receipt_number: data.reimbursement_details.official_receipt_number,
          diagnosis: data.diagnosis || [],
          procedures: data.procedures || [],
          professional_fees: data.professional_fees || [],
          hospital_items: data.hospital_items || [],
          reimbursement_info: data.reimbursement_info || [],
          batch_number: data.batch_number
        }

        if (data.reimbursement_details.type === 'InPatient') {
          _claim = {
            ..._claim,
            date_to: data.date.to,
            room_type: data.room.type,
            room_rate: data.room.rate || 0,
            pro_id: data.pro.id,
            pro_name: data.pro.name
          }
        } else if (data.reimbursement_details.type === 'Dental') {
          _claim = {
            ..._claim,
            procedures: data.dental_procedures || [],
            doctor_id: data.doctor.id,
            doctor_name: data.doctor.name,
            contract_year_from: data.contract_year.from,
            contract_year_to: data.contract_year.to
          }
        } else {
          _claim = {
            ..._claim,
            doctor_id: data.doctor.id,
            doctor_name: data.doctor.name,
            department: data.department
          }
        }

        dispatch(setClaimFields(_claim))
        setAllowedFields({
          ..._allowedFields,
          ...AdditionalFields(
            data.reimbursement_details.type === 'InPatient'
              ? 'InPatient'
              : data.reimbursement_details.type === 'Dental'
                ? 'Dental'
                : ''
          )
        })
      })
    }

    setBatchNumber(_batchNumber)
  }, [])

  const totalAmount = (currentValue, i) => {
    const total =
      parseFloat(i.gross_amount) +
      parseFloat(i.vat_amount) -
      parseFloat(i.amount_paid_by_member ? i.amount_paid_by_member : 0) -
      parseFloat(i.philhealth_amount_paid ? i.philhealth_amount_paid : 0)
    return currentValue + total
  }

  React.useEffect(() => {
    let sum = 0
    if (claim) {
      if (claim.procedures) sum = claim.procedures.reduce(totalAmount, sum)
      if (claim.hospital_items) sum = claim.hospital_items.reduce(totalAmount, sum)
      if (claim.professional_fees) sum = claim.professional_fees.reduce(totalAmount, sum)
    }

    dispatch(setGrandTotal(sum))
  }, [claim])

  const handleOnChangeFacility = args => {
    const rooms = (onChangeFacility(args) || []).map(item => ({
      text: item.type,
      value: item.type,
      rate: item.rate
    }))
    for (let i of rooms) {
      if (i.value === claim.room_type) {
        console.log(i)
        dispatch(setClaimRoom({ room_type: i.value, room_rate: i.rate }))
      }
    }

    dispatch(setDropDown({ rooms }))
  }

  const handleOnChangeRoomType = e => {
    const { value } = e.target
    for (let i of dropDown.rooms) {
      if (i.value === value) {
        return dispatch(setClaimRoom({ room_type: i.value, room_rate: i.rate }))
      }
    }
  }

  const [errors, setErrors] = React.useState({})

  const doValidateBase = () => {
    const data = {}
    const { diagnosis, ...fields } = allowedFields
    for (let f in fields) data[f] = claim[f]

    const { error = {} } = MainValidator(Object.keys({ ...fields, flag: type === 'Dental' }))({
      ...data,
      flag: type === 'Dental'
    })
    return error
  }

  const validateDiagnosis = () => {
    if (type === 'Dental') {
      return {}
    }
    return DiagnosisListValidator(claim.diagnosis)
  }

  const doValidateAll = () => {
    const base_errors = doValidateBase()
    const diagnosis = validateDiagnosis()
    setErrors({ ...base_errors, diagnosis })
    return !!(Object.keys(base_errors).length || diagnosis.error)
  }

  const [disableControls, setDisableControls] = React.useState(false)
  const handleOnSave = async goBack => {
    if (doValidateAll()) return

    const proceed = await SoftValidatorService({
      fields: [{ 'Approval Code': claim.approval_code }, { 'LOA No.': claim.loa_number }]
    })

    if (!proceed) return

    setDisableControls(true)
    LoadingService.loading(true)

    const data = {}
    for (let f in { ...allowedFields, ...lineItemFields }) data[f] = claim[f]

    if (!data.date_to) {
      data.date_to = new Date().toISOString()
    }

    if (isAdd) {
      ClaimsEncodingServices.addClaim({ batch_number: batchNumber, data }).then(res => {
        LoadingService.loading(false)
        if (goBack) return props.history.push(`/batches/${batchNumber}`)
        props.history.push(`/`)
      })
    } else {
      const { _claimNumber } = props.match.params
      ClaimsEncodingServices.updateClaim({ batch_number: batchNumber, claim_number: _claimNumber, data }).then(res => {
        LoadingService.loading(false)
        if (goBack) return props.history.push(`/batches/${batchNumber}`)
        props.history.push(`/`)
      })
    }
  }

  const onChangeReimbursementType = ({ value, field }) => {
    setAllowedFields({ ...initialAllowedFields, ...AdditionalFields(value) })
    setType(value)

    if (value === 'InPatient') {
      dispatch(
        setClaimFields({
          ...claim,
          date_to: new Date().toISOString()
        })
      )
    } else if (value === 'Dental') {
      dispatch(
        setClaimFields({
          ...claim,
          reimbursement_type: 'Dental',
          date_from: new Date().toISOString(),
          contract_year_to: '',
          contract_year_from: '',
          doctor_id: '',
          doctor_name: ''
        })
      )
    }

    dispatch(setClaimBaseDetails({ value, field }))
  }

  React.useEffect(() => {
    dispatch(setDentalProcedure({ show: false }))
    dispatch(setProcedure({ show: false }))
    dispatch(setHospitalItem({ show: false }))
    dispatch(setProfessionalFee({ show: false }))
  }, [type])

  return (
    <React.Fragment>
      {claim && (
        <div className={classes.root}>
          <Paper className={classes['first-container']}>
            <BaseDetails
              claim={claim}
              errors={errors}
              disableControls={disableControls}
              showReimbursement
              onChange={({ value, field }) => dispatch(setClaimBaseDetails({ value, field }))}
              onChangeReimbursementType={onChangeReimbursementType}
              handleMemberAccountNumber={type === 'Dental'}
              isDental={type === 'Dental'}
            />

            <div className={classes.groups}>
              <div className={classes['input']}>
                <AutoComplete
                  id='medical-partner-id'
                  required
                  label='Medical Partner ID'
                  value={claim.facility_id}
                  disabled={disableControls}
                  suggestions={suggestions.facility.id}
                  onChange={(value, isSuggestionClick) =>
                    handleOnChangeFacility({
                      field: 'facility_id',
                      value,
                      isSuggestionClick
                    })
                  }
                  error={errors.facility_id}
                />
              </div>

              <div className={classes['input']}>
                <AutoComplete
                  id='medical-partner-facility-name'
                  required
                  label='Medical Partner (Facility) Name'
                  value={claim.facility_name}
                  disabled={disableControls}
                  suggestions={suggestions.facility.name}
                  onChange={(value, isSuggestionClick) =>
                    handleOnChangeFacility({
                      field: 'facility_name',
                      value,
                      isSuggestionClick
                    })
                  }
                  error={errors.facility_name}
                />
              </div>

              {AdditionalFields.exist(allowedFields, 'doctor_id') && (
                <div className={classes['input']}>
                  <AutoComplete
                    id='dp-medical-partner-id'
                    required
                    label='Medical Partner ID'
                    value={claim.doctor_id}
                    disabled={disableControls}
                    suggestions={suggestions.doctor.id}
                    onChange={(value, isSuggestionClick) =>
                      onChangeDoctor({
                        field: 'doctor_id',
                        value,
                        isSuggestionClick
                      })
                    }
                    error={errors.doctor_id}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'doctor_name') && (
                <div className={classes['input']}>
                  <AutoComplete
                    id='doctor-name'
                    required
                    label='Medical Partner (Doctor) Name'
                    value={claim.doctor_name}
                    disabled={disableControls}
                    suggestions={suggestions.doctor.full_name}
                    onChange={(value, isSuggestionClick) =>
                      onChangeDoctor({
                        field: 'doctor_name',
                        value,
                        isSuggestionClick
                      })
                    }
                    error={errors.doctor_name}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'date_from') && AdditionalFields.exist(allowedFields, 'date_to') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <DatePicker
                      id='admission-date-from'
                      required
                      label='Admission Date (From)'
                      value={claim.date_from}
                      disabled={disableControls}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: moment(event.toISOString())
                              .startOf('day')
                              .toISOString(),
                            field: 'date_from'
                          })
                        )
                      }
                      error={errors.date_from}
                    />
                  </div>

                  <div className={classes['input']}>
                    <DatePicker
                      id='admission-date-to'
                      required
                      label='Admission Date (To)'
                      value={claim.date_to}
                      disabled={disableControls}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.toISOString(),
                            field: 'date_to'
                          })
                        )
                      }
                      error={errors.date_to}
                    />
                  </div>
                </React.Fragment>
              )}
            </div>

            <div className={classes.groups}>
              {AdditionalFields.exist(allowedFields, 'date_from') && type === 'Dental' && (
                <div className={classes['input']}>
                  <DatePicker
                    id='date-of-service'
                    required
                    label='Date of Service'
                    value={claim.date_from}
                    disabled={disableControls}
                    onChange={event => {
                      dispatch(
                        setClaimBaseDetails({
                          value: event.toISOString(),
                          field: 'date_from'
                        })
                      )
                      dispatch(
                        setClaimBaseDetails({
                          value: event.toISOString(),
                          field: 'date_to'
                        })
                      )
                    }}
                    error={errors.date_from}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'contract_year_from') && (
                <div className={`${classes['input']} ${classes['contract-range']}`}>
                  <ContractRange
                    id='contract-year'
                    contract_year_from={claim.contract_year_from}
                    contract_year_to={claim.contract_year_to}
                    suggestions={suggestions.contract.years}
                    onChange={({ from, to }) => {
                      dispatch(
                        setClaimBaseDetails({
                          field: 'contract_year_from',
                          value: from
                        })
                      )
                      dispatch(
                        setClaimBaseDetails({
                          field: 'contract_year_to',
                          value: to
                        })
                      )
                    }}
                    error={errors.contract_year_from}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'department') && (
                <div className={classes['input']}>
                  <TextField
                    id='department'
                    label='Department'
                    value={claim.department}
                    disabled={disableControls}
                    onChange={event =>
                      dispatch(
                        setClaimBaseDetails({
                          value: event.target.value,
                          field: 'department'
                        })
                      )
                    }
                    error={errors.pro_id}
                  />
                </div>
              )}

              {!AdditionalFields.exist(allowedFields, 'room_rate') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <TextField
                      id='medicine-category'
                      required
                      label='Medicine Category'
                      value={claim.medicine_category}
                      disabled={disableControls}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'medicine_category'
                          })
                        )
                      }
                      error={errors.medicine_category}
                    />
                  </div>

                  <div className={classes['input']}>
                    <TextField
                      id='reimbursement-category'
                      label='Reimbursement Category'
                      value={claim.reimbursement_category}
                      disabled={disableControls}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'reimbursement_category'
                          })
                        )
                      }
                      error={errors.reimbursement_category}
                    />
                  </div>
                </React.Fragment>
              )}

              {AdditionalFields.exist(allowedFields, 'room_type') && (
                <div className={classes['input']}>
                  <DropDown
                    id='room-type'
                    required
                    label='Room Type'
                    items={dropDown.rooms}
                    value={claim.room_type}
                    disabled={disableControls}
                    onChange={handleOnChangeRoomType}
                    error={errors.room_type}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'room_rate') && (
                <div className={classes['input']}>
                  <TextFieldCurrency
                    id='room-rate'
                    required
                    label='Room Rate'
                    value={claim.room_rate}
                    disabled={disableControls}
                    onChange={event =>
                      dispatch(
                        setClaimBaseDetails({
                          value: event.target.value,
                          field: 'room_rate'
                        })
                      )
                    }
                    error={errors.room_rate}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'pro_id') && (
                <div className={classes['input']}>
                  <AutoComplete
                    id='pro-id'
                    required
                    label='PRO ID'
                    value={claim.pro_id}
                    suggestions={suggestions.pro.id}
                    disabled={disableControls}
                    onChange={(value, isSuggestionClick) =>
                      onChangePro({
                        field: 'pro_id',
                        value,
                        isSuggestionClick
                      })
                    }
                    error={errors.pro_id}
                  />
                </div>
              )}

              {AdditionalFields.exist(allowedFields, 'pro_name') && (
                <div className={classes['input']}>
                  <AutoComplete
                    id='pro-name'
                    required
                    label='PRO Name'
                    value={claim.pro_name}
                    disabled={disableControls}
                    suggestions={suggestions.pro.name}
                    onChange={(value, isSuggestionClick) =>
                      onChangePro({
                        field: 'pro_name',
                        value,
                        isSuggestionClick
                      })
                    }
                    error={errors.pro_name}
                  />
                </div>
              )}
            </div>

            <div className={classes.groups}>
              {AdditionalFields.exist(allowedFields, 'room_rate') && (
                <React.Fragment>
                  <div className={classes['input']}>
                    <TextField
                      id='medicine-category'
                      required
                      label='Medicine Category'
                      value={claim.medicine_category}
                      disabled={disableControls}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'medicine_category'
                          })
                        )
                      }
                      error={errors.medicine_category}
                    />
                  </div>

                  <div className={classes['input']}>
                    <TextField
                      id='reimbursement-category'
                      label='Reimbursement Category'
                      value={claim.reimbursement_category}
                      disabled={disableControls}
                      onChange={event =>
                        dispatch(
                          setClaimBaseDetails({
                            value: event.target.value,
                            field: 'reimbursement_category'
                          })
                        )
                      }
                      error={errors.reimbursement_category}
                    />
                  </div>
                </React.Fragment>
              )}

              <div className={classes['input']}>
                <TextField
                  id='official-receipt-number'
                  label='Official Receipt Number'
                  value={claim.official_receipt_number}
                  disabled={disableControls}
                  required
                  onChange={event =>
                    dispatch(
                      setClaimBaseDetails({
                        value: event.target.value,
                        field: 'official_receipt_number'
                      })
                    )
                  }
                  error={errors.official_receipt_number}
                />
              </div>
            </div>

            <div className={classes['child-container']}>
              <DiagnosisList items={claim.diagnosis} errors={errors.diagnosis} />
            </div>
          </Paper>

          <div className={classes['tables-container']}>
            <div className={classes['left-side']} id='left-side-panel'>
              <ProcedureList data={claim.procedures} isDental={type === 'Dental'} />
              <HospitalItemList data={claim.hospital_items} />
              <ProfessionalFeeList data={claim.professional_fees} />
            </div>

            <div className={classes['right-side']} id='right-side-panel'>
              <div className={classes['wrapper']}>
                <div className={classes['sticky']}>
                  {/* temporary dental proc  dental_procedure */}
                  <DentalProcedureForm />
                  <ProcedureForm />
                  <HospitalItemForm />
                  <ProfessionalFeeForm />
                </div>
              </div>
            </div>
          </div>

          <div className={classes['grand-total']}>
            <Typography variant='subtitle2'>TOTAL PHP {CurrencyFormatter(grandTotal)}</Typography>
          </div>

          <ClaimFooter
            handleOnSave={handleOnSave}
            setOpenDialog={setOpenDialog}
            openDialog={openDialog}
            setShowReimbursementModal={setShowReimbursementModal}
            onOk={() => {
              setOpenDialog(false)
              props.history.push(`/batches/${batchNumber}`)
            }}
          />
          <ReimbursementInfoComponent
            claim={claim}
            show={showReimbursementModal}
            setShowModal={setShowReimbursementModal}
            handleOnSave={reimbursement_info =>
              dispatch(
                setClaimBaseDetails({
                  value: reimbursement_info,
                  field: 'reimbursement_info'
                })
              )
            }
          />
          <SoftValidatorAlertComponent />
        </div>
      )}
    </React.Fragment>
  )
}
