import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
// import TableSortLabel from '@material-ui/core/TableSortLabel'
import Typography from '@material-ui/core/Typography'
import { CurrencyFormatter, DateFormatter } from 'Utilities/TextFormatter'

// Components
import Table from 'SharedComponents/Table/Table'
import TextField from 'SharedComponents/TextField/CustomTextField'
import SearchTextComponent from 'SharedComponents/SearchTextComponent'

// Services
import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'
import { extractLoa, setDoctors } from './helper'
import lodash from 'lodash'

// Styles
import classes from './Utilization.module.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { setLimitType, resetLimitType } from '../../Store/actions'

const headCells = [
  { name: 'Claim ID', sortField: 'claim_number', search: true },
  { name: 'Diagnosis', sortField: 'diagnosis', search: true },
  { name: 'Procedure', sortField: 'procedure', search: true },
  { name: 'Provider', sortField: 'provider', search: true },
  { name: 'Amount', sortField: 'amount', search: true },
  { name: 'Session', sortField: 'session', search: true },
  { name: 'Date', sortField: 'date', search: true },
  { name: 'Type', sortField: 'type', search: true },
  { name: 'Status', sortField: 'status', search: true }
]

const randomRange = (min, max) => {
  return Math.random() * (max - min) + min
}

export default function Utilization (props) {
  const dispatch = useDispatch()
  const title = useSelector(state => state.title)

  const { fetchService, diagnosis, member = {}, memberBenefitLimit = {}, utilizationLoa = {} } = props
  const [list, setList] = React.useState({
    rows: [],
    alter: []
  })

  const [filters, setFilters] = React.useState({})
  const [allFilters, setAllFilters] = React.useState({})
  const [filteredRows, setFilteredRows] = React.useState({
    data: list.rows,
    orderBy: '',
    order: 'desc',
    category: 'all',
    search: '',
    count: 0,
    page: 1,
    limit: 10,
    rowsPerPageOptions: [10, 15, 20]
  })

  const [graph, setGraph] = React.useState({
    utilized: 0,
    ibnr: 0,
    total: 0
  })

  React.useEffect(() => {
    if (member) {
      let type
      let limit

      if (!memberBenefitLimit.length <= 0) {
        type = memberBenefitLimit[0].type
        limit = memberBenefitLimit[0].limit
      }

      if (type === 'ABL') {
        dispatch(setLimitType('Annual Benefit Limit'))

        setGraph(state => ({
          ...state,
          total: limit || randomRange
        }))

        fetchService({
          member_account_no: member.member_id,
          type: '',
          icd_code: ''
        }).then(res => {
          const { data } = res.data

          let _utilizationLoa = extractLoa(utilizationLoa)

          let list = setDoctors(_utilizationLoa.concat(data))

          setList(state => ({ ...state, rows: list, alter: list }))
          setFilteredRows(state => ({ ...state, count: list.length, data: list.slice(0, state.limit) }))

          let ibnr = 0
          let utilized = 0

          _utilizationLoa.map(item => (ibnr += item.total_amount))
          data.map(item => (utilized += item.grand_total))

          setGraph(state => ({
            ...state,
            ibnr,
            utilized
          }))
        })
      } else if (type === 'MBL') {
        dispatch(setLimitType('Maximum Benefit Limit'))

        setGraph(state => ({
          ...state,
          total: limit || randomRange
        }))

        let primary = diagnosis.filter(icd => icd.primary === true)[0] || {
          code: '',
          description: '',
          category_code: ''
        }

        IcdCptStorageServices.lookups({
          on: 'icd-10',
          params: { query: primary.code, by: 'code' }
        }).then(res => {
          const { data } = res.data
          const { category_code } = data[0] || []
          primary.category_code = category_code

          fetchService({
            member_account_no: member.member_id,
            type: 'mbl',
            icd_code: primary.category_code
          }).then(res => {
            const { data } = res.data

            // ICD Screening
            let _utilizationLoa = utilizationLoa.filter(item => {
              if (item.primary_icd10_code) {
                return item.primary_icd10_code.startsWith(primary.category_code)
              }
            })

            // extractation of needed fields
            _utilizationLoa = extractLoa(_utilizationLoa)
            let list = setDoctors(_utilizationLoa.concat(data))

            setList(state => ({ ...state, rows: list, alter: list }))
            setFilteredRows(state => ({ ...state, count: list.length, data: list.slice(0, state.limit) }))

            let ibnr = 0
            let utilized = 0

            _utilizationLoa.map(item => (ibnr += item.total_amount))
            data.map(item => (utilized += item.grand_total))

            setGraph(state => ({
              ...state,
              ibnr,
              utilized
            }))
          })
        })
      }
    } else {
      dispatch(setLimitType('Member Not Found'))
    }
  }, [member, memberBenefitLimit, diagnosis])

  React.useEffect(() => {
    dispatch(resetLimitType())
  }, [])

  React.useEffect(() => {
    let rows = allFilter(list.rows, allFilters)
    rows = columnFilter(rows, filters)
    // rows = lodash.sortBy(rows, 'claim_no')
    setList(state => ({ ...state, alter: rows }))
    setFilteredRows(state => ({
      ...state,
      page: 1,
      count: rows.length,
      data: Object.keys(filters).length !== 0 ? lodash.chunk(rows, state.limit)[0] : lodash.take(rows, state.limit)
    }))
  }, [filters, allFilters])

  const [debounce, setDebounce] = React.useState(null)

  const handleSearchChange = args => {
    const { event, field } = args
    const search = event.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        if (field !== 'all') setFilters(state => ({ ...state, [field]: search }))
        if (field === 'all') setAllFilters({ [field]: search })
      }, 500)
    )
  }

  const compare = (list, find, comparer = includesAny, fields = []) => {
    if (Array.isArray(list)) {
      return list.some(item => {
        for (const field of fields) {
          if (comparer(item[field], find)) return true
        }
      })
    } else {
      if (comparer(list, find)) return true
    }
    return false
  }

  const includesAny = (s1 = '', s2 = '') => {
    return s1.toLowerCase().includes(s2.toLowerCase())
  }

  const allFilter = (rows, filters) => {
    let filteredRows = rows
    return filteredRows.filter(row => {
      if (includesAny(row.claim_no, filters.all)) return true

      if (includesAny(row.partner_name, filters.all)) return true

      if (DateFormatter(row.date_admitted) === filters.all) return true

      if (includesAny(row.type, filters.all)) return true
      // temporary
      if (includesAny(row.status === 'Open' ? row.status : 'Approved', filters.all)) return true

      if (row.grand_total === parseFloat(filters.all)) return true

      if (row.session === parseFloat(filters.all)) return true

      // console.log(compare(row.diagnosis, filters.all, includesAny, ['icd10_code', 'diagnosis']))
      if (row.__type === 'UtilizationInPatient') {
        if (row.doctors) {
          if (compare(row.doctors, filters.all, includesAny, ['id', 'name'])) return true
        }

        if (row.diagnosis) {
          if (compare(row.diagnosis, filters.all, includesAny, ['icd10_code', 'diagnosis'])) return true
        }

        if (row.procedures) {
          if (row.procedures) {
            if (compare(row.procedures, filters.all, includesAny, ['cpt_code', 'procedure_name'])) return true
          }
        }
      } else if (row.__type === 'UtilizationOutPatientProcedure') {
        if (row.doctors) {
          if (compare(row.doctors, filters.all, includesAny, ['id', 'name'])) return true
        }

        if (row.diagnosis) {
          if (compare(row.diagnosis, filters.all, includesAny, ['icd10_code', 'diagnosis'])) return true
        }

        if (row.procedures) {
          if (row.procedures) {
            if (compare(row.procedures, filters.all, includesAny, ['cpt_code', 'procedure_name'])) return true
          }
        }
      } else if (row.__type === 'UtilizationOutPatientConsultation') {
        if (row.doctors) {
          if (compare(row.doctors, filters.all, includesAny, ['id', 'name'])) return true
        }

        if (row.diagnosis) {
          if (compare(row.diagnosis, filters.all, includesAny, ['icd10_code', 'diagnosis'])) return true
        }
      } else if (row.__type === 'UtilizationEmergency') {
        if (row.doctors) {
          if (compare(row.doctors, filters.all, includesAny, ['id', 'name'])) return true
        }

        if (row.diagnosis) {
          if (compare(row.diagnosis, filters.all, includesAny, ['icd10_code', 'diagnosis'])) return true
        }

        if (row.procedures) {
          if (compare(row.procedures, filters.all, includesAny, ['cpt_code', 'procedure_name'])) return true
        }
      } else if (row.__type === 'UtilizationReimbursement') {
        if (row.doctors) {
          if (compare(row.doctors, filters.all, includesAny, ['id', 'name'])) return true
        }

        if (row.diagnosis) {
          if (compare(row.diagnosis, filters.all, includesAny, ['icd10_code', 'diagnosis'])) return true
        }

        if (row.procedures) {
          if (compare(row.procedures, filters.all, includesAny, ['cpt_code', 'procedure_name'])) return true
        }
      }
    })
  }

  const columnFilter = (rows, filters) => {
    // hard coded ugly static search
    // TODO refactor for dynamic deep comparison
    let filteredRows = rows
    for (const filter in filters) if (filters[filter] === '') delete filters[filter]
    return filteredRows.filter(row => {
      if (!includesAny(row.claim_no, filters.claim_number)) return false

      // if (!includesAny(row.partner_name, filters.provider)) return false

      if (filters.date && DateFormatter(row.date_admitted) !== filters.date) return false

      if (!includesAny(row.type, filters.type)) return false
      // temporary
      if (!includesAny(row.status === 'Open' ? row.status : 'Approved', filters.status)) return false

      if (filters.amount && row.grand_total !== parseFloat(filters.amount)) return false

      if (filters.session && row.session !== parseFloat(filters.session)) return false

      if (row.__type === 'UtilizationInPatient') {
        if (filters.provider) {
          if (row.doctors || row.partner_name) {
            if (
              !compare(row.doctors, filters.provider, includesAny, ['id', 'name']) &&
              !includesAny(row.partner_name, filters.provider)
            ) {
              return false
            }
          }
        }

        if (filters.diagnosis) {
          if (row.diagnosis) {
            if (!compare(row.diagnosis, filters.diagnosis, includesAny, ['icd10_code', 'diagnosis'])) return false
          }
        }

        if (filters.procedure) {
          if (row.procedures) {
            if (!compare(row.procedures, filters.procedure, includesAny, ['cpt_code', 'procedure_name'])) return false
          }
        }
      } else if (row.__type === 'UtilizationOutPatientProcedure') {
        if (filters.provider) {
          if (row.doctors || row.partner_name) {
            if (
              !compare(row.doctors, filters.provider, includesAny, ['id', 'name']) &&
              !includesAny(row.partner_name, filters.provider)
            ) {
              return false
            }
          }
        }

        if (filters.diagnosis) {
          if (row.diagnosis) {
            if (!compare(row.diagnosis, filters.diagnosis, includesAny, ['icd10_code', 'diagnosis'])) return false
          }
        }

        if (filters.procedure) {
          if (row.procedures) {
            if (!compare(row.procedures, filters.procedure, includesAny, ['cpt_code', 'procedure_name'])) return false
          }
        }
      } else if (row.__type === 'UtilizationOutPatientConsultation') {
        if (filters.provider) {
          if (row.doctors || row.partner_name) {
            if (
              !compare(row.doctors, filters.provider, includesAny, ['id', 'name']) &&
              !includesAny(row.partner_name, filters.provider)
            ) {
              return false
            }
          }
        }

        if (filters.diagnosis) {
          if (row.diagnosis) {
            if (!compare(row.diagnosis, filters.diagnosis, includesAny, ['icd10_code', 'diagnosis'])) return false
          }
        }
      } else if (row.__type === 'UtilizationEmergency') {
        if (filters.provider) {
          if (row.doctors || row.partner_name) {
            if (
              !compare(row.doctors, filters.provider, includesAny, ['id', 'name']) &&
              !includesAny(row.partner_name, filters.provider)
            ) {
              return false
            }
          }
        }

        if (filters.diagnosis) {
          if (row.diagnosis) {
            if (!compare(row.diagnosis, filters.diagnosis, includesAny, ['icd10_code', 'diagnosis'])) return false
          }
        }

        if (filters.procedure) {
          if (row.procedures) {
            if (!compare(row.procedures, filters.procedure, includesAny, ['cpt_code', 'procedure_name'])) return false
          }
        }
      } else if (row.__type === 'UtilizationReimbursement') {
        if (filters.provider) {
          if (row.doctors || row.partner_name) {
            if (
              !compare(row.doctors, filters.provider, includesAny, ['id', 'name']) &&
              !includesAny(row.partner_name, filters.provider)
            ) {
              return false
            }
          }
        }

        if (filters.diagnosis) {
          if (row.diagnosis) {
            if (!compare(row.diagnosis, filters.diagnosis, includesAny, ['icd10_code', 'diagnosis'])) return false
          }
        }

        if (filters.procedure) {
          if (row.procedures) {
            if (!compare(row.procedures, filters.procedure, includesAny, ['cpt_code', 'procedure_name'])) return false
          }
        }
      }
      return true
    })
  }

  // TODO
  // THIS CAN BE BETTER
  const handleOnPaginate = args => {
    if (args && args.limit && args.page) {
      setList(state => ({ ...state, limit: args.limit }))
      setFilteredRows(state => ({
        ...state,
        count: list.alter.length,
        page: args.page,
        limit: args.limit,
        data: lodash.take(list.alter, args.limit)
      }))
    } else if (args && args.page) {
      if (list.alter.length === list.rows.length) {
        return setFilteredRows(state => ({
          ...state,
          page: args.page,
          data: lodash.chunk(list.rows, state.limit)[args.page - 1]
        }))
      }
      setFilteredRows(state => ({
        ...state,
        page: args.page,
        data: lodash.chunk(list.alter, state.limit)[args.page - 1]
      }))
    }
  }

  const renderRows = ({ rows = [] }) => {
    return rows.map((row, index) => (
      <TableRow key={`claim-${index}`} hover role='checkbox' tabIndex={-1}>
        <TableCell>
          <div className={classes.cell}>
            <span>{row.claim_no}</span>
            <span className={classes.label}>SWIPE</span>
            <span>YES</span>
          </div>
        </TableCell>
        <TableCell>
          {row.diagnosis.map((row, index) => (
            <div key={`icd-${index}`} className={classes.cell}>
              <span>{row.diagnosis}</span>
              <span className={classes.label}>ICD 10 CODE</span>
              <span>{row.icd10_code}</span>
            </div>
          ))}
        </TableCell>

        <TableCell>
          <div className={classes.cell}>
            {row.procedures.map((row, index) => (
              <div key={`procedure-${index}`} className={classes.cell}>
                <span>{row.procedure_name}</span>
                <span className={classes.label}>CPT CODE</span>
                <span>{row.cpt_code}</span>
              </div>
            ))}
          </div>
        </TableCell>

        <TableCell>
          <div className={classes.cell}>
            <div className={classes.cell}>
              <span>{row.partner_name}</span>
              <span className={classes.label}>MEDICAL DOCTOR</span>
              {row.doctors.map((row, index) => (
                <span key={`doctor-${index}`}>{`${row.name},`}</span>
              ))}
            </div>
          </div>
        </TableCell>
        <TableCell align='right'>
          <span>{CurrencyFormatter(row.grand_total)}</span>
        </TableCell>
        <TableCell align='right'>
          <span>{row.session}</span>
        </TableCell>
        <TableCell>
          <span>{DateFormatter(row.date_admitted)}</span>
        </TableCell>
        <TableCell>
          <span>{row.type}</span>
        </TableCell>
        <TableCell align='center'>
          <span>{row.status === 'Open' ? 'Open' : 'Approved'}</span>
        </TableCell>
        <TableCell align='center'>
          <FontAwesomeIcon icon='file-alt' size='lg' className={classes.icon} />
        </TableCell>
      </TableRow>
    ))
  }

  return (
    <div className={classes.root}>
      <div className={classes.header}>
        <div className={classes.graph}>
          <div className={classes['anual-benefit']}>
            <Typography variant='button'>{title}</Typography>
            <Typography variant='h6'>{`PHP ${CurrencyFormatter(graph.total)}`}</Typography>
          </div>

          <div className={classes['line-graph']}>
            <div className={classes.container}>
              <div className={classes['line-container']}>
                <span
                  className={classes.item}
                  style={{ width: `${(graph.utilized / graph.total) * 100}%`, background: '#4fb083' }}
                />
                <span
                  className={classes.item}
                  style={{ width: `${(graph.ibnr / graph.total) * 100}%`, background: '#2a2b6e' }}
                />
                <span
                  className={classes.item}
                  style={{
                    width: `${
                      ((graph.total - graph.ibnr - graph.utilized) / graph.total) * 100 < 0
                        ? 0
                        : ((graph.total - graph.ibnr - graph.utilized) / graph.total) * 100
                    }%`,
                    background: '#f8b439'
                  }}
                />
              </div>
              <div className={classes['line-details']}>
                <div className={classes.item}>
                  <div className={classes.label}>
                    <span className={classes.color} style={{ background: '#4fb083' }} />
                    <Typography variant='button'>UTILIZED</Typography>
                  </div>
                  <Typography variant='button' style={{ color: '#4fb083' }}>
                    {`PHP ${CurrencyFormatter(graph.utilized)}`}
                  </Typography>
                </div>
                <div className={classes.item}>
                  <div className={classes.label}>
                    <span className={classes.color} style={{ background: '#2a2b6e' }} />
                    <Typography variant='button'>IBNR</Typography>
                  </div>
                  <Typography variant='button' style={{ color: '#2a2b6e' }}>
                    {`PHP ${CurrencyFormatter(graph.ibnr)}`}
                  </Typography>
                </div>
                <div className={classes.item}>
                  <div className={classes.label}>
                    <span className={classes.color} style={{ background: '#f8b439' }} />
                    <Typography variant='button'>AVAILABLE</Typography>
                  </div>
                  <Typography variant='button' style={{ color: '#f8b439' }}>
                    {`PHP ${CurrencyFormatter(graph.total - graph.ibnr - graph.utilized)}`}
                  </Typography>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className={classes.search}>
          <SearchTextComponent
            id='search-utilization'
            onChange={event => handleSearchChange({ event, field: 'all' })}
            placeholder='Search'
          />
        </div>
      </div>
      <div className={classes.table}>
        <Table
          TableContainerProps={{ style: { maxHeight: '500px', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {headCells.map(headCell => (
                <TableCell key={headCell.sortField}>
                  {headCell.name}
                  {headCell.search && (
                    <TextField
                      className={`${classes.search}`}
                      onChange={event => handleSearchChange({ event, field: headCell.sortField })}
                    />
                  )}
                </TableCell>
              ))}

              <TableCell align='center' />
            </TableRow>
          }
          body={renderRows({ rows: filteredRows.data })}
          pagination
          count={filteredRows.count}
          page={filteredRows.page}
          limit={filteredRows.limit}
          rowsPerPageOptions={filteredRows.rowsPerPageOptions}
          onChange={args => handleOnPaginate(args)}
        />
      </div>
    </div>
  )
}
