import React from 'react'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import startCase from 'lodash/startCase'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import TaskTray from 'SharedComponents/TaskTrayComponent/TaskTrayComponent'
import TicketList from 'SharedComponents/TicketListComponent/TicketListComponent'
import ViewList from '../../Components/ViewList/ViewList'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import MedicalConsultantServices from 'Services/Api/MedicalConsultantServices'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './Dashboard.module.scss'

export default function Dashboard (props) {
  const [claims, setClaims] = React.useState({
    for_review: { count: 0, data: [] },
    ticket: { count: 0, data: [] }
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'latest.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    PageTitleService('Dashboard')
    BreadcrumbsService.setSelectedItems(['dashboard'])

    //* For Claims
    Promise.all([
      MedicalConsultantServices.getClaims({
        category: 'for-review',
        limit: 10,
        sort: 'latest.date.due|desc',
        range: 'this-week'
      }),
      MedicalConsultantServices.getClaimCount({ category: 'for-review' })
    ]).then(values => {
      const [claimResponse, claimCountResponse] = values
      const allClaims = claimResponse.data.data.results
      const allClaimsCount = claimCountResponse.data.data
      setClaims(prevState => ({ ...prevState, for_review: { data: allClaims, count: allClaimsCount } }))
    })
  }, [])

  const handleFilterChange = category => value => {
    // Not yet working, but change of filter will not show error.

    MedicalConsultantServices.getClaims({
      category,
      range: value,
      limit: 10,
      sort: 'latest.due.date|desc'
    }).then(({ data: res }) => {
      const { data } = res
      setClaims(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
    })
  }

  const handleOnReview = claim_number => {
    props.history.push(`/claims/review/${claim_number}`)
  }

  const handleOnShowViewList = category => () => {
    let title = startCase(`${category} Claims`)
    if (category === 'for_review') title = 'For Review'

    setViewList({ ...initialValue, show: true, title, category })
    MedicalConsultantServices.getClaims({ category, limit: 10, sort: 'latest.date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    MedicalConsultantServices.getClaims(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnClickItemViewList = ({ category, claim_number }) => {
    if (category === 'for_review') handleOnReview(claim_number)
  }

  return (
    <div className={classes.root}>
      <Grid container className={classes.tiles} spacing={3} direction='row'>
        <Grid item className={classes['task-tray']}>
          <TaskTray sizes={{ xs: 12 }}>
            <DashboardTile
              count={{ color: 3, value: claims.for_review.count }}
              sizes={{ sm: 12 }}
              title='Escalated Claims'
              viewList={
                <Typography variant='caption' className='bold text-link' onClick={handleOnShowViewList('for-review')}>
                  View List
                </Typography>
              }
              onFilterChange={handleFilterChange('for_review')}
            >
              <Table size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell>Claim No.</TableCell>
                    <TableCell>Due Date</TableCell>
                    <TableCell>Facility Name</TableCell>
                    <TableCell />
                  </TableRow>
                </TableHead>
                <TableBody>
                  {claims.for_review.data.map((item, index) => (
                    <TableRow key={item.claim_number}>
                      <TableCell>{item.claim_number}</TableCell>
                      <TableCell>{DateFormatter(item.date.due)}</TableCell>
                      <TableCell>{item.facility.name}</TableCell>
                      <TableCell>
                        <Typography
                          variant='caption'
                          className={`text-link underline`}
                          onClick={() => handleOnReview(item.claim_number)}
                        >
                          Review
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </DashboardTile>
          </TaskTray>
        </Grid>

        <Grid item className={classes['ticket-list']}>
          <TicketList count={0} title='Open Tickets' viewAllLink={{ to: '/', caption: 'View all' }}>
            <Table size='small'>
              <TableHead>
                <TableRow>
                  <TableCell>Ticket #</TableCell>
                  <TableCell>Date</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {claims.ticket.data.map((item, index) => (
                  <TableRow key={index}>
                    <TableCell>2334324343</TableCell>
                    <TableCell>08/12/2019</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TicketList>
        </Grid>
      </Grid>

      <ViewList
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        onItemClick={handleOnClickItemViewList}
      />
    </div>
  )
}
