import instance from './instance'
const axios = instance('/c/pr')

export default class PaymentRequestServices {
  static getClaims (params) {
    return axios({
      url: '/claims',
      method: 'GET',
      params
    })
  }

  static getClaimCount (params) {
    return axios({
      url: `/claims/count`,
      method: 'GET',
      params
    })
  }

  static getPaymentRequest ({ bundle_number }) {
    return axios({
      url: `/payment-request/${bundle_number}`,
      method: 'GET'
    })
  }

  static getPaymentRequestBatches ({ bundle_number, params }) {
    return axios({
      url: `/payment-request/${bundle_number}/batches`,
      method: 'GET',
      params
    })
  }

  static submit ({ bundle_number, data }) {
    return axios({
      url: `/payment-request/${bundle_number}/submit`,
      method: 'PUT',
      data
    })
  }
}
