import React from 'react'
import Snackbar from '@material-ui/core/Snackbar'
import Alert from '@material-ui/lab/Alert'
import AlertTitle from '@material-ui/lab/AlertTitle'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { setSnackbar } from 'Pages/Protected/Store/actions'

// Style
import classes from './SnackbarComponent.module.scss'

let dispatch = null

const SnackbarComponent = () => {
  const snackbar = useSelector(state => state.snackbar)
  dispatch = useDispatch()

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') return

    dispatch(setSnackbar({ open: false }))
  }

  return (
    <Snackbar
      className={classes.root}
      open={snackbar.open}
      autoHideDuration={3000}
      onClose={handleClose}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right'
      }}
    >
      <div>
        <Alert
          elevation={6}
          variant='filled'
          severity={snackbar.severity}
          action={
            <IconButton aria-label='delete' className={classes.icon} onClick={handleClose}>
              <CloseIcon fontSize='small' />
            </IconButton>
          }
        >
          <AlertTitle>Error</AlertTitle>
          {snackbar.message}
        </Alert>
      </div>
    </Snackbar>
  )
}

const SnackbarService = {
  set: args => dispatch(setSnackbar(args))
}

export { SnackbarComponent, SnackbarService }
