import numeral from 'numeral'
import moment from 'moment'

const CurrencyFormatter = value => {
  return numeral(value).format('0,0.00')
}

const UpperWord = sentence => {
  return sentence.charAt(0).toUpperCase() + sentence.slice(1)
}

const DigitFormatter = value => {
  return numeral(value).format('0,0')
}

const DateFormatter = (value, format = 'MM/DD/YYYY') => {
  return moment(value).format(format)
}

const DateFormatterFromNow = value => {
  return moment(value).fromNow()
}

export { CurrencyFormatter, DigitFormatter, UpperWord, DateFormatter, DateFormatterFromNow }
