import React from 'react'
import Typography from '@material-ui/core/Typography'
import { useSelector, useDispatch } from 'react-redux'
import { setPageTitle } from 'Pages/Protected/Store/actions'

// CSS Classes
import classes from './PageTitleComponent.module.scss'

let dispatch = null

const PageTitleComponent = () => {
  dispatch = useDispatch()
  const pageTitle = useSelector(state => state.pageTitle)
  return (
    <div className={classes.root}>
      <Typography variant='h6' className={classes.title} id='dashboard-page-title'>
        {pageTitle.text}
      </Typography>

      {pageTitle.status && (
        <span
          className={`${classes.status}`}
          style={{ color: pageTitle.status.color, borderColor: pageTitle.status.color }}
        >
          {pageTitle.status.text}
        </span>
      )}
    </div>
  )
}

const PageTitleService = (text, status = null) => {
  dispatch(setPageTitle({ text, status }))
}

export { PageTitleComponent, PageTitleService }
