export const resetEscalation = payload => {
  return {
    type: 'RESET_ESCALATION_MESSAGES',
    payload
  }
}

export const setEscalationShow = payload => {
  return {
    type: 'SET_ESCALATION_SHOW',
    payload
  }
}

export const addEscalationMessages = payload => {
  return {
    type: 'ADD_ESCALATION_MESSAGES',
    payload
  }
}

export const setEscalationMessages = payload => {
  return {
    type: 'SET_ESCALATION_MESSAGES',
    payload
  }
}

export const setEscalationMessage = payload => {
  return {
    type: 'SET_ESCALATION_MESSAGE',
    payload
  }
}

export const setEscalationAssignee = payload => {
  return {
    type: 'SET_ESCALATION_ASSIGNEE',
    payload
  }
}
