import React from 'react'
import Paper from '@material-ui/core/Paper'
import { subscribe } from 'redux-subscriber'

// Components
import { CptTextFields, CptTextField } from '../CptTextField/CptTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import Button from '@material-ui/core/Button'
import Alert from 'SharedComponents/Alert/Alert'
import DentalProcedureFormValidator from './DentalProcedureFormValidator'
import ToothNumber from '../ToothNumber/ToothNumber'

// Styles
import classes from './DentalProcedureForm.module.scss'

// Services
import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'

// Store
import { useSelector, useDispatch } from 'react-redux'

import {
  setDentalProcedure,
  resetSuggestionObject,
  setSuggestionObject,
  addClaimProcedure,
  updateClaimProcedure,
  removeClaimProcedure
} from '../../Store/actions'

export default function DentalProcedureForm (props) {
  const dispatch = useDispatch()
  const [openDialog, setOpenDialog] = React.useState(false)

  const dentalProcedure = useSelector(state => state.dentalProcedure)
  const suggestions = useSelector(state => state.suggestions)
  const [debounce, setDebounce] = React.useState(null)

  const [errors, setErrors] = React.useState({})

  React.useEffect(() => {
    const unsubscribe = subscribe('dentalProcedure.show', state => {
      setErrors({})
      setOpenDialog(false)
    })
    return () => unsubscribe()
  }, [])

  const handleOnChangeProcedureForm = ({ value, field }) => {
    dispatch(setDentalProcedure({ details: { [field]: value } }))
  }

  const handleOnChangeCpt = ({ value, field, isSuggestionClick, selectedIndex }) => {
    dispatch(setDentalProcedure({ details: { [field]: value } }))

    if (isSuggestionClick) {
      if (!isNaN(selectedIndex)) {
        try {
          const i = suggestions.cpt.data[selectedIndex]
          dispatch(resetSuggestionObject({ field: 'cpt' }))
          return dispatch(
            setDentalProcedure({
              details: { description: i.description, code: i.code.toString() }
            })
          )
        } catch (ex) {}
      }
    } else getCptSuggestions({ value, by: field })

    dispatch(resetSuggestionObject({ field: 'cpt' }))
  }

  const getCptSuggestions = ({ value, by }) => {
    if (value.trim().length < 3) return
    clearTimeout(debounce)

    setDebounce(
      setTimeout(() => {
        if (value) {
          IcdCptStorageServices.lookups({
            on: 'cpt',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])
            dispatch(
              setSuggestionObject({
                field: 'cpt',
                suggestion: { [by]: _items, data }
              })
            )
          })
        }
      }, 500)
    )
  }

  const validate = () => {
    const { error } = DentalProcedureFormValidator({
      description: dentalProcedure.details.description,
      code: dentalProcedure.details.code.toString(),
      tooth_number: dentalProcedure.details.tooth_number,
      gross_amount: dentalProcedure.details.gross_amount,
      vat_amount: dentalProcedure.details.vat_amount,
      amount_paid_by_member: dentalProcedure.details.amount_paid_by_member
    })
    console.log(error)
    setErrors(error || {})
    return !!error
  }

  const handleOnSaveProcedure = () => {
    if (validate()) return

    dispatch(addClaimProcedure(dentalProcedure.details))
    dispatch(setDentalProcedure({ show: false }))
  }

  const handleOnUpdateProcedure = index => {
    if (validate()) return

    dispatch(updateClaimProcedure({ index, details: dentalProcedure.details }))
    dispatch(setDentalProcedure({ show: false }))
  }

  const handleOnRemoveProcedure = index => {
    dispatch(removeClaimProcedure({ index }))
    dispatch(setDentalProcedure({ show: false }))
  }

  return (
    <React.Fragment>
      {dentalProcedure.show && (
        <React.Fragment>
          <Paper className={classes.root}>
            <CptTextFields>
              <CptTextField
                id={`procedure-description-autocomplete`}
                codeId={`procedure-code-autocomplete`}
                description={dentalProcedure.details.description}
                suggestions={suggestions.cpt}
                code={dentalProcedure.details.code}
                onChangeDescription={(value, isSuggestionClick, selectedIndex) =>
                  handleOnChangeCpt({
                    field: 'description',
                    value,
                    isSuggestionClick,
                    selectedIndex
                  })
                }
                onChangeCode={(value, isSuggestionClick, selectedIndex) =>
                  handleOnChangeCpt({ field: 'code', value, isSuggestionClick, selectedIndex })
                }
                errors={errors}
              />
            </CptTextFields>

            <div className={classes.groups}>
              <ToothNumber
                id='tooth-number'
                label='Tooth Number'
                required
                value={dentalProcedure.details.tooth_number}
                onChange={event =>
                  handleOnChangeProcedureForm({
                    field: 'tooth_number',
                    value: event.target.value
                  })
                }
                error={errors.tooth_number}
              />
            </div>
            <div className={classes.groups}>
              <div className={classes['input']}>
                <TextFieldCurrency
                  id='procedure-gross-amount'
                  label='Gross Amount'
                  value={dentalProcedure.details.gross_amount}
                  onChange={event =>
                    handleOnChangeProcedureForm({
                      field: 'gross_amount',
                      value: event.target.value
                    })
                  }
                  error={errors.gross_amount}
                />
              </div>

              <div className={classes['input']}>
                <TextFieldCurrency
                  id='procedure-vat-amount'
                  label='VAT Amount'
                  value={dentalProcedure.details.vat_amount}
                  onChange={event =>
                    handleOnChangeProcedureForm({
                      field: 'vat_amount',
                      value: event.target.value
                    })
                  }
                  error={errors.vat_amount}
                />
              </div>

              <div className={classes['input']}>
                <TextFieldCurrency
                  id='procedure-amount-paid-by-member'
                  label='Amount Paid by Member'
                  value={dentalProcedure.details.amount_paid_by_member}
                  onChange={event =>
                    handleOnChangeProcedureForm({
                      field: 'amount_paid_by_member',
                      value: event.target.value
                    })
                  }
                  error={errors.amount_paid_by_member}
                />
              </div>
            </div>

            <div className={`${classes.groups} ${classes.buttons}`}>
              {dentalProcedure.mode === 'add' && (
                <React.Fragment>
                  <Button
                    variant='contained'
                    color='primary'
                    className='button-positive'
                    onClick={handleOnSaveProcedure}
                  >
                    Save
                  </Button>
                  <Button
                    variant='contained'
                    color='secondary'
                    onClick={() => dispatch(setDentalProcedure({ show: false }))}
                  >
                    Cancel
                  </Button>
                </React.Fragment>
              )}
              {dentalProcedure.mode !== 'add' && (
                <React.Fragment>
                  <Button
                    variant='contained'
                    color='primary'
                    className='button-positive'
                    onClick={() => handleOnUpdateProcedure(dentalProcedure.index)}
                  >
                    Update
                  </Button>
                  <Button variant='contained' color='secondary' onClick={() => setOpenDialog(true)}>
                    Delete
                  </Button>
                </React.Fragment>
              )}
            </div>
          </Paper>

          <Alert
            open={openDialog}
            message='Are you sure you want to delete this procedure?'
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setOpenDialog(false)}
            onOk={() => handleOnRemoveProcedure(dentalProcedure.index)}
          />
        </React.Fragment>
      )}
    </React.Fragment>
  )
}
