const initialValue = {
  show: false,
  mode: null,
  index: null,
  details: {
    name: '',
    item_code: '',
    gross_amount: 0,
    vat_amount: 0,
    amount_paid_by_member: 0,
    philhealth_amount_paid: 0,
    philhealth_remarks: '',
    amount_not_covered: 0,
    reason: '',
    amount_due_from_member: 0,
    reason_for_charging_member: ''
  }
}

const hospitalItemReducer = (state = initialValue, action) => {
  switch (action.type) {
    case 'SET_HOSPITAL_ITEM':
      return (() => {
        const { details = {}, ...others } = action.payload
        return {
          ...initialValue,
          ...state,
          ...others,
          details: others.mode === 'add' ? initialValue.details : { ...state.details, ...details }
        }
      })()

    default:
      return state
  }
}

export default hospitalItemReducer
