import instance from './instance'
const axios = instance()
const axiosClient = instance('/client')
export default class CommonServices {
  // static login (data) {
  //   return axios({
  //     url: '/auth/login',
  //     method: 'POST',
  //     data: { ...data, isAuthorized: false }
  //   })
  // }

  static login (data) {
    return axiosClient({
      url: '/api/login',
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data: { ...data, isAuthorized: false }
    })
  }

  static logout (data) {
    return axiosClient({
      url: '/api/logout',
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }
  static getMe (data) {
    return axios({
      url: '/me',
      method: 'GET'
    })
  }
}
