import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import TextFieldSubmit from 'SharedComponents/TextFieldSubmit/TextFieldSubmit'
import Alert from 'SharedComponents/Alert/Alert'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './ChatBox.module.scss'

// Validator
import ExcalateValidator from './EscalateValidator'

export default function ChatBox (props) {
  let {
    messages = [],
    title,
    permissions,
    placeholder = 'Compose Message',
    confirmMessage = 'Are you sure you want to reply to the escalated claim?',
    okLabel = 'Send',
    cancelLabel = 'Cancel',
    onSubmitComment = () => {},
    confirm = false
  } = props

  if (permissions.options) {
    confirmMessage = permissions.options.confirmMessage
    okLabel = permissions.options.okLabel
    cancelLabel = permissions.options.cancelLabel
    confirm = permissions.options.confirm
  }

  let lastDate = ''

  const [confirmSubmitDialog, setConfirmSubmitDialog] = React.useState(false)
  const [errors, setErrors] = React.useState({})
  const [comment, setComment] = React.useState('')

  const isMe = args => {
    const user_id = localStorage.getItem('user_id')
    return args._id === user_id
  }

  const doDateSplit = date => {
    const sdate = DateFormatter(date)

    if (lastDate !== sdate) {
      lastDate = sdate
      return true
    }
    return false
  }

  const doValidate = () => {
    const { error } = ExcalateValidator({ comment })
    if (error) {
      setErrors(error)
      return !!error
    }
  }

  const doSubmit = () => {
    if (confirm) setConfirmSubmitDialog(true)
    else onSubmitComment(comment)
  }

  return (
    <React.Fragment>
      {permissions.read && (
        <div className={classes.root}>
          <Paper className={classes.wrapper}>
            {title && (
              <div className={classes.header}>
                <Typography variant='h6' className={classes.title}>
                  {title}
                </Typography>
              </div>
            )}

            <div className={classes.body}>
              {messages.length <= 0 ? (
                <div className={classes.placeholder}>
                  <Typography variant='subtitle2'>No Message</Typography>
                </div>
              ) : (
                <div className={classes.messages}>
                  {messages.map((m, index) => (
                    <React.Fragment key={`${m.timestamp.object}`}>
                      {doDateSplit(m.timestamp.object) && (
                        <Typography variant='subtitle2' className={classes.split}>
                          {DateFormatter(m.timestamp.object)}
                        </Typography>
                      )}
                      <div
                        key={`${index}`}
                        className={`${classes.balloon} ${isMe(m.sender) ? classes.me : classes.receive} `}
                      >
                        <div className={classes.mheader}>
                          <Typography variant='subtitle2' className={classes.sender}>
                            {m.sender.username}
                          </Typography>
                          <Typography variant='subtitle2' className={classes.time}>
                            {DateFormatter(m.timestamp.object, 'hh:mm A')}
                          </Typography>
                        </div>
                        <div className={classes.mbody}>
                          <Typography
                            variant='subtitle2'
                            style={{ whiteSpace: 'pre-line' }}
                            className={classes.content}
                          >
                            {m.message}
                          </Typography>
                        </div>
                      </div>
                    </React.Fragment>
                  ))}
                </div>
              )}
            </div>
            <div className={classes.action}>
              {permissions.create && (
                <div className={classes['comment-box']}>
                  <TextFieldSubmit
                    className={classes.comment}
                    multiline
                    rows={5}
                    id=''
                    value={comment}
                    autoFocus
                    fullWidth
                    placeholder={placeholder}
                    onChange={event => setComment(event.target.value)}
                    onSubmit={event => !doValidate() && doSubmit()}
                    error={errors.comment}
                  />
                </div>
              )}
            </div>
          </Paper>
          <Alert
            open={confirmSubmitDialog}
            message={confirmMessage}
            cancelLabel={cancelLabel}
            okLabel={okLabel}
            onCancel={() => setConfirmSubmitDialog(false)}
            onOk={() => {
              setConfirmSubmitDialog(false)
              onSubmitComment(comment)
            }}
          />
        </div>
      )}
    </React.Fragment>
  )
}
