import React from 'react'
import Radio from '@material-ui/core/Radio'
import Tooltip from '@material-ui/core/Tooltip'
import IconButton from '@material-ui/core/IconButton'
import DeleteIcon from '@material-ui/icons/Delete'

// Compoents
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'

// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Typography from '@material-ui/core/Typography'
// import Popover from '@material-ui/core/Popover'

import classes from './ICDTextField.module.scss'

const ICDTextFields = props => {
  const { children, required } = props
  return (
    <div className={classes.root}>
      {/* Headers */}
      <div className={classes.header}>
        <Typography variant='subtitle2' className={classes.delete}>
          {''}
        </Typography>

        <Typography variant='subtitle2' className={classes.description}>
          ICD10 Description {required.description && ' *'}
        </Typography>

        <Typography variant='subtitle2' className={classes.code}>
          ICD10 Code {required.code && ' *'}
        </Typography>

        <Typography variant='subtitle2' className={classes.primary}>
          Primary
        </Typography>

        <Typography variant='subtitle2' className={classes.discrepancy}>
          {''}
        </Typography>
      </div>

      {children}
    </div>
  )
}

const ICDTextField = props => {
  const {
    id = '',
    codeId = '',
    description = '',
    code = '',
    primary = false,
    suggestions = { description: [], code: [] },
    onChangeDescription,
    onChangeCode,
    onChangePrimary,
    onDeleteDiagnosis,
    error = { description: '', code: '' },
    permissions
  } = props

  const [tooltipOpen, setTooltipOpen] = React.useState(false)
  const handleTooltipOpen = () => {
    setTooltipOpen(!!description.trim())
  }

  return (
    <div className={classes.wrapper}>
      <div className={classes.delete}>
        {permissions.delete && (
          <IconButton id='delete-icd' onClick={index => onDeleteDiagnosis({ index })}>
            <DeleteIcon fontSize='small' />
          </IconButton>
        )}
      </div>

      <Tooltip onClose={() => setTooltipOpen(false)} onOpen={handleTooltipOpen} open={tooltipOpen} title={description}>
        <div className={`${classes['description']}`}>
          <AutoComplete
            id={id}
            value={description}
            suggestions={suggestions.description}
            onChange={(value, isSuggestionClick) => onChangeDescription({ value, isSuggestionClick })}
            error={error.description}
            readOnly={!permissions.update}
          />
        </div>
      </Tooltip>
      <div className={`${classes['code']}`}>
        <AutoComplete
          id={codeId}
          value={code}
          suggestions={suggestions.code}
          onChange={(value, isSuggestionClick) => onChangeCode({ value, isSuggestionClick })}
          error={error.code}
          readOnly={!permissions.update}
        />
      </div>
      <div className={classes.primary}>
        <Radio color='primary' checked={primary} onClick={onChangePrimary} disabled={!permissions.update} />
      </div>
    </div>
  )
}

export { ICDTextFields, ICDTextField }
