import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Slide from '@material-ui/core/Slide'
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography'

import { Document, Page, pdfjs } from 'react-pdf'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import printJS from 'print-js'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { setPdf } from 'Pages/Protected/Store/actions'

// Styles
import classes from './PdfViewer2.module.scss'

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`

const Transition = React.forwardRef(function Transition (props, ref) {
  return <Slide direction='up' ref={ref} {...props} />
})

let dispatch = null

const PdfViewer2Component = () => {
  dispatch = useDispatch()

  const {
    open = false,
    name = '',
    fileName = '',
    initialPageNumber = 1,
    initialScale = 1,
    service,
    onClose,
    enabled
  } = useSelector(state => state.pdf)

  const [file, setFile] = React.useState()
  const [numPages, setNumPages] = React.useState(0)
  const [scale, setScale] = React.useState(initialScale)
  const [pageNumber, setPageNumber] = React.useState(initialPageNumber)

  React.useEffect(() => {
    dispatch(setPdf({ enabled: true }))

    if (service) {
      service.then(response => {
        const _blob = new Blob([response.data])
        setFile(_blob)
      })
    }
  }, [service])

  const handleOnClose = () => {
    if (!enabled) return

    if (onClose) onClose()
    dispatch(setPdf({ open: false, service: null, onClose: null }))
    setFile()
  }

  const download = () => {
    const url = URL.createObjectURL(new Blob([file]))
    const link = document.createElement('a')
    link.href = url
    link.setAttribute('download', `${fileName}.pdf`)
    document.body.appendChild(link)
    link.click()
  }

  const print = async () => {
    try {
      const { ipcRenderer } = window.require('electron')
      const data = await blobToBase64(file)
      ipcRenderer.send('print-to-pdf', { data, fileName })

      dispatch(setPdf({ enabled: false }))
      ipcRenderer.on('print-to-pdf-close', event => {
        dispatch(setPdf({ enabled: true }))
      })
    } catch (error) {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onloadend = function () {
        const dataUrl = reader.result
        const printable = dataUrl.split(',')[1]
        printJS({ printable, type: 'pdf', base64: true })
      }
    }
  }

  const blobToBase64 = blob => {
    return new Promise(resolve => {
      const reader = new FileReader()
      reader.onload = function () {
        const dataUrl = reader.result
        const base64 = dataUrl.split(',')[1]
        resolve(base64)
      }
      reader.readAsDataURL(blob)
    })
  }

  return (
    <Dialog fullScreen open={open} onClose={handleOnClose} TransitionComponent={Transition}>
      <AppBar style={{ position: 'relative' }}>
        <Toolbar>
          <IconButton edge='start' color='inherit' onClick={handleOnClose} aria-label='close'>
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' className={classes.title}>
            Preview Sheet
          </Typography>
        </Toolbar>
      </AppBar>
      <DialogContent style={{ padding: '0px' }}>
        <div className={classes.root}>
          <div className={classes.controls}>
            <Typography className={classes['file-name']} align='left'>
              {name}
            </Typography>
            <Typography className={classes['page-number']}>Page: {pageNumber}</Typography>
            <Tooltip title='Previous Page' className={`${classes.control} ${numPages <= 1 ? classes.disabled : ''}`}>
              <span>
                <IconButton
                  disabled={numPages <= 1}
                  onClick={() => setPageNumber(pageNumber > 1 ? pageNumber - 1 : pageNumber)}
                >
                  <FontAwesomeIcon icon='arrow-alt-circle-left' className={classes.icon} />
                </IconButton>
              </span>
            </Tooltip>

            <Tooltip title='Next Page' className={`${classes.control} ${numPages <= 1 ? classes.disabled : ''}`}>
              <span>
                <IconButton
                  disabled={numPages <= 1}
                  onClick={() => setPageNumber(pageNumber < numPages ? pageNumber + 1 : pageNumber)}
                >
                  <FontAwesomeIcon icon='arrow-alt-circle-right' className={classes.icon} />
                </IconButton>
              </span>
            </Tooltip>

            <Tooltip title='Zoom in' className={classes.control}>
              <span>
                <IconButton onClick={() => setScale(scale + 0.2)}>
                  <FontAwesomeIcon icon='search-plus' className={classes.icon} />
                </IconButton>
              </span>
            </Tooltip>

            <Tooltip title='Zoom out' className={classes.control}>
              <span>
                <IconButton onClick={() => (scale === 0.6 ? '' : setScale(scale - 0.2))}>
                  <FontAwesomeIcon icon='search-minus' className={classes.icon} />
                </IconButton>
              </span>
            </Tooltip>

            <Tooltip title='Print' className={`${classes.control} ${!enabled ? classes.disabled : ''}`}>
              <span>
                <IconButton disabled={!enabled} onClick={print}>
                  <FontAwesomeIcon icon='print' className={classes.icon} />
                </IconButton>
              </span>
            </Tooltip>

            <Tooltip title='Download' className={`${classes.control} ${!enabled ? classes.disabled : ''}`}>
              <span>
                <IconButton disabled={!enabled} onClick={download}>
                  <FontAwesomeIcon icon='save' className={classes.icon} />
                </IconButton>
              </span>
            </Tooltip>
          </div>

          <div className={classes.pdf}>
            <Document
              file={file}
              onLoadSuccess={document => {
                setNumPages(document.numPages)
              }}
            >
              <Page pageNumber={pageNumber} scale={scale} />
            </Document>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  )
}

const PdfViewer2Service = options => dispatch(setPdf(options))

export { PdfViewer2Component, PdfViewer2Service }
