const initialValue = {
  messages: []
}

const reconReducer = (state = initialValue, action) => {
  switch (action.type) {
    case 'RESET_RECON_NOTES':
      return initialValue

    case 'SET_RECON_NOTES':
      return (() => {
        const { messages } = action.payload
        return {
          ...state,
          messages: [...messages]
        }
      })()

    default:
      return state
  }
}

export default reconReducer
