import Joi from '@hapi/joi'

export default function (selected_fields) {
  const available_fields = {
    approval_code: Joi.string()
      .trim()
      .label('Approval Code')
      .allow('', null),
    loa_number: Joi.string()
      .trim()
      .label('LOA No.')
      .allow('', null),

    // TODO Claim Source here

    member_account_number: Joi.string()
      .trim()
      .label('Member Account No.')
      .messages({ 'string.empty': `"Member Account No." is required.` })
      .required(),
    member_last_name: Joi.string()
      .trim()
      .label('Member Last Name')
      .messages({ 'string.empty': `"Member Last Name" is required.` })
      .required(),
    member_first_name: Joi.string()
      .trim()
      .label('Member First Name')
      .messages({ 'string.empty': `"Member First Name" is required.` })
      .required(),
    member_middle_name: Joi.string()
      .trim()
      .label('Member Middle Name')
      .messages({ 'string.empty': `"Member Middle Name" is required.` })
      .when('flag', {
        is: true,
        then: Joi.required(),
        otherwise: Joi.allow('', null)
      }),

    // TODO addt'l dental validation
    flag: Joi.boolean().default(false),

    // TODO Client Account Number*
    // TODO Client Account Name*

    customer_account_number: Joi.string()
      .label('Customer (Company) Account Number')
      .required(),
    company_name: Joi.string()
      .label('Company Name')
      .required(),
    facility_id: Joi.string()
      .trim()
      .label('Medical Partner ID')
      .messages({ 'string.empty': `"Medical Partner ID" is required.` })
      .required(),
    facility_name: Joi.string()
      .trim()
      .label('Medical Partner (Facility) Name')
      .messages({ 'string.empty': `"Medical Partner (Facility) Name" is required.` })
      .required(),
    doctor_id: Joi.string()
      .trim()
      .label('Medical Partner ID')
      .default('')
      .allow('', null),
    doctor_name: Joi.string()
      .trim()
      .label('Medical Partner (Doctor) Name')
      .default('')
      .allow('', null),
    anesthesiologist_id: Joi.string()
      .trim()
      .label('Medical Partner ID (Anesthesiologist)')
      .allow('', null),
    anesthesiologist_name: Joi.string()
      .trim()
      .label('Medical Partner (Anesthesiologist) Name')
      .allow('', null),
    date_from: Joi.date()
      .label('Admission Date (From)')
      .required()
      .max('now'),
    date_to: Joi.date()
      .label('Admission Date (To)')
      .required()
      .min(Joi.ref('date_from'))
      .max('now')
      .messages({
        'date.min': `"Admission Date (To)" should be on or after "Admission Date (From)"`
      }),
    contract_year_from: Joi.date().label('Contract Year (From)'),
    contract_year_to: Joi.date().label('Contract Year (To)'),
    department: Joi.string()
      .trim()
      .label('Department')
      .default('')
      .allow('', null),
    recommendation: Joi.string()
      .trim()
      .label('Recommendation')
      .allow('', null),
    room_type: Joi.string()
      .trim()
      .label('Room Type')
      .messages({ 'string.empty': `"Room Type" is required.` })
      .required(),
    room_rate: Joi.number()
      .label('Room Rate')
      .messages({ 'string.empty': `"Room Rate" is required.` })
      .required(),
    pro_id: Joi.string()
      .trim()
      .label('PRO ID')
      .messages({ 'string.empty': `"PRO ID" is required.` })
      .required(),
    pro_name: Joi.string()
      .trim()
      .label('PRO Name')
      .messages({ 'string.empty': `"PRO Name" is required.` })
      .required(),
    claim_sub_type: Joi.string()
      .trim()
      .label('Claim Sub Type')
      .messages({ 'string.empty': `"Claim Sub Type" is required.` })
      .required(),
    claim_sub_sub_type: Joi.string()
      .trim()
      .label('Claim Sub-subType')
      .allow('', null),
    with_corporate_guarantee: Joi.string()
      .trim()
      .label('Corporate Guaranty')
      .allow('', null),
    reimbursement_type: Joi.string()
      .trim()
      .label('Reimbursement Type')
      .messages({ 'string.empty': `"Reimbursement Type" is required.` })
      .required(),
    medicine_category: Joi.string()
      .trim()
      .label('Medicine Category')
      .messages({ 'string.empty': `"Medicine Category" is required.` })
      .required(),
    reimbursement_category: Joi.string()
      .trim()
      .label('Medicine Category')
      .messages({ 'string.empty': `"Reimbursement Category" is required.` })
      .allow('', null),
    official_receipt_number: Joi.string()
      .trim()
      .label('Medicine Category')
      .messages({ 'string.empty': `"Official Receipt Number" is required.` })
      .required(),
    loa_notes: Joi.string()
      .trim()
      .allow('', null)
  }

  const result = {}
  for (const field of selected_fields) {
    result[field] = available_fields[field]
  }

  if (result['date_from'] && !result['date_to']) {
    result['date_from'] = Joi.date()
      .label('Date of Service')
      .required()
      .max('now')
  }

  return result
}
