const InPatient = ['date_to', 'room_type', 'room_rate', 'pro_id', 'pro_name']
const Dental = ['doctor_id', 'doctor_name', 'contract_year_from', 'contract_year_to', 'date_from']
const other_fields = ['doctor_id', 'doctor_name', 'department']

const AdditionalFields = type => {
  if (type === undefined) return {}

  const additional_fields = type === 'InPatient' ? InPatient : type === 'Dental' ? Dental : other_fields
  let result = {}

  for (let field of additional_fields) result[field] = ''

  return result
}

AdditionalFields.exist = (fields, field) => fields[field] !== undefined

export default AdditionalFields
