const initialValue = {
  messages: [],
  assignee: { id: '', name: '' },
  message: ''
}

const reassignReducer = (state = initialValue, action) => {
  switch (action.type) {
    case 'RESET_REASSIGN_MESSAGES':
      return initialValue

    case 'ADD_REASSIGN_MESSAGES':
      return (() => {
        const { messages } = action.payload
        return {
          ...state,
          messages: [...state.messages, { ...messages }]
        }
      })()

    case 'SET_REASSIGN_MESSAGES':
      return (() => {
        const { messages } = action.payload
        return {
          ...state,
          messages: [...messages]
        }
      })()

    case 'SET_REASSIGN_MESSAGE':
      return (() => {
        return {
          ...state,
          message: action.payload
        }
      })()

    case 'SET_REASSIGN_ASSIGNEE':
      return (() => {
        const { id, name } = action.payload
        return {
          ...state,
          assignee: { id, name }
        }
      })()

    default:
      return state
  }
}

export default reassignReducer
