import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { ErrorPageService } from 'SharedComponents/404/404'

import { exclude } from './exclude'

const handler = {
  success: response => response,
  fail: error => {
    const { response, config } = error

    if (!exclude({ headers: config.headers, status: 'all' })) {
      if (response) {
        const { status } = response

        if (status >= 500 && status < 600) {
          // Internal Server Error
          SnackbarService.set({ open: true, message: 'Something went wrong!', severity: 'error' })
        } else if (status === 404) {
          // Resource not found
          if (!exclude({ headers: response.config.headers, status })) {
            ErrorPageService.redirect()
          }
        }
      } else {
        // No Internet
        SnackbarService.set({ open: true, message: 'No internet connection!', severity: 'error' })
      }
    }

    LoadingService.loading(false)

    return Promise.reject(error)
  }
}

export default handler
