import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import MenuItem from '@material-ui/core/MenuItem'

import classes from './AutoComplete.module.scss'
export default function AutoComplete (props) {
  const { label = '', required, readOnly, suggestions = [], onChange, error, ...other } = props
  const { value = '' } = props

  const [focus, setFocus] = React.useState()
  const [_error, setError] = React.useState()

  React.useEffect(() => {
    setError()
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
  }, [error, value])

  const handleOnChange = event => {
    setError()
    if (typeof onChange === 'function') onChange(event.target.value)
  }

  const handleItemClick = index => {
    setError()
    if (typeof onChange === 'function') onChange(suggestions[index], true, index)
  }

  const handleBlur = e => {
    if (!e.currentTarget.contains(e.relatedTarget)) setFocus(false)
  }

  return (
    <div className={classes.root}>
      {label && (
        <Typography variant='subtitle2' className={classes.label}>
          {label}
          {!readOnly && required ? ' *' : ''}
        </Typography>
      )}

      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper
              className={`${classes['autocomplete-field']} ${
                _error ? classes['has-error'] : focus && classes.focus
              } box-shadow-field`}
              onBlur={handleBlur}
            >
              <InputBase
                className={classes.input}
                onChange={handleOnChange}
                {...other}
                autoComplete='off'
                onFocus={() => setFocus(true)}
              />
              {focus && (
                <Paper className={classes.suggestions} square>
                  {suggestions.map((suggestion, index) => (
                    <MenuItem
                      key={`${props.id}-suggestion-${index}`}
                      onClick={() => {
                        setFocus(true)
                        handleItemClick(index)
                      }}
                    >
                      {suggestion}
                    </MenuItem>
                  ))}
                </Paper>
              )}
            </Paper>
            {_error && <span className={classes.error}>{_error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}
