import React from 'react'

// Services
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Components
import ClaimInPatient from './Components/ClaimInPatient/ClaimInPatient'
import ClaimOutPatientProcedure from './Components/ClaimOutPatientProcedure/ClaimOutPatientProcedure'
import ClaimOutPatientConsultation from './Components/ClaimOutPatientConsultation/ClaimOutPatientConsultation'
import ClaimEmergency from './Components/ClaimEmergency/ClaimEmergency'
import ClaimMedicineAllowance from './Components/ClaimMedicineAllowance/ClaimMedicineAllowance'
import ClaimDental from './Components/ClaimDental/ClaimDental'
import ClaimAPE from './Components/ClaimAPE/ClaimAPE'
// import ClaimECU from './Components/ClaimECU/ClaimECU'
// import ClaimPPE from './Components/ClaimPPE/ClaimPPE'
// import ClaimPharmacy from './Components/ClaimPharmacy/ClaimPharmacy'
// import ClaimMedicalSupplies from './Components/ClaimMedicalSupplies/ClaimMedicalSupplies'
// import ClaimClinicManagement from './Components/ClaimClinicManagement/ClaimClinicManagement'
import ClaimReimbursement from './Components/ClaimReimbursement/ClaimReimbursement'

import { DashboardRedirect } from 'SharedComponents/DashboardRedirect/DashboardRedirect'

export default function GetClaimComponent (_props) {
  const { _type, ...props } = _props
  const SelectecClaimType = selected => {
    // const type = ClaimTypes[selected].other_name

    switch (selected) {
      case 'InPatient':
        return <ClaimInPatient {...props} />

      case 'OutPatientProcedure':
        return <ClaimOutPatientProcedure {...props} />

      case 'OutPatientConsultation':
        return <ClaimOutPatientConsultation {...props} />

      case 'Emergency':
        return <ClaimEmergency {...props} />

      case 'Dental':
        return <ClaimDental {...props} />

      case 'MedicineAllowance':
        return <ClaimMedicineAllowance {...props} />

      case 'APE':
        return <ClaimAPE {...props} />

      // case 'ECU':
      //   return <ClaimECU {...props} />

      // case 'PPE':
      //   return <ClaimPPE {...props} />

      // case 'Pharmacy':
      //   return <ClaimPharmacy {...props} />

      // case 'MedicalSupplies':
      //   return <ClaimMedicalSupplies {...props} />

      // case 'ClinicManagement':
      //   return <ClaimClinicManagement {...props} />

      case 'Reimbursement':
        return <ClaimReimbursement {...props} />

      default:
        LoadingService.loading(false)
        return <DashboardRedirect title='Claim Type Under Development' />
    }
  }

  return <React.Fragment>{SelectecClaimType(_type)}</React.Fragment>
}
