import React from 'react'

import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
// import TextField from '@material-ui/core/TextField'
// import Button from '@material-ui/core/Button'
import Link from '@material-ui/core/Link'
import Button from '@material-ui/core/Button'

// Components
import CustomTextField from 'SharedComponents/TextField/CustomTextField'

import { IcdCptStatusComponent, IcdCptStatusReset, IcdCptStatusSet } from 'SharedComponents/IcdCptStatus/IcdCptStatus'

import { to } from 'await-to-js'
import CommonServices from 'Services/Api/CommonServices'

// import { ConfigController } from 'Controllers/ConfigController'
import ConfigController2 from 'Controllers/ConfigController2'

import classes from './LoginPage.module.scss'
import images from 'Constant/images/images'

export default function LoginPage ({ history }) {
  const [disableControls, setDisableControls] = React.useState(false)
  const [user, setUser] = React.useState({ username: '', password: '' })
  const [loginError, setLoginError] = React.useState({
    show: false,
    message: ''
  })

  React.useEffect(() => {
    IcdCptStatusReset()
    document.title = 'HIMS - Login'
  }, [])

  const submit = async event => {
    event.preventDefault()
    setDisableControls(true)

    const [err, res] = await to(CommonServices.login(user))

    if (err) {
      setDisableControls(false)
      let message = 'Login Failed!'
      try {
        message = err.response.data.message
      } catch (ex) {}

      setLoginError({ show: true, message: message })
    } else {
      const { login, icd10, cpt } = res.data

      await ConfigController2.setUserManagementData(login)

      ConfigController2.updateCPT_ICD({ icd10, cpt, IcdCptStatusSet })

      window.localStorage.setItem('from_login', true)
      history.push('/')
    }
  }

  return (
    <React.Fragment>
      {' '}
      <Grid container>
        <Grid item container xs={8} />
        <Grid item container xs={3} direction='column' justify='center' alignItems='flex-start'>
          <Grid item container className={classes.logo} alignItems='flex-end' justify='center'>
            <img src={images.logo} alt='logo' />
          </Grid>
          <Grid item>
            <Paper className={classes.container}>
              <Typography variant='h5'>Intellicare HIMS</Typography>

              <Typography variant='caption' className={`${classes.error} ${!loginError.show ? classes.hidden : ''}`}>
                {loginError.message}
              </Typography>

              <form className={classes.form}>
                <CustomTextField
                  id='username'
                  label='Username'
                  disabled={disableControls}
                  onChange={e => setUser({ ...user, username: e.target.value })}
                />

                <CustomTextField
                  id='password'
                  label='Password'
                  type='password'
                  disabled={disableControls}
                  onChange={e => setUser({ ...user, password: e.target.value })}
                />

                <Button
                  type='submit'
                  variant='contained'
                  color='primary'
                  className='button-neutral'
                  disabled={disableControls}
                  onClick={submit}
                >
                  Login
                </Button>

                <div className={classes['forgot-password']}>
                  <Link href='https://fb.com'>Forgot your password?</Link>
                </div>
              </form>
            </Paper>
          </Grid>
          <Grid item container>
            <Typography className={classes.note}>
              Problems logging in? Please <a href='./'>contact your IT Team</a> for assistance.
            </Typography>
          </Grid>
        </Grid>
      </Grid>
      {/* Initialize CPT ICD Progress */}
      <IcdCptStatusComponent />
    </React.Fragment>
  )
}
