import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

// Components

import { ICDTextFields, ICDTextField } from '../ICDTextField/ICDTextField'

// Styles
import classes from './DiagnosisList.module.scss'

// Services
import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'

// Store
import { useSelector, useDispatch } from 'react-redux'

import {
  addClaimDiagnosis,
  addSuggestionToArray,
  setClaimPrimaryDiagnosis,
  setClaimDiagnosis,
  removeSuggestionToArray,
  resetSuggestionArray,
  setSuggestionOnArray,
  removeClaimDiagnosis
} from '../../Store/actions'

export default function DiagnosisList (props) {
  const { items, errors = [] } = props

  const dispatch = useDispatch()
  const suggestions = useSelector(state => state.suggestions)

  const handleOnClickAddDiagnosis = () => {
    dispatch(addClaimDiagnosis())
    dispatch(addSuggestionToArray({ field: 'icd10' }))
  }

  const handleOnChangeIcd10 = args => {
    const { field, value, isSuggestionClick, index } = args

    dispatch(setClaimDiagnosis({ index, data: { [field]: value } }))

    if (isSuggestionClick) {
      for (let i of suggestions.icd10[index].data) {
        if (value === i[field]) {
          dispatch(
            setClaimDiagnosis({
              index,
              data: { description: i.description, code: i.code }
            })
          )
        }
      }
    } else getIcd10Suggestions({ value, by: field, index })

    dispatch(resetSuggestionArray({ field: 'icd10', index }))
  }

  const [debounce, setDebounce] = React.useState(null)

  const getIcd10Suggestions = ({ value, by, index }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        if (value) {
          IcdCptStorageServices.lookups({
            on: 'icd-10',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])

            dispatch(
              setSuggestionOnArray({
                field: 'icd10',
                index,
                by,
                suggestion: _items,
                data
              })
            )
          })
        }
      }, 500)
    )
  }

  const handleOnRemoveDiagnosis = index => {
    dispatch(removeClaimDiagnosis({ index }))
    dispatch(removeSuggestionToArray({ index, field: 'icd10' }))
  }

  React.useEffect(() => items && items.length === 0 && handleOnClickAddDiagnosis(), [])

  return (
    <div className={classes.groups}>
      <div className={`${classes['diagnosis-list']} ${errors.error && classes['has-error']}`}>
        <Typography variant='h6' className={classes.title}>
          Diagnosis List
        </Typography>
        <ICDTextFields>
          {items.map((item, index) => {
            let error = null

            try {
              error = errors.items[index]
            } catch (_error) {}

            return (
              <ICDTextField
                key={`icd-${index}`}
                id={`icd-description-${index}`}
                codeId={`icd-code-${index}`}
                errors={error || {}}
                description={item.description}
                suggestions={suggestions.icd10[index]}
                code={item.code}
                primary={item.primary}
                onChangeDescription={args => handleOnChangeIcd10({ field: 'description', index, ...args })}
                onChangeCode={args => handleOnChangeIcd10({ field: 'code', index, ...args })}
                onChangePrimary={() => dispatch(setClaimPrimaryDiagnosis({ index }))}
                onDeleteDiagnosis={() => handleOnRemoveDiagnosis(index)}
              />
            )
          })}
        </ICDTextFields>

        <Button
          id='add-diagnosis-button'
          className={classes['add-diagnosis-button']}
          onClick={handleOnClickAddDiagnosis}
        >
          + Add a diagnosis
        </Button>
      </div>
      {errors.error && <span className={classes.error}>{errors.list}</span>}
    </div>
  )
}
