import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import DeleteIcon from '@material-ui/icons/Delete'
import Button from '@material-ui/core/Button'

// Components
import Table from 'SharedComponents/Table/Table'
import TextField from 'SharedComponents/TextField/CustomTextField'
import DatePicker from 'SharedComponents/DatePicker/DatePicker'
import Alert from 'SharedComponents/Alert/Alert'

// Styles
import classes from './ReimbursementInfoComponent.module.scss'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

// Libraries

const headCells = [
  { id: 'date-created', name: 'Date Created' },
  { id: 'prepared-by', name: 'Prepared By' },
  { id: 'date-encoded', name: 'Date Encoded' },
  { id: 'released-to', name: 'Released To' }
]

export default function ReimbursementInfoComponent (props) {
  const { show, setShowModal, handleOnSave, doReject } = props
  const {
    batch_number,
    member_last_name,
    member_first_name,
    member_middle_name,
    reimbursement_info: data = []
  } = props.claim

  const container = React.useRef()

  const [showAdd, setShowAdd] = React.useState()
  const [rejectModal, setRejectModal] = React.useState(false)

  const [items, setItems] = React.useState([])
  React.useEffect(() => {
    setItems(data)
  }, [data])

  const handleOnItemClick = ({ index, value = true }) => {
    if (showAdd && value) return

    setItems(items => {
      return items.map((item, _index) => {
        if (_index === index) item.show = value
        return item
      })
    })

    setTimeout(() => {
      setItems(items => {
        return items.map((item, _index) => {
          if (_index === index) item.showCell = value
          return item
        })
      })
    }, 1)
  }

  const handleOnClickAdd = () => {
    setShowAdd(true)

    setTimeout(() => {
      container.current.scrollTo({
        top: container.current.scrollHeight,
        behavior: 'smooth'
      })
    }, 1)
  }

  const handleOnClickCancel = index => {
    if (index === 'add') return setShowAdd()
    handleOnItemClick({ index, value: false })
  }

  const handleOnClickSave = ({ item }) => {
    setItems(items => {
      const _items = [...items, item]
      handleOnItemSave(_items)
      return _items
    })
    setShowAdd()
  }

  const handleOnClickUpdate = ({ index, item }) => {
    items[index] = item
    setItems(items => {
      let _items = items.map((_item, _index) => {
        if (_index === index) _item = { ...item, show: false, showCell: false }
        return _item
      })

      handleOnItemSave(_items)
      return _items
    })
  }

  const handleOnClose = () => setShowModal(false)

  const handleOnItemSave = items => {
    const _items = []

    for (let i of items) {
      _items.push({
        date_created: i.date_created,
        prepared_by: i.prepared_by,
        date_encoded: i.date_encoded,
        released_to: i.released_to,
        date_released: i.date_released,
        date_return: i.date_return,
        remarks: i.remarks,
        reasons: i.reasons
      })
    }

    handleOnSave(_items)
  }

  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography variant='h6' className={classes.title}>
            Reimbursement Info
          </Typography>
          <IconButton aria-label='close' className={classes.button} onClick={handleOnClose}>
            <CloseIcon />
          </IconButton>
        </div>
      </DialogTitle>

      <DialogContent dividers className={classes.content}>
        <div className={classes['basic-info']}>
          <Typography variant='subtitle2' className={classes.label}>
            Batch No.: {batch_number}
          </Typography>

          <Typography variant='subtitle2' className={classes.label}>
            Member Name.: {member_last_name}, {member_first_name} {member_middle_name}
          </Typography>
        </div>
        <Table
          TableContainerProps={{
            style: { height: '100%', overflow: 'auto', position: 'relative' },
            ref: container
          }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {headCells.map(headCell => (
                <TableCell key={headCell.id} id={`header-${headCell.id}`}>
                  {headCell.name}
                </TableCell>
              ))}
            </TableRow>
          }
          pagination={false}
          body={
            <React.Fragment>
              {items.map((item, index) => (
                <React.Fragment key={index}>
                  <TableRow onClick={() => handleOnItemClick({ index })}>
                    <TableCell id={`batch-number-${index}`}>{DateFormatter(item.date_created)}</TableCell>
                    <TableCell id={`prepared-by-${index}`}>{item.prepared_by}</TableCell>
                    <TableCell id={`date-encoded-${index}`}>{DateFormatter(item.date_encoded)}</TableCell>
                    <TableCell id={`released-to-${index}`}>{item.released_to}</TableCell>
                  </TableRow>

                  {item.show && (
                    <ItemForm
                      index={index}
                      item={item}
                      handleOnClickCancel={handleOnClickCancel}
                      handleOnClickUpdate={handleOnClickUpdate}
                    />
                  )}
                </React.Fragment>
              ))}

              {/* Add Form */}
              {showAdd && (
                <ItemForm
                  index='add'
                  item={{}}
                  handleOnClickSave={handleOnClickSave}
                  handleOnClickCancel={handleOnClickCancel}
                />
              )}
              {/* Add Form */}
            </React.Fragment>
          }
        />

        <div className={classes.footer}>
          <Button variant='contained' color='primary' id='cancel' className='button-negative' onClick={handleOnClose}>
            Close
          </Button>

          {doReject && (
            <Button
              variant='contained'
              color='primary'
              id='reject'
              className='button-neutral'
              onClick={() => setRejectModal(true)}
            >
              Reject
            </Button>
          )}

          <Button
            variant='contained'
            color='primary'
            id='save-for-now'
            className='button-positive'
            onClick={handleOnClickAdd}
            disabled={showAdd}
          >
            Add
          </Button>
          <Button variant='contained' color='primary' className='button-positive'>
            Print Notice
          </Button>
        </div>
      </DialogContent>

      <Alert
        open={rejectModal}
        message='Are you sure you want to reject this claim?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setRejectModal(false)}
        onOk={() => {
          setRejectModal(false)
          setShowModal(false)
          doReject()
        }}
      />
    </Dialog>
  )
}

const ItemForm = props => {
  const defaultFunction = () => {}
  const { index, item: _item, readOnly } = props

  // Events
  const {
    handleOnClickCancel = defaultFunction,
    handleOnClickSave = defaultFunction,
    handleOnClickUpdate = defaultFunction
  } = props

  const [item, setItem] = React.useState({})

  React.useEffect(() => {
    const today = new Date().toISOString()

    setItem({
      date_created: today,
      prepared_by: '',
      date_encoded: today,
      released_to: '',
      date_released: today,
      date_return: today,
      remarks: '',
      reasons: [],
      ..._item
    })
  }, [])

  const handleOnChange = ({ field, value }) => {
    setItem(p => ({ ...p, [field]: value }))
  }

  const handleOnClickAddReason = () => {
    setItem(item => ({
      ...item,
      reasons: [...item.reasons, { value: '', other_details: '' }]
    }))
  }
  const handleOnClickRemoveReason = __index => {
    setItem(item => ({
      ...item,
      reasons: item.reasons.filter((item, _index) => __index !== _index)
    }))
  }

  const handleOnChangeReason = ({ field, value, index }) => {
    setItem(item => ({
      ...item,
      reasons: item.reasons.map((item, _index) => {
        if (index === _index) item[field] = value
        return item
      })
    }))
  }

  const _handleOnClickUpdate = () => {
    handleOnClickUpdate({ index, item })
  }

  const _handleOnClickSave = () => {
    handleOnClickSave({ item })
  }

  return (
    <TableRow key={`collapsible-${index}`}>
      <TableCell colSpan={headCells.length} id={`collabsible-cell-${index}`} className={classes['collapsible-cell']}>
        <div className={`${classes.container} ${!item.showCell || index === 'add' ? classes['show-cell'] : ''}`}>
          <div className={classes.cell}>
            <Typography variant='h6' className={classes.label}>
              {index === 'add' ? 'Add Item' : 'Update Item'}
            </Typography>

            <div className={classes.groups}>
              <div className={classes['input']}>
                <DatePicker
                  id={`date-created-${index}`}
                  label='Date Created'
                  value={item.date_created}
                  onChange={event => handleOnChange({ field: 'date_created', value: event.toISOString() })}
                  // error={errors.date_from}
                />
              </div>

              <div className={classes['input']}>
                <TextField
                  id={`prepared-by-${index}`}
                  label='Prepared By'
                  value={item.prepared_by || ''}
                  onChange={event => handleOnChange({ field: 'prepared_by', value: event.target.value })}
                  // error={errors.loa_number}
                />
              </div>

              <div className={classes['input']}>
                <DatePicker
                  id={`date-encoded-${index}`}
                  label='Date Encoded'
                  value={item.date_encoded}
                  onChange={event => handleOnChange({ field: 'date_encoded', value: event.toISOString() })}
                  // error={errors.date_from}
                />
              </div>

              <div className={classes['input']}>
                <TextField
                  id={`released-to-${index}`}
                  label='Released To'
                  value={item.released_to || ''}
                  onChange={event => handleOnChange({ field: 'released_to', value: event.target.value })}
                  // error={errors.loa_number}
                />
              </div>
            </div>

            <div className={classes.groups}>
              <div className={classes['input']}>
                <DatePicker
                  id={`date-released-${index}`}
                  label='Date Released'
                  value={item.date_released}
                  onChange={event => handleOnChange({ field: 'date_released', value: event.toISOString() })}
                />
              </div>

              <div className={classes['input']}>
                <DatePicker
                  id={`return-date-${index}`}
                  label='Return Date'
                  value={item.date_return}
                  onChange={event => handleOnChange({ field: 'date_return', value: event.toISOString() })}
                />
              </div>

              <div className={classes['input']} style={{ width: '50%' }}>
                <TextField
                  constainerStyle={{ width: 'unset', flex: 1 }}
                  multiline
                  rows={3}
                  id='remarks'
                  label='Remarks'
                  value={item.remarks || ''}
                  onChange={event => handleOnChange({ field: 'remarks', value: event.target.value })}
                />
              </div>
            </div>

            <div className={classes['reasons-container']}>
              <div className={classes.header}>
                <Typography variant='subtitle2' className={classes.label}>
                  Reasons
                </Typography>
                <Typography variant='subtitle2' className={classes.label}>
                  Other Details
                </Typography>

                <div className={classes.delete} />
              </div>

              {/* Reasons */}
              {(item.reasons || []).map((item, _index) => (
                <div className={classes.reasons} key={`reason-${_index}`}>
                  <div className={classes['input']} style={{ width: '50%' }}>
                    <TextField
                      id={`reason-text-${_index}`}
                      value={item.value || ''}
                      onChange={event =>
                        handleOnChangeReason({ field: 'value', value: event.target.value, index: _index })
                      }
                    />
                  </div>

                  <div className={classes['input']} style={{ width: '50%' }}>
                    <TextField
                      id={`reason-other-details-${_index}`}
                      value={item.other_details || ''}
                      onChange={event =>
                        handleOnChangeReason({ field: 'other_details', value: event.target.value, index: _index })
                      }
                    />
                  </div>

                  <div className={classes.delete}>
                    <IconButton id='delete-reason'>
                      <DeleteIcon fontSize='small' onClick={() => handleOnClickRemoveReason(_index)} />
                    </IconButton>
                  </div>
                </div>
              ))}

              <Typography
                variant='body2'
                className={`${classes['add-link']} ${(item.reasons || []).length ? '' : classes['empty-reasons']}`}
                onClick={() => handleOnClickAddReason()}
              >
                + Add a reason
              </Typography>
              {/* Reasons */}
            </div>

            <div className={classes.footer}>
              <React.Fragment>
                {!readOnly && (
                  <React.Fragment>
                    <Button
                      variant='contained'
                      color='primary'
                      id='cancel'
                      className='button-negative'
                      onClick={() => handleOnClickCancel(index)}
                    >
                      Cancel
                    </Button>
                    {index === 'add' && (
                      <React.Fragment>
                        <Button
                          variant='contained'
                          color='primary'
                          id='save-for-now'
                          className='button-positive'
                          onClick={_handleOnClickSave}
                        >
                          Save
                        </Button>
                      </React.Fragment>
                    )}
                    {index !== 'add' && (
                      <React.Fragment>
                        <Button
                          variant='contained'
                          color='primary'
                          id='save-for-now'
                          className='button-positive'
                          onClick={_handleOnClickUpdate}
                        >
                          Update
                        </Button>
                      </React.Fragment>
                    )}
                  </React.Fragment>
                )}
              </React.Fragment>
            </div>
          </div>
        </div>
      </TableCell>
    </TableRow>
  )
}
