import React from 'react'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'

// CSS Classes
import classes from './TaskTrayComponent.module.scss'

export default function TaskTrayComponent ({ children, sizes, title = 'TASK TRAY', showTitle = true }) {
  let items = React.Children.toArray(children)

  items = items.filter((item, index) => {
    if (item.type.displayName === 'DashboardTileComponent') {
      return React.cloneElement(item, {
        key: index
      })
    }
  })

  const _sizes = sizes || {
    xs: 12,
    sm: 6,
    lg: 4
  }

  return (
    <React.Fragment>
      <Grid container className={classes.root}>
        {showTitle && (
          <Typography variant='subtitle1' className={classes.title}>
            {title}
          </Typography>
        )}
        <Grid container className={classes.wrapper} spacing={3}>
          {items.map((item, index) => {
            let itemSize = item.props.sizes || {}
            let s = { ..._sizes, ...itemSize }
            return (
              <Grid item {...s} key={index}>
                {item}
              </Grid>
            )
          })}
        </Grid>
      </Grid>
    </React.Fragment>
  )
}
