import React from 'react'
// import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import moment from 'moment'

// Components
import DropDown from 'SharedComponents/DropDown/DropDown'
import CustomTextField from 'SharedComponents/TextField/CustomTextField'
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import TextFieldNumber from 'SharedComponents/TextFieldNumber/TextFieldNumber'
import Alert from 'SharedComponents/Alert/Alert'

// Constant
import ClaimTypes from 'Constant/claimTypes'

// Styles
import classes from './BatchForm.module.scss'

// Validator
import BatchFormValidator from './BatchFormValidator'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { setShowBatchForm, setSelectedBatch } from '../../Store/actions'

const claim_types = ClaimTypes.dropdown()

const claim_sources = [
  { value: 'Hospital', text: 'Hospital' },
  { value: 'Clinic', text: 'Clinic' }
]

export default function BatchForm ({
  close,
  open,
  onSave,
  bundle,
  onChange,
  disableControls,
  onChangeDoctor,
  disableComponents
}) {
  const dispatch = useDispatch()
  const batch = useSelector(state => state.batch.selected)
  const show = useSelector(state => state.batch.show)
  const action = useSelector(state => state.batch.action)
  const suggestions = useSelector(state => state.suggestions)
  const saveControl = useSelector(state => state.batch.saveControl)

  const [confirmAction, setConfirmAction] = React.useState({
    show: false,
    message: 'Are you sure you want to save this batch?'
  })

  const [errors, setErrors] = React.useState({})
  React.useEffect(() => {
    setErrors({})
  }, [])

  const validate = () => {
    const { error } = BatchFormValidator({
      claim_type: batch.claim_type,
      claim_source: batch.claim_source,
      total_rcs: batch.total_rcs,
      amount_billed: batch.amount_billed,
      notes: batch.notes,
      doctor_id: batch.doctor_id,
      doctor_name: batch.doctor_name
    })
    setErrors(error || {})
    return !!error
  }

  return (
    <React.Fragment>
      <Dialog open={show}>
        <DialogTitle>{action}</DialogTitle>
        <DialogContent>
          <Grid container spacing={3} className={classes.root}>
            <Grid item sm={6} xs={12} className={classes.input}>
              <CustomTextField readOnly='true' label='Bundle No.' value={bundle.bundle_number} />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <CustomTextField readOnly='true' label='SOA No.' value={bundle.soa_number} />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <CustomTextField
                readOnly='true'
                label='Date Received'
                value={moment(bundle.date_received).format('MM/DD/YYYY')}
              />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <CustomTextField readOnly='true' label='Due Date' value={moment(bundle.due_date).format('MM/DD/YYYY')} />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <CustomTextField readOnly='true' label='Medical Partner ID' value={bundle.facility_id} />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <CustomTextField readOnly='true' label='Medical Partner Name (Facility)' value={bundle.facility_name} />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <AutoComplete
                id='doctor-id'
                label='Medical Partner ID'
                value={batch.doctor_id}
                disabled={disableControls}
                suggestions={suggestions.doctor.id}
                onChange={(value, isSuggestionClick) =>
                  onChangeDoctor({
                    field: 'doctor_id',
                    value,
                    isSuggestionClick
                  })
                }
                error={errors.doctor_id}
              />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <AutoComplete
                id='doctor-name'
                label='Medical Partner Name (Doctor)'
                value={batch.doctor_name}
                disabled={disableControls}
                suggestions={suggestions.doctor.full_name}
                onChange={(value, isSuggestionClick) =>
                  onChangeDoctor({
                    field: 'doctor_name',
                    value,
                    isSuggestionClick
                  })
                }
                error={errors.doctor_name}
              />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <DropDown
                id='claim-type'
                label='Claim Type'
                required
                items={claim_types}
                value={batch.claim_type || ''}
                disabled={disableControls}
                onChange={event => dispatch(setSelectedBatch({ claim_type: event.target.value }))}
                error={errors.claim_type}
                disableComponents={disableComponents}
              />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <DropDown
                id='claim-source'
                label='Claim Source'
                required
                items={claim_sources}
                value={batch.claim_source || ''}
                disabled={disableControls}
                onChange={event => dispatch(setSelectedBatch({ claim_source: event.target.value }))}
                error={errors.claim_source}
                disableComponents={disableComponents}
              />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <TextFieldNumber
                id='total-rcs'
                label={'Total No. of RCS'}
                required
                value={batch.total_rcs}
                disabled={disableControls}
                onChange={event => dispatch(setSelectedBatch({ total_rcs: event.target.value }))}
                error={errors.total_rcs}
              />
            </Grid>

            <Grid item sm={6} xs={12} className={classes.input}>
              <TextFieldCurrency
                id='billed-amount'
                label='Billed Amount'
                value={batch.amount_billed || 0}
                disabled={disableControls}
                // currency={false}
                onChange={event => dispatch(setSelectedBatch({ amount_billed: event.target.value }))}
                error={errors.amount_billed}
              />
            </Grid>

            <Grid item xs={12} className={classes.input}>
              <CustomTextField
                id='notes-comments'
                multiline
                rows={6}
                label='Notes/Comments'
                value={batch.notes || ''}
                disabled={disableControls}
                onChange={event => dispatch(setSelectedBatch({ notes: event.target.value }))}
                error={errors.notes}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            id='cancel'
            variant='contained'
            color='primary'
            className='button-negative'
            onClick={() => {
              dispatch(setShowBatchForm(false))
              setErrors({})
            }}
          >
            Cancel
          </Button>
          <Button
            id='save'
            variant='contained'
            color='primary'
            className='button-positive'
            disabled={saveControl}
            onClick={() => !validate() && setConfirmAction(state => ({ ...state, show: true }))}
          >
            Save
          </Button>
          <Alert
            open={confirmAction.show}
            message={confirmAction.message}
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setConfirmAction(state => ({ ...state, show: false }))}
            onOk={() => {
              setConfirmAction(state => ({ ...state, show: false }))
              onSave(batch)
            }}
          />
        </DialogActions>
      </Dialog>
    </React.Fragment>
  )
}
