const types = {
  'In Patient': { prefix: 'IP' },
  'Out Patient Procedure': { prefix: 'OP' },
  'Out Patient Consultation': { prefix: 'PF' },
  Reimbursement: { prefix: 'RM' },
  Emergency: { prefix: 'ER' },
  Dental: { prefix: 'DN' },
  'Medicine Allowance': { prefix: 'MA' },
  APE: { prefix: 'AP' },
  ECU: { prefix: 'EC' },
  Pharmacy: { prefix: 'PH' },
  PPE: { prefix: 'PE' },
  'Medical Supplies': { prefix: 'MS' },
  'Clinic Management': { prefix: 'CM' }
}

const dropdown = () => {
  const list = []
  for (const type in types) {
    list.push({ text: type, value: type })
  }
  return list
}

export default { types, dropdown }

