import React from 'react'
import Button from '@material-ui/core/Button'

// Components
import Alert from 'SharedComponents/Alert/Alert'

// Styles
import classes from './ClaimFooter.module.scss'

export default function ClaimFooter (props) {
  const { handleOnSave, setOpenDialog, openDialog, onOk, setShowReimbursementModal } = props
  return (
    <div className={classes.root}>
      {setShowReimbursementModal && (
        <Button
          variant='contained'
          color='primary'
          id='save-for-now'
          className='button-positive'
          onClick={() => setShowReimbursementModal(true)}
        >
          Return
        </Button>
      )}

      <Button
        variant='contained'
        color='primary'
        id='save-for-now'
        className='button-positive'
        onClick={() => handleOnSave(true)}
      >
        Save for now
      </Button>
      <Button variant='contained' color='primary' className='button-positive' onClick={() => handleOnSave(false)}>
        Save
      </Button>
      <Button
        variant='contained'
        color='primary'
        id='cancel'
        className='button-negative'
        onClick={() => setOpenDialog(true)}
      >
        Cancel
      </Button>

      <Alert
        open={openDialog}
        message='Are you sure you want to cancel?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setOpenDialog(false)}
        onOk={onOk}
      />
    </div>
  )
}
