export default function LimitTable (props) {
  const { benefitType, rows } = props

  const [elemRows, setElemRows] = React.useState([])
  const [elemColumns, setElemColumns] = React.useState([])

  React.useEffect(() => {
    setElemColumns(generateColumns({ benefitType }))
    setElemRows(generateRows({ benefitType, rows }))
  }, [props])

  return (
    <div className={classes.root}>
      <div className={classes.table}>
        <Table
          TableContainerProps={{ style: { maxHeight: '400px', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={elemColumns}
          body={elemRows}
          pagination
          count={0}
          page={1}
          limit={5}
          rowsPerPageOptions={[5, 10, 15]}
          onChange={args => {}}
        />
      </div>
    </div>
  )
}
