const claimReducer = (state = {}, action) => {
  switch (action.type) {
    case 'RESET':
      return null

    case 'SET_CLAIM_FIELDS':
      return action.payload

    case 'SET_CLAIM_BASE_DETAILS':
      return (() => {
        const { value, field } = action.payload
        return { ...state, [field]: value }
      })()

    case 'SET_ClAIM_FACILITY':
      return { ...state, ...action.payload }

    case 'SET_ClAIM_DOCTOR':
      return { ...state, ...action.payload }

    case 'SET_ClAIM_ROOM':
      return { ...state, ...action.payload }

    case 'ADD_ClAIM_DIAGNOSIS':
      return {
        ...state,
        diagnosis: [
          ...state.diagnosis,
          {
            code: '',
            description: '',
            primary: false
          }
        ]
      }

    case 'SET_ClAIM_DIAGNOSIS':
      return (() => {
        const { index, data } = action.payload
        return {
          ...state,
          diagnosis: state.diagnosis.map((i, _index) => {
            if (index === _index) {
              for (let f in data) i[f] = data[f]
            }
            return i
          })
        }
      })()

    case 'SET_ClAIM_PRIMARY_DIAGNOSIS':
      return (() => {
        const { index } = action.payload
        return {
          ...state,
          diagnosis: state.diagnosis.map((i, _index) => {
            i.primary = index === _index
            return i
          })
        }
      })()

    case 'REMOVE_ClAIM_DIAGNOSIS':
      return (() => {
        const { index } = action.payload
        return {
          ...state,
          diagnosis: state.diagnosis.filter((i, _index) => index !== _index)
        }
      })()

    case 'ADD_ClAIM_PROCEDURE':
      return {
        ...state,
        procedures: [...state.procedures, action.payload]
      }

    case 'UPDATE_ClAIM_PROCEDURE':
      return (() => {
        const { index, details } = action.payload
        return {
          ...state,
          procedures: state.procedures.map((i, _index) => {
            if (index === _index) {
              for (const f in details) i[f] = details[f]
            }
            return i
          })
        }
      })()

    case 'REMOVE_ClAIM_PROCEDURE':
      return (() => {
        const { index } = action.payload
        return {
          ...state,
          procedures: state.procedures.filter((i, _index) => {
            if (index !== _index) return i
          })
        }
      })()

    case 'ADD_ClAIM_HOSPITAL_ITEM':
      return {
        ...state,
        hospital_items: [...state.hospital_items, action.payload]
      }

    case 'UPDATE_ClAIM_HOSPITAL_ITEM':
      return (() => {
        const { index, details } = action.payload
        return {
          ...state,
          hospital_items: state.hospital_items.map((i, _index) => {
            if (index === _index) {
              for (const f in details) i[f] = details[f]
            }
            return i
          })
        }
      })()

    case 'REMOVE_ClAIM_HOSPITAL_ITEM':
      return (() => {
        const { index } = action.payload
        return {
          ...state,
          hospital_items: state.hospital_items.filter((i, _index) => {
            if (index !== _index) return i
          })
        }
      })()

    case 'ADD_ClAIM_PROFESSIONAL_FEE':
      return {
        ...state,
        professional_fees: [...state.professional_fees, action.payload]
      }

    case 'UPDATE_ClAIM_PROFESSIONAL_FEE':
      return (() => {
        const { index, details } = action.payload
        return {
          ...state,
          professional_fees: state.professional_fees.map((i, _index) => {
            if (index === _index) {
              for (const f in details) i[f] = details[f]
            }
            return i
          })
        }
      })()

    case 'REMOVE_ClAIM_PROFESSIONAL_FEE':
      return (() => {
        const { index } = action.payload
        return {
          ...state,
          professional_fees: state.professional_fees.filter((i, _index) => {
            if (index !== _index) return i
          })
        }
      })()

    case 'SET_CLAIM_PRO':
      return {
        ...state,
        ...action.payload
      }

    default:
      return state
  }
}

export default claimReducer
