function extractLoa (data) {
  return data.map(item => {
    const init = {
      claim_number: item.claim_number ? item.claim_number : '',
      __type:
        item.ticket_type === 'Outpatient Procedure'
          ? 'UtilizationOutPatientProcedure'
          : item.ticket_type === 'Inpatient'
            ? 'UtilizationInPatient'
            : item.ticket_type === 'Consultation'
              ? 'UtilizationOutPatientConsultation'
              : item.ticket_type === 'Emergency Room'
                ? 'UtilizationEmergency'
                : 'UtilizationReimbursemt',
      type: item.type ? item.type : 'IBNR',
      grand_total: item.total_amount ? item.total_amount : 0,
      diagnosis: item.diagnoses ? item.diagnoses : [],
      doctors: item.professional_fees
        ? item.professional_fees.map((doctor, index) => ({
          name: doctor.doctor_name,
          specialization: doctor.specialization,
          id: `${index}`
        }))
        : [],
      date_admitted: item.date_issued ? item.date_issued : '',
      procedures: item.procedures ? item.procedures : [],
      professional_fees: item.professional_fees ? item.professional_fees : []
    }
    return {
      ...item,
      __type: init.__type,
      claim_no: init.claim_number,
      diagnosis: init.diagnosis,
      grand_total: init.grand_total,
      session: 1,
      date_admitted: init.date_admitted,
      type: init.type,
      doctors: init.doctors,
      procedures: init.procedures,
      professional_fees: init.professional_fees
    }
  })
}

function setDoctors (data) {
  return data.map(item => {
    let newDoctor = [{ id: item.partner_doctor_id, name: item.partner_doctor_name }]
    let _doctors = [...item.doctors]
    if (typeof newDoctor[0].id !== 'undefined') {
      _doctors = [..._doctors, ...newDoctor]
    }

    return {
      ...item,
      doctors: _doctors
    }
  })
}

export { extractLoa, setDoctors }
