import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'

import PrintIcon from '@material-ui/icons/Print'

// Components
import Table from 'SharedComponents/Table/Table'
import SearchTextComponent from 'SharedComponents/SearchTextComponent'

// Styles
import classes from './ViewList.module.scss'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const headCells = [
  { id: 'claim-number', name: ' Claim Number', sortField: 'latest.claim_number' },
  { id: 'due-date', name: ' Due Date', sortField: 'latest.date.due' },
  { id: 'facility-name', name: ' Medical Partner Name (Facility)', sortField: 'latest.facility.name' }
]

export default function CustomizedDialogs ({ viewList, setViewList, onViewListChange, onItemClick }) {
  const { category } = viewList

  const handleOnClose = () => {
    setViewList(p => ({ ...p, show: false }))
  }

  const [debounce, setDebounce] = React.useState(null)

  const handleSearchChange = e => {
    const search = e.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        setViewList(p => ({ ...p, search }))
        onViewListChange({ search, page: 1 })
      }, 500)
    )
  }

  const handleOnSort = orderBy => e => {
    setViewList(p => ({ ...p, orderBy, order: !viewList.order }))
    onViewListChange({ sort: `${orderBy}|${viewList.order ? 'asc' : 'desc'}` })
  }

  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={viewList.show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography variant='h6' className={classes.title}>
            {viewList.title}
          </Typography>
          <IconButton aria-label='close' className={classes.button} onClick={handleOnClose}>
            <CloseIcon />
          </IconButton>
        </div>
      </DialogTitle>

      <DialogContent dividers className={classes.content}>
        <div className={classes.search}>
          <SearchTextComponent
            id='search'
            onChange={handleSearchChange}
            placeholder='Search for claim number, date or facility name'
          />
        </div>
        <Table
          TableContainerProps={{ style: { height: '100%', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {headCells.map(headCell => (
                <TableCell key={headCell.sortField} id={headCell.id}>
                  <TableSortLabel
                    active={viewList.orderBy === headCell.sortField}
                    direction={viewList.order ? 'asc' : 'desc'}
                    onClick={handleOnSort(headCell.sortField)}
                  >
                    {headCell.name}
                  </TableSortLabel>
                </TableCell>
              ))}

              <TableCell align='center' />
            </TableRow>
          }
          body={viewList.data.map((row, index) => (
            <TableRow key={row.claim_number}>
              <TableCell id={`claim-number-${index}`}>{row.claim_number}</TableCell>
              <TableCell id={`due-date-${index}`}>{DateFormatter(row.date.due)}</TableCell>
              <TableCell id={`facility-name-${index}`}>{row.facility.name}</TableCell>
              <TableCell
                id={
                  category === 'open'
                    ? `analyze-claim-${index}`
                    : category === 'my'
                      ? `update-claim-${index}`
                      : category === 'submitted'
                        ? `print-${index}`
                        : `update-esc-claim-${index}`
                }
              >
                {category === 'submitted' && (
                  <React.Fragment>
                    <IconButton
                      id={`print-${index}`}
                      aria-label='more'
                      aria-controls='long-menu'
                      aria-haspopup='true'
                      onClick={event =>
                        onItemClick({ category, claim_number: row.claim_number, event: event, claim: row })
                      }
                    >
                      <PrintIcon />
                    </IconButton>
                  </React.Fragment>
                )}

                {category !== 'submitted' && (
                  <Typography
                    variant='caption'
                    className='text-link underline'
                    onClick={() => onItemClick({ category, claim_number: row.claim_number })}
                  >
                    {category === 'open' && 'Analyze'}
                    {category === 'my' && 'Update'}
                    {category === 'escalated' && 'Update'}
                  </Typography>
                )}
              </TableCell>
            </TableRow>
          ))}
          count={viewList.count}
          page={viewList.page}
          limit={viewList.limit}
          rowsPerPageOptions={[10, 15, 20]}
          onChange={onViewListChange}
        />
      </DialogContent>
    </Dialog>
  )
}
