import React from 'react'
import CircularProgress from '@material-ui/core/CircularProgress'
import { useSelector, useDispatch } from 'react-redux'
import { setpageLoading } from 'Pages/Protected/Store/actions'

// Style
import classes from './LoadingComponent.module.scss'

let dispatch = null

const LoadingComponent = ({ onScrollContainer, containerNode }) => {
  const [containerStyle, setContainerStyle] = React.useState({})

  React.useEffect(() => {
    if (containerNode) {
      const { top, left } = containerNode.getBoundingClientRect()
      setContainerStyle({ top: `${top}px`, left: `${left}px` })
    }
  }, [containerNode])

  dispatch = useDispatch()
  const loading = useSelector(state => state.pageLoading)

  return (
    <React.Fragment>
      {loading && (
        <div className={classes.root} style={containerStyle} onWheel={onScrollContainer}>
          <div className={classes.overlay} />
          <CircularProgress className={classes.loading} size={50} />
        </div>
      )}
    </React.Fragment>
  )
}

const LoadingService = {
  loading: value => dispatch(setpageLoading(value))
}

export { LoadingComponent, LoadingService }
