import React from 'react'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Button from '@material-ui/core/Button'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

import moment from 'moment'
import startCase from 'lodash/startCase'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import TaskTray from 'SharedComponents/TaskTrayComponent/TaskTrayComponent'
import TicketList from 'SharedComponents/TicketListComponent/TicketListComponent'
import ViewList from '../../Components/ViewList/ViewList'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BatchEncodingServices from 'Services/Api/BatchEncodingServices'

// Styles
import classes from './Dashboard.module.scss'

export default function Dashboard (props) {
  const [bundles, setBundles] = React.useState({
    created: { count: 0, data: [] },
    submitted: { count: 0, data: [] }
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  const generateBundleNumber = () => {
    BatchEncodingServices.createBundle().then(res => {
      const { data } = res.data
      props.history.push(`/bundles/${data}/create`)
    })
  }

  React.useEffect(() => {
    PageTitleService('Dashboard')
    BreadcrumbsService.setSelectedItems(['dashboard'])

    //* Created Bundles
    Promise.all([
      BatchEncodingServices.getBundles({ category: 'created', limit: 10, sort: 'date.due|desc', range: 'this-week' }),
      BatchEncodingServices.getBundleCount({ category: 'created' })
    ]).then(values => {
      const [batchResponse, batchCountResponse] = values
      const createdBundles = batchResponse.data.data.results

      const createdBundleCount = batchCountResponse.data.data
      setBundles(prevState => ({ ...prevState, created: { data: createdBundles, count: createdBundleCount } }))
    })

    //* Submitted Bundles
    Promise.all([
      BatchEncodingServices.getBundles({ category: 'submitted', limit: 10, sort: 'date.due|desc', range: 'this-week' }),
      BatchEncodingServices.getBundleCount({ category: 'submitted' })
    ]).then(values => {
      const [batchResponse, batchCountResponse] = values
      const submittedBundles = batchResponse.data.data.results

      const submittedBundleCount = batchCountResponse.data.data
      setBundles(prevState => ({ ...prevState, submitted: { data: submittedBundles, count: submittedBundleCount } }))
    })
  }, [])

  const handleFilterChange = category => value => {
    BatchEncodingServices.getBundles({ category, range: value, limit: 10, sort: 'date.due|desc' }).then(
      ({ data: res }) => {
        const { data } = res
        setBundles(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
      }
    )
  }

  const handleOnShowViewList = category => () => {
    setViewList({ ...initialValue, show: true, title: startCase(`${category} Bundles`), category })
    BatchEncodingServices.getBundles({ category, limit: 10, sort: 'date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    BatchEncodingServices.getBundles(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  return (
    <div className={classes.root}>
      <Grid container className={classes.tiles} spacing={3} direction='row'>
        <Grid item className={classes['task-tray']}>
          <TaskTray sizes={{ xs: 12 }}>
            <DashboardTile
              count={{ color: 3, value: bundles.created.count, id: 'created-count' }}
              sizes={{ sm: 12 }}
              title='Created Bundles'
              id='created-bundles'
              viewList={
                <Typography
                  variant='caption'
                  className='bold text-link'
                  onClick={handleOnShowViewList('created')}
                  id='created-view-list'
                >
                  View List
                </Typography>
              }
              actionHeader={
                <Button
                  variant='contained'
                  color='primary'
                  className='button-neutral'
                  onClick={generateBundleNumber}
                  id='create-new-bundle'
                >
                  Create New Bundle
                </Button>
              }
              onFilterChange={handleFilterChange('created')}
            >
              <Table size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell id='created-bundle-no'>Bundle No.</TableCell>
                    <TableCell id='created-due-date'>Due Date</TableCell>
                    <TableCell id='created-facility-name'>Facility Name</TableCell>
                    <TableCell />
                  </TableRow>
                </TableHead>
                <TableBody>
                  {bundles.created.data.map((item, index) => (
                    <TableRow key={item.bundle_number}>
                      <TableCell id={`created-bundle-no-${index}`}>{item.bundle_number}</TableCell>
                      <TableCell id={`created-due-date-${index}`}>
                        {moment(item.date.due).format('MM/DD/YYYY')}
                      </TableCell>
                      <TableCell id={`created-facility-name-${index}`}>{item.facility.name}</TableCell>
                      <TableCell>
                        <Link to={`/bundles/${item.bundle_number}`}>
                          <Typography variant='caption' className='text-link underline' id={`update-bundle-${index}`}>
                            Update
                          </Typography>
                        </Link>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </DashboardTile>

            <DashboardTile
              count={{ color: 3, value: bundles.submitted.count, id: 'submitted-count' }}
              sizes={{ sm: 12 }}
              title='Submitted Bundles'
              id='submitted-bundles'
              viewList={
                <Typography
                  variant='caption'
                  className='bold text-link'
                  onClick={handleOnShowViewList('submitted')}
                  id='submitted-view-list'
                >
                  View List
                </Typography>
              }
              onFilterChange={handleFilterChange('submitted')}
            >
              <Table size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell id='submitted-bundle-no'>Bundle No.</TableCell>
                    <TableCell id='submitted-due-date'>Due Date</TableCell>
                    <TableCell id='submitted-facility-name'>Facility Name</TableCell>
                    <TableCell />
                  </TableRow>
                </TableHead>
                <TableBody>
                  {bundles.submitted.data.map((item, index) => (
                    <TableRow key={item.bundle_number}>
                      <TableCell id={`submitted-bundle-no-${index}`}>{item.bundle_number}</TableCell>
                      <TableCell id={`submitted-due-date-${index}`}>
                        {moment(item.date.due).format('MM/DD/YYYY')}
                      </TableCell>
                      <TableCell id={`submitted-facility-name-${index}`}>{item.facility.name}</TableCell>
                      <TableCell>
                        <Link to={`/bundles/${item.bundle_number}`}>
                          <Typography variant='caption' className='text-link underline' id={`view-bundle-${index}`}>
                            View
                          </Typography>
                        </Link>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </DashboardTile>
          </TaskTray>
        </Grid>

        <Grid item className={classes['ticket-list']}>
          <TicketList
            count={0}
            title='Open Tickets'
            // viewAll={{ to: '/batches?category=open', caption: 'View List' }}
            viewAllLink={{ to: '/batches?category=open', caption: '' }}
          >
            <Table size='small'>
              <TableHead>
                <TableRow>
                  <TableCell>Ticket #</TableCell>
                  <TableCell>Date</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {[].map((item, index) => (
                  <TableRow key={index}>
                    <TableCell>2334324343</TableCell>
                    <TableCell>08/12/2019</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TicketList>
        </Grid>
      </Grid>
      <ViewList viewList={viewList} setViewList={setViewList} onViewListChange={handleOnViewListChange} />
    </div>
  )
}
