import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  code: Joi.string().trim()
    .label('ICD10 Code')
    .required()
    .messages({
      'string.empty': `"ICD10 Code" is required.`
    }),
  description: Joi.string().trim()
    .label('ICD10 Description')
    .required()
    .messages({
      'string.empty': `"ICD10 Description" is required.`
    })
}).options({ abortEarly: false })

const validate = data => {
  const items = []
  let hasPrimary = false
  let itemError = true
  for (let d of data) {
    itemError = false
    if (d.primary) hasPrimary = true
    const { error } = ExtractValidationResult(schema.validate({ code: d.code, description: d.description }))

    items.push(error || {})
  }

  let list = ''

  if (data.length && !hasPrimary) {
    list = 'Diagnosis should have at least one primary item'
    itemError = true
  } else if (!data.length) list = 'Diagnosis should have at least one item'

  for (let i of items) {
    if (i.code) {
      itemError = true
      break
    }
  }

  return {
    items,
    list,
    error: itemError
  }
}

export default validate
