import React from 'react'
import Button from '@material-ui/core/Button'

// Components
import BatchDetails from '../../Components/BatchDetails/BatchDetails'
import ClaimListHeader from '../../Components/ClaimListHeader/ClaimListHeader'
import ClaimList from '../../Components/ClaimList/ClaimList'
import Alert from 'SharedComponents/Alert/Alert'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import ProviderSchedule from '../../Components/ProviderSchedule/ProviderSchedule'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsEncodingServices from 'Services/Api/ClaimsEncodingServices'

// Styles
import classes from './BatchPage.module.scss'

export default function BatchPage (props) {
  const [batchNumber, setBatchNumber] = React.useState(null)
  const [selectedClaimNumber, setSelectedClaimNumber] = React.useState(null)
  const [details, setDetails] = React.useState()
  const [claims, setClaims] = React.useState([])
  const [submitStatus, setsubmitStatus] = React.useState('')
  const [claimCount, setClaimCount] = React.useState(0)
  const [openDialog, setOpenDialog] = React.useState({})
  const [openDeleteDialog, setOpenDeleteDialog] = React.useState(false)
  const [showProviderSchedule, setShowProviderSchedule] = React.useState(false)
  const [forDeletion, setForDeletion] = React.useState({ claim_numbers: [] })
  const [selected, setSelected] = React.useState([])
  const [debounce, setDebounce] = React.useState(null)
  const [dialog, setDialog] = React.useState({})

  const handleSearchChange = search => {
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        /*
         * @returnData is the sanitized input string
         * returned by the component ClaimsListHeader 's
         * inputChange props
         */
        getClaimsAndCount({ batch_number: batchNumber, search })
      }, 500)
    )
  }

  const getClaimsAndCount = ({ batch_number, search = '' }) => {
    ClaimsEncodingServices.getClaims({ batch_number, params: { search } }).then(res => {
      const { results, count } = res.data.data
      setClaims(results)
      setClaimCount(count)
      LoadingService.loading(false)
    })
  }

  React.useEffect(() => {
    const _batchNumber = props.match.params._batchNumber
    setBatchNumber(_batchNumber)

    ClaimsEncodingServices.getBatch({ batch_number: _batchNumber }).then(res => {
      const { data } = res.data
      setDetails(data)

      let _status = {
        text: 'In Progress',
        color: 'blue'
      }

      if (data.status === 'SUBMITTED') {
        _status = {
          text: 'Completed',
          color: 'green'
        }
      }

      setsubmitStatus(_status.text)

      LoadingService.loading(true)

      BreadcrumbsService.setSelectedItems(['dashboard', 'batches', `specificBatch:${_batchNumber}`])

      PageTitleService(`Batch No. ${_batchNumber}`, {
        text: _status.text,
        color: _status.color
      })

      getClaimsAndCount({ batch_number: _batchNumber })
    })
  }, [props.match.params._batchNumber])

  const handleOnItemClick = index => {
    const { claim_number } = claims[index]
    setSelectedClaimNumber(claim_number)

    // if (submitStatus === 'Completed') return

    // props.history.push(`/batches/${batchNumber}/claims/${claim_number}`)
  }

  const handleOnUpdate = () => {
    if (submitStatus === 'Completed') return

    // if (selected.length > 1) {
    //   return setDialog({
    //     open: true,
    //     message: 'Please select ONLY one claim to update.',
    //     okLabel: 'OK',
    //     onOk: () => setDialog(state => ({ ...state, open: false }))
    //   })
    // } else if (!selected.length) {
    //   return setDialog({
    //     open: true,
    //     message: 'Please select one claim to update.',
    //     okLabel: 'OK',
    //     onOk: () => setDialog(state => ({ ...state, open: false }))
    //   })
    // }
    console.log(selectedClaimNumber)
    if (!selectedClaimNumber || selectedClaimNumber === '') {
      return setDialog({
        open: true,
        message: 'Please select one claim to update.',
        okLabel: 'OK',
        onOk: () => setDialog(state => ({ ...state, open: false }))
      })
    }

    props.history.push(`/batches/${batchNumber}/claims/${selectedClaimNumber}`)
  }

  const handleOnSubmit = index => {
    LoadingService.loading(true)
    setOpenDialog(state => ({ ...state, show: false }))

    ClaimsEncodingServices.submitBatch({ batch_number: batchNumber }).then(res => {
      LoadingService.loading(false)
      props.history.push(`/`)
    })
  }

  const onOpenDelete = claim => {
    // setselectedClaim(claim)
    setOpenDeleteDialog(true)
  }

  const onCancelDelete = () => {
    setOpenDeleteDialog(false)
  }

  const onConfirmDelete = () => {
    ClaimsEncodingServices.deleteMultipleClaim({
      batch_number: batchNumber,
      data: forDeletion
    })
      .then(() => {
        getClaimsAndCount({ batch_number: batchNumber })
        setOpenDeleteDialog(false)
        setForDeletion({ claim_numbers: [] })
      })
      .catch(() => {
        setOpenDeleteDialog(false)
      })
  }

  return (
    <div className={classes.root}>
      {details && (
        <React.Fragment>
          <BatchDetails details={details} />
          <ClaimListHeader
            batchNumber={batchNumber}
            type={details.claim.type}
            onClickProviderSchedule={() => setShowProviderSchedule(true)}
            // onClickPPESchedule={() => setShowPPESchedule(true)}
            onClickAdd={() => props.history.push(`/batches/${batchNumber}/claims/add`)}
            count={claimCount}
            status={submitStatus}
            inputChange={handleSearchChange}
            disabled={!!(details.claim.type === 'Reimbursement' && claims.length > 0)}
          />
          <ClaimList
            items={claims}
            selectedClaimNumber={selectedClaimNumber}
            onItemClick={handleOnItemClick}
            type={details.claim.type}
            onConfirm={onConfirmDelete}
            openDialog={openDeleteDialog}
            onCancel={onCancelDelete}
            forDeletion={forDeletion}
            setForDeletion={setForDeletion}
            setSelected={setSelected}
            submittedBatch={submitStatus === 'Completed'}
          />
          <div className={classes.footer}>
            <div className={classes.actions}>
              {submitStatus === 'In Progress' && (
                <React.Fragment>
                  {forDeletion.claim_numbers.length > 0 && (
                    <Button
                      variant='contained'
                      color='primary'
                      className={`${'button-negative'} ${classes['delete-button']}`}
                      onClick={() => {
                        onOpenDelete(true)
                      }}
                      id='cancel'
                    >
                      Delete
                    </Button>
                  )}

                  <Button
                    id='cancel'
                    variant='contained'
                    color='primary'
                    className='button-negative'
                    onClick={() => props.history.push('/')}
                  >
                    Cancel
                  </Button>
                  <Button variant='contained' color='primary' className='button-positive' onClick={handleOnUpdate}>
                    Update Claim
                  </Button>
                  <Button
                    id='submit-batch'
                    variant='contained'
                    color='primary'
                    className='button-positive'
                    onClick={() =>
                      setOpenDialog(state => ({
                        ...state,
                        show: true,
                        message: 'Are you sure you want to submit this batch?',
                        handleOnOk: handleOnSubmit
                      }))
                    }
                  >
                    Submit
                  </Button>
                </React.Fragment>
              )}

              {submitStatus === 'Completed' && (
                <Button
                  variant='contained'
                  color='primary'
                  className='button-negative'
                  onClick={() => props.history.push('/')}
                >
                  Close
                </Button>
              )}
            </div>
          </div>
          <ProviderSchedule
            type={details.claim.type}
            facility={details.facility}
            dialog={showProviderSchedule}
            setDialog={setShowProviderSchedule}
          />

          <Alert
            open={openDialog.show}
            message={openDialog.message}
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setOpenDialog(state => ({ ...state, show: false }))}
            onOk={openDialog.handleOnOk}
          />
        </React.Fragment>
      )}
      <Alert
        open={!!dialog.open}
        message={dialog.message}
        cancelLabel={dialog.cancelLabel}
        okLabel={dialog.okLabel}
        onOk={dialog.onOk}
        onCancel={dialog.onCancel}
      />
    </div>
  )
}
