import React from 'react'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Alert from 'SharedComponents/Alert/Alert'
import Button from '@material-ui/core/Button'

import { CurrencyFormatter, DateFormatter } from 'Utilities/TextFormatter'
import CustomTextField from 'SharedComponents/TextField/CustomTextField'
import TextFieldCurrency from 'SharedComponents/TextFieldCurrency/TextFieldCurrency'
import EnhancedTable from 'SharedComponents/EnhancedTable/EnhancedTable'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Checkbox from '@material-ui/core/Checkbox'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import PaymentRequestServices from 'Services/Api/PaymentRequestServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// CSS
import classes from './PaymentRequestPage.module.scss'

const pendingHeadCells = [
  { id: 'pending-batch-no', numeric: false, center: false, disablePadding: false, label: 'Batch No.' },
  { id: 'pending-claim-type', numeric: false, center: true, disablePadding: false, label: 'Claim Type' },
  { id: 'pending-total-rcs', numeric: false, center: true, disablePadding: false, label: 'Total No. of RCS' },
  { id: 'pending-billed-amount', numeric: true, center: false, disablePadding: false, label: 'Billed Amount' }
]
const submittedHeadCells = [
  { id: 'submitted-batch-no', numeric: false, center: false, disablePadding: false, label: 'Batch No.' },
  { id: 'submitted-claim-type', numeric: false, center: true, disablePadding: false, label: 'Claim Type' },
  { id: 'submitted-total-rcs', numeric: false, center: true, disablePadding: false, label: 'Total No. of RCS' },
  { id: 'submitted-billed-amount', numeric: true, center: false, disablePadding: false, label: 'Billed Amount' }
]
export default function PaymentRequestPage (props) {
  const [bundle, setBundle] = React.useState(null)
  const [bundle_number, setBundleNumber] = React.useState(null)
  const [dialog, setDialog] = React.useState({})

  const [batches, setBatches] = React.useState({
    pending: { data: [], header: {}, loading: true },
    submitted: { data: [], header: {}, loading: true }
  })

  React.useEffect(() => {
    const { bundle_number } = props.match.params

    PageTitleService(`Request for Payment: ${bundle_number}`)
    BreadcrumbsService.setSelectedItems(['dashboard', 'paymentRequest', `specificPaymentRequest:${bundle_number}`])

    setBundleNumber(bundle_number)
    PaymentRequestServices.getPaymentRequest({ bundle_number }).then(({ data: res }) => {
      const { data } = res
      setBundle(data)
      getBatches({ bundle_number, bundle: data })
    })
  }, [])

  const getBatches = ({ bundle_number, bundle }) => {
    // * Pending Payment request
    PaymentRequestServices.getPaymentRequestBatches({ bundle_number, params: { category: 'pending' } }).then(
      ({ data: res }) => resetBatches({ type: 'pending', data: res.data, bundle })
    )

    // * Submitted Payment request
    PaymentRequestServices.getPaymentRequestBatches({ bundle_number, params: { category: 'submitted' } }).then(
      ({ data: res }) => resetBatches({ type: 'submitted', data: res.data, bundle })
    )
  }

  const resetBatches = ({ type, data, bundle }) => {
    const total_batches = `${data.length} / ${bundle.total_batch}`
    const total_rcs = `${data.reduce((acc, batch) => acc + batch.claims.length, 0)} / ${bundle.total_rcs}`

    data = data.map(i => ({
      ...i,
      selected: false,
      total_amount: i.claims.reduce((acc, claim) => acc + claim.utilization_amount, 0)
    }))

    const approved_amount = data.reduce((acc, batch) => acc + batch.total_amount, 0)

    setBatches(state => ({
      ...state,
      [type]: {
        ...state[type],
        data,
        header: {
          total_batches,
          total_rcs,
          approved_amount,
          pr_amount: 0
        },
        loading: false,
        indeterminate: undefined,
        checked: undefined
      }
    }))
  }

  const handleClick = ({ index, batches: selectedBatches }) => {
    const _batch = {
      ...batches[selectedBatches],
      data: batches[selectedBatches].data.map((i, _index) => {
        if (_index === index) i.selected = !i.selected
        return i
      })
    }

    setBatches(state => ({ ...state, [selectedBatches]: _batch }))
    changeSelectAllState({ batches: selectedBatches, data: _batch })
  }

  const handleSelectAllClick = ({ batches: selectedBatches }) => {
    let _batch = batches[selectedBatches]

    _batch.data.map(i => {
      i.selected = _batch.indeterminate ? true : !_batch.checked
      return i
    })

    setBatches(state => ({ ...state, [selectedBatches]: _batch }))
    changeSelectAllState({ batches: selectedBatches, data: _batch })
  }

  const changeSelectAllState = ({ batches: selectedBatches, data }) => {
    let _batch = data || batches[selectedBatches]

    let selected_count = 0
    let pr_amount = 0
    for (let b of _batch.data) {
      if (b.selected) {
        pr_amount += b.total_amount
        selected_count++
      }
    }

    let indeterminate = selected_count ? selected_count < _batch.data.length : false
    let checked = selected_count ? selected_count === _batch.data.length : false

    _batch = { ..._batch, indeterminate, checked, header: { ..._batch.header, pr_amount } }
    setBatches(state => ({ ...state, [selectedBatches]: _batch }))
  }

  const handleOnSubmit = () => {
    let selected = getSelectedPending(batches.pending.data)

    if (!selected.length) {
      return setDialog({
        open: true,
        message: 'Please select a batch for submission to Treasury Department.',
        okLabel: 'Close',
        onOk: () => setDialog(state => ({ ...state, open: false }))
      })
    }

    const onOk = () => {
      LoadingService.loading(true)
      setDialog(state => ({ ...state, open: false }))
      const requestBatch = []

      for (let select of selected) {
        const claims = select.claims.map(claim => {
          return claim.claim_number
        })
        requestBatch.push(PaymentRequestServices.submit({ bundle_number, data: { claims } }))
      }

      Promise.all(requestBatch).then(
        ({ data: res }) => {
          const data = batches.pending.data.filter((i, index) => {
            let should_include = true
            for (let s of selected) {
              if (s.index === index) {
                should_include = false
                break
              }
            }
            if (should_include) return i
          })
          resetBatches({ type: 'pending', data, bundle })
          resetBatches({ type: 'submitted', data: [...selected, ...batches.submitted.data], bundle })
          LoadingService.loading(false)
        },
        err => {
          console.log(err)
        }
      )
    }

    setDialog({
      open: true,
      message: 'Are you sure you want to submit?',
      okLabel: 'Yes',
      cancelLabel: 'No',
      onCancel: () => setDialog(state => ({ ...state, open: false })),
      onOk
    })
  }

  const handleOnCancel = () => {
    setDialog({
      open: true,
      message: 'Are you sure you want to cancel?',
      okLabel: 'Yes',
      onOk: () => props.history.push(`/`),
      cancelLabel: 'Close',
      onCancel: () => setDialog(state => ({ ...state, open: false }))
    })
  }

  const getSelectedPending = data => {
    return data.filter((i, index) => {
      if (i.selected) {
        i.index = index
        return i
      }
    })
  }

  return (
    <div className={classes.root}>
      {bundle ? (
        <React.Fragment>
          <Paper className={classes['text-fields']}>
            <Grid className={classes['sub-container']} container spacing={4}>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='Bundle No.' id='bundle-number' value={bundle.bundle_number} />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='Total No. of RCS' id='total-rcs' value={bundle.total_rcs} />
              </Grid>
              <Grid item xs={12} />

              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField
                  readOnly
                  label='Date Received'
                  id='date-received'
                  value={DateFormatter(bundle.date.received)}
                />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='Due Date' id='date-due' value={DateFormatter(bundle.date.due)} />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='SOA No.' id='soa-number' value={bundle.soa.number} />
              </Grid>

              <Grid item xs={3} className={classes['input-container']}>
                <TextFieldCurrency
                  readOnly
                  label='Total SOA Amount'
                  id='total-soa'
                  value={CurrencyFormatter(bundle.soa.amount)}
                />
              </Grid>

              <Grid item xs={12} />

              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='Medical Partner ID' id='facility-id' value={bundle.facility.id} />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField
                  readOnly
                  label='Medical Partner Name (Facility)'
                  id='facility-name'
                  value={bundle.facility.name}
                />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField readOnly label='Medical Partner ID' id='doctor-id' value={bundle.doctor.id} />
              </Grid>
              <Grid item xs={3} className={classes['input-container']}>
                <CustomTextField
                  readOnly
                  label='Medical Partner Name (Doctor)'
                  id='doctor-name'
                  value={bundle.doctor.name}
                />
              </Grid>
            </Grid>
          </Paper>
          <Paper className={classes['table-container']}>
            <div className={classes.headers}>
              <Typography className={classes.header}>Total Batches: {batches.pending.header.total_batches}</Typography>
              <Typography className={classes.header}>Total RCS: {batches.pending.header.total_rcs}</Typography>
              <Typography className={classes.header}>
                Total Approved Amount: PHP{' '}
                <span id='total-approved-amount'>{CurrencyFormatter(batches.pending.header.approved_amount)}</span>
              </Typography>
              <Typography className={classes.header}>
                Total PR Amount: PHP{' '}
                <span id='total-pr-amount'>{CurrencyFormatter(batches.pending.header.pr_amount)}</span>
              </Typography>
            </div>

            <EnhancedTable
              className={classes.table}
              headCells={pendingHeadCells}
              handleSelectAllClick={() => handleSelectAllClick({ batches: 'pending' })}
              indeterminate={!!batches.pending.indeterminate}
              checked={!!batches.pending.checked}
              checkAll
              checkboxColor='default'
            >
              <TableBody>
                {batches.pending.data.map((batch, index) => {
                  return (
                    <TableRow
                      onClick={event => handleClick({ index, batches: 'pending' })}
                      role='checkbox'
                      tabIndex={-1}
                      key={`${batch.batch_number}-${index}`}
                    >
                      <TableCell padding='checkbox'>
                        <Checkbox checked={batch.selected} color='default' />
                      </TableCell>
                      <TableCell align='left' id={`pending-batch-number-${index}`}>
                        {batch.batch_number}
                      </TableCell>
                      <TableCell align='center' id={`pending-claim-type-${index}`}>
                        {batch.type}
                      </TableCell>
                      <TableCell
                        align='center'
                        id={`pending-total-rcs-${index}`}
                      >{`${batch.claims.length} / ${batch.total_rcs}`}</TableCell>
                      <TableCell align='right' id={`pending-billed-amount-${index}`}>
                        {CurrencyFormatter(batch.total_amount)}
                      </TableCell>
                    </TableRow>
                  )
                })}
                {!batches.pending.data.length && (
                  <TableRow style={{ height: 50 }}>
                    <TableCell colSpan={5} />
                  </TableRow>
                )}
              </TableBody>
            </EnhancedTable>
          </Paper>
          <div className={classes.buttonWrapper}>
            <Button
              variant='contained'
              color='primary'
              className='button-negative'
              onClick={handleOnCancel}
              id='cancel'
            >
              Cancel
            </Button>
            <Button
              variant='contained'
              color='primary'
              className='button-positive'
              onClick={handleOnSubmit}
              id='submit'
            >
              Submit
            </Button>
          </div>

          <Paper className={classes['table-container']}>
            <div className={classes.headers}>
              <Typography className={classes['submit-header']}>Submitted Batches</Typography>
            </div>
            <EnhancedTable className={classes.table} headCells={submittedHeadCells}>
              <TableBody>
                {batches.submitted.data.map((batch, index) => {
                  return (
                    <TableRow
                      // hover
                      tabIndex={-1}
                      key={`${batch.batch_number}-${index}`}
                    >
                      <TableCell align='left' id={`submitted-batch-number-${index}`}>
                        {batch.batch_number}
                      </TableCell>
                      <TableCell align='center' id={`submitted-claim-type-${index}`}>
                        {batch.type}
                      </TableCell>
                      <TableCell
                        align='center'
                        id={`submitted-total-rcs-${index}`}
                      >{`${batch.claims.length} / ${batch.total_rcs}`}</TableCell>
                      <TableCell align='right' id={`submitted-billed-amount-${index}`}>
                        {CurrencyFormatter(batch.total_amount)}
                      </TableCell>
                    </TableRow>
                  )
                })}
                {!batches.submitted.data.length && (
                  <TableRow style={{ height: 50 }}>
                    <TableCell colSpan={5} />
                  </TableRow>
                )}
              </TableBody>
            </EnhancedTable>
          </Paper>
        </React.Fragment>
      ) : (
        ''
      )}

      <Alert
        open={!!dialog.open}
        message={dialog.message}
        cancelLabel={dialog.cancelLabel}
        okLabel={dialog.okLabel}
        onOk={dialog.onOk}
        onCancel={dialog.onCancel}
      />
    </div>
  )
}
