import React from 'react'
import Typography from '@material-ui/core/Typography'
import Paper from '@material-ui/core/Paper'

// Styles
import classes from './ContractRange.module.scss'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

export default function ContractRange (props) {
  const {
    contract_year_from,
    contract_year_to,
    suggestions,
    error,
    // disableControls = false,
    onChange = () => {}
    // readOnly = false
  } = props

  const [focus, setFocus] = React.useState()
  const [_error, setError] = React.useState()

  React.useEffect(() => {
    setError()
    if (!(contract_year_from || contract_year_to)) {
      setError(error)
    }
  }, [error, contract_year_from, contract_year_to])

  const handleOnItemClick = ({ event, item }) => {
    onChange(item)
    setTimeout(() => setFocus(false), 10)
  }
  return (
    <div className={classes['root']}>
      <div
        className={`${classes.container} ${_error ? classes['has-error'] : focus && classes.focus} box-shadow-field`}
        tabIndex={0}
        onFocus={() => setFocus(true)}
        onClick={() => setFocus(true)}
        onBlur={() => setFocus(false)}
      >
        <div className={classes['label']}>
          <Typography variant='subtitle2'>Contract Year:</Typography>
        </div>

        <div className={classes['values']}>
          <Typography variant='subtitle1'>
            <span>From: </span>
            {contract_year_from ? DateFormatter(contract_year_from) : '----------'} <span>To: </span>
            {contract_year_to ? DateFormatter(contract_year_to) : '----------'}
          </Typography>
        </div>

        {focus && (
          <Paper className={`${classes.suggestions} ${error && classes['add-margin']}`} square>
            <div className={classes.header}>
              <Typography variant='subtitle2'>Member Contract Period</Typography>
            </div>

            <div className={classes['values-container']}>
              <div className={classes.message}>
                <Typography variant='body2'>Please select contract period for this claim</Typography>
              </div>

              <div className={classes.list}>
                {suggestions.map((item, index) => (
                  <div key={index} className={classes.item} onClick={event => handleOnItemClick({ event, item })}>
                    {DateFormatter(item.from)} To {DateFormatter(item.to)}
                  </div>
                ))}
              </div>
            </div>
          </Paper>
        )}

        {_error && <span className={classes.error}>"Contract Year" is required.</span>}
      </div>
    </div>
  )
}
