import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import { Table, TableHeader, TableBody, TableRow } from 'SharedComponents/CustomTable/CustomTable'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './ProcedureList.module.scss'

// Store
import { useDispatch } from 'react-redux'
import {
  setProcedure,
  setProfessionalFee,
  setHospitalItem,
  resetSuggestionObject,
  setDentalProcedure
} from '../../Store/actions'

export default function ProcedureList (props) {
  const { data, isDental } = props
  const dispatch = useDispatch()

  const [totalAmount, setTotalAmount] = React.useState(0)

  React.useEffect(() => {
    let amount = 0
    for (let i of data) {
      amount += amountPerItem(i)
    }
    setTotalAmount(amount)
  }, [data])

  React.useState(() => {
    if (isDental) return dispatch(setDentalProcedure({ show: false }))

    dispatch(setProcedure({ show: false }))
  }, [])

  const amountPerItem = i => {
    return (
      parseFloat(i.gross_amount) +
      parseFloat(i.vat_amount) -
      parseFloat(i.amount_paid_by_member) -
      parseFloat(!isDental ? i.philhealth_amount_paid : 0)
    )
  }

  const showProcedureForm = params => e => {
    dispatch(resetSuggestionObject({ field: 'cpt' }))

    if (isDental) {
      dispatch(setDentalProcedure(params))
    } else {
      dispatch(setProcedure(params))
    }

    // TODO currently creating baseform
    dispatch(setHospitalItem({ show: false }))
    dispatch(setProfessionalFee({ show: false }))
  }
  const generateKey = i =>
    `${i.description} ${i.code} ${i.gross_amount} ${i.vat_amount} ${i.amount_paid_by_member} ${i.philhealth_amount_paid}`

  return (
    <Paper className={classes.root}>
      <div className={classes.table}>
        <div className={classes['table-header']}>
          <Typography variant='subtitle2' className={classes['table-description']}>
            List of Procedures
          </Typography>
          <Typography
            variant='body2'
            className={classes['add-link']}
            onClick={showProcedureForm({ show: true, mode: 'add' })}
          >
            + Add Procedure
          </Typography>
        </div>

        <Table>
          <TableHeader
            items={[
              { name: 'Procedure Name' },
              { name: 'CPT Code', align: 'center' },
              { name: 'Amount (PHP)', width: 100, align: 'right' }
            ]}
          />

          <TableBody>
            {data.map((i, index) => (
              <TableRow
                key={`${index} ${generateKey(i)}`}
                className={`${classes['row']}`}
                onClick={showProcedureForm({
                  show: true,
                  mode: 'edit',
                  index,
                  details: data[index]
                })}
              >
                <td>{i.description}</td>
                <td className={`${classes.align} ${classes.center}`}>{i.code}</td>
                <td className={`${classes.align} ${classes.right}`}>{CurrencyFormatter(amountPerItem(i))}</td>
              </TableRow>
            ))}
          </TableBody>
        </Table>

        <div className={classes['table-total']}>
          <Typography variant='body2' className={classes.caption}>
            Running Total (PHP)
          </Typography>
          <Typography variant='body2' className={classes.amount}>
            {CurrencyFormatter(totalAmount)}
          </Typography>
        </div>
      </div>
    </Paper>
  )
}
