import React from 'react'
import Typography from '@material-ui/core/Typography'

// Components
// import TextFieldNumber from 'SharedComponents/TextFieldNumber/TextFieldNumber'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Styles
import classes from './ToothNumber.module.scss'

export default function ToothNumber (props) {
  const { label = '', readOnly, error, onChange, value = '', required = false } = props
  return (
    <div className={classes['root']}>
      <Typography variant='subtitle2' className={classes['label']}>
        {`${label} ${required ? '*' : ''}`}
      </Typography>
      <div>
        <TextField value={value} id='tooth-number' onChange={onChange} readOnly={readOnly} error={error} />
      </div>
    </div>
  )
}
