import React from 'react'

// Components
import Typography from '@material-ui/core/Typography'
import TreeView from '@material-ui/lab/TreeView'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import ChevronRightIcon from '@material-ui/icons/ChevronRight'
import TreeItem from '@material-ui/lab/TreeItem'

// Styles
import classes from './SideBar.module.scss'

export default function SideBar (props) {
  const { contractId, planTypeList, categoryNode, roomAndBoardList, coverageList, riderList, onItemClick } = props

  // console.log(planTypeList) // Base []
  // console.log(categoryNode) // Room {} name
  // console.log(roomAndBoardList) // Room Rows
  // console.log(coverageList) // CV []
  // console.log(riderList) // RI []

  return (
    <div className={classes.root}>
      <div className={classes['contract-id']}>
        <span>CONTRACT ID</span>
        <span className={classes.id}>{contractId}</span>
      </div>
      <div className={classes['plan-types']}>
        <TreeView defaultCollapseIcon={<ExpandMoreIcon />} defaultExpandIcon={<ChevronRightIcon />}>
          <TreeItem
            key={`node-room-board`}
            className={`${classes.node}`}
            nodeId={`node-room-board`}
            label={'Room and Board'}
            onClick={() => onItemClick({ list: 'room-board', planPackage: 'RB', benefitCategory: 'RBC' })}
          />
          <TreeItem
            key={`node-coverage`}
            className={`${classes.node} ${classes['benefit-label']}`}
            nodeId={`node-coverage`}
            label={'COVERAGE'}
          >
            {coverageList
              .filter(item => item.included)
              .map((item, idx) => (
                <TreeItem
                  key={`node-coverage-${item.tree_id}`}
                  className={classes.node}
                  nodeId={`node-coverage-${item.tree_id}`}
                  label={item.name}
                  onClick={() => onItemClick({ list: 'coverage', planPackage: 'CV', benefitCategory: item.code })}
                />
              ))}
          </TreeItem>
          <TreeItem
            key={`node-riders`}
            className={`${classes.node} ${classes['benefit-label']}`}
            nodeId={`node-riders`}
            label={'RIDERS'}
          >
            {riderList
              .filter(item => item.included)
              .map((item, idx) => (
                <TreeItem
                  key={`node-riders-${item.tree_id}`}
                  className={classes.node}
                  nodeId={`node-riders-${item.tree_id}`}
                  label={item.name}
                  onClick={() => onItemClick({ list: 'riders', planPackage: 'CV', benefitCategory: item.code })}
                />
              ))}
          </TreeItem>
        </TreeView>
      </div>
    </div>
  )
}
