import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/ca')

export default class ClaimsAdjudicationServices {
  static assignClaim ({ claim_number }) {
    return axios({
      url: `/claims/${claim_number}/assign`,
      method: 'PUT'
    })
  }

  static getLetterOfAdvice ({ claimNo }) {
    return axios({
      url: `/claims/${claimNo}/reports/letter-of-advice`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/pdf' }
    })
  }

  static getAnalysisSheet ({ claimNo }) {
    return axios({
      url: `/claims/${claimNo}/reports/analysis-sheet`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/pdf' }
    })
  }

  static getReports ({ claimNo }) {
    return axios({
      url: `/claims/${claimNo}/reports`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/pdf' }
    })
  }

  static getClaims (params) {
    return axios({
      url: '/claims',
      method: 'GET',
      params
    })
  }

  static getClaimCount (params) {
    return axios({
      url: `/claims/count`,
      method: 'GET',
      params
    })
  }

  static getClaim ({ claim_number }) {
    return new Promise(resolve => {
      Promise.all([
        axios({
          url: `/claims/${claim_number}`,
          method: 'GET'
        }),
        axios({
          url: `/claims/${claim_number}/analyze`,
          method: 'GET'
        })
      ]).then(values => {
        const [getClaimResponse, analyzeRespone] = values
        const res = {
          data: { data: { ...getClaimResponse.data.data, analysis_results: { result: analyzeRespone.data.data } } }
        }
        resolve(res)
      })
    })
  }

  static update ({ claim_number, body }) {
    return axios({
      url: `/claims/${claim_number}`,
      method: 'PUT',
      data: body
    })
  }

  static analyze ({ claim_number }) {
    return axios({
      url: `/claims/${claim_number}/analyze`,
      method: 'GET'
    })
  }

  static approve ({ claim_number }) {
    return axios({
      url: `/claims/${claim_number}/approve`,
      method: 'POST'
    })
  }

  static reject ({ claim_number }) {
    return axios({
      url: `/claims/${claim_number}/reject`,
      method: 'POST'
    })
  }

  static escalate ({ claim_number, data }) {
    return axios({
      url: `/claims/${claim_number}/escalate`,
      method: 'POST',
      data
    })
  }

  static getEscalationCount (params) {
    return axios({
      url: `/escalations/count`,
      method: 'GET',
      params
    })
  }

  static getEscalations ({ claim_number }) {
    return axios({
      url: `/escalations/${claim_number}`,
      method: 'GET'
    })
  }

  static returnEscalation ({ claim_number }) {
    return axios({
      url: `/escalations/${claim_number}/return`,
      method: 'PATCH'
    })
  }

  static getRejectionCount (params) {
    return axios({
      url: `/rejections/count`,
      method: 'GET',
      params
    })
  }

  static getRejections ({ claim_number }) {
    return axios({
      url: `/rejections/${claim_number}`,
      method: 'GET'
    })
  }

  static reconsiderRejection ({ claim_number }) {
    return axios({
      url: `/rejections/${claim_number}/reconsider`,
      method: 'PATCH'
    })
  }

  static lookups ({ on, params }) {
    return axios({
      url: `/lookups/${on}`,
      method: 'GET',
      params: { limit: 10, ...params },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getComments ({ claim_number }) {
    return axios({
      url: `/claims/${claim_number}/comments`,
      method: 'GET'
    })
  }

  static getUsers (params) {
    return axios({
      url: `/lookups/users`,
      method: 'GET',
      params,
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getDropdown (params) {
    return axios({
      url: '/lookups/dropdown',
      method: 'GET',
      params,
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getUtilizations ({ member_account_no, type, icd_code }) {
    return axios({
      url: `/claims/${member_account_no}/utilizations`,
      method: 'GET',
      params: { type, icd_code }
    })
  }

  static getPartners ({ member_account_no, params }) {
    return axios({
      url: `/claims/${member_account_no}/partners`,
      method: 'GET',
      params,
      data: { member_account_no }
    })
  }

  static getDoctors ({ member_account_no, facility_id, filters, params }) {
    return axios({
      url: `/claims/${member_account_no}/${facility_id}/doctors`,
      method: 'GET',
      params,
      data: { member_account_no, facility_id, filters }
    })
  }

  static isFacilityExists (params) {
    return axios({
      url: `/lookups/medical-partner/exists`,
      method: 'GET',
      params
    })
  }

  static isDoctorExists (params) {
    return axios({
      url: `/lookups/doctor-partner/exists`,
      method: 'GET',
      params
    })
  }
}
