import React from 'react'
import Tooltip from '@material-ui/core/Tooltip'

// Compoents
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'

// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Typography from '@material-ui/core/Typography'
// import Popover from '@material-ui/core/Popover'

import classes from './CptTextField.module.scss'

const CptTextFields = ({ children }) => {
  return (
    <div className={classes.root}>
      {/* Headers */}
      <div className={classes.header}>
        <Typography variant='subtitle2' className={classes.description}>
          Procedure Details *
        </Typography>

        <Typography variant='subtitle2' className={classes.code}>
          CPT Code *
        </Typography>
      </div>

      {children}
    </div>
  )
}

const CptTextField = props => {
  const {
    id = '',
    codeId = '',
    errors,
    description = '',
    code = '',
    suggestions = [],
    onChangeDescription,
    onChangeCode = () => {}
  } = props

  const [tooltipOpen, setTooltipOpen] = React.useState(false)
  const handleTooltipOpen = () => {
    setTooltipOpen(!!description.trim())
  }

  return (
    <div className={classes.wrapper}>
      <div className={classes['sub-wrapper']}>
        <Tooltip
          onClose={() => setTooltipOpen(false)}
          onOpen={handleTooltipOpen}
          open={tooltipOpen}
          title={description}
        >
          <div className={`${classes['description']}`}>
            <AutoComplete
              id={id}
              value={description}
              suggestions={suggestions.description}
              onChange={onChangeDescription}
              error={errors.description}
            />
          </div>
        </Tooltip>

        <div className={`${classes['code']}`}>
          <AutoComplete
            id={codeId}
            value={code}
            suggestions={suggestions.code}
            onChange={onChangeCode}
            error={errors.code}
          />
        </div>
      </div>
    </div>
  )
}

export { CptTextFields, CptTextField }
