import React from 'react'
import Alert from 'SharedComponents/Alert/Alert'

let _setDialog = null
let _resolve = null
const SoftValidatorAlertComponent = () => {
  const [dialog, setDialog] = React.useState({})

  React.useState(() => {
    setDialog(state => ({ ...state, show: false }))
    _setDialog = setDialog
  }, [])

  const action = t => {
    setDialog(state => ({ ...state, show: false }))
    _resolve(t)
  }

  return (
    <Alert
      open={dialog.show}
      message={dialog.message}
      cancelLabel='No'
      okLabel='Yes'
      onCancel={() => action()}
      onOk={() => action(true)}
    />
  )
}

const SoftValidatorService = ({ fields = [] }) => {
  const _fields = []

  fields.map(i => {
    const [key] = Object.keys(i)
    if (!i[key].trim()) _fields.push(key)
  })

  if (!_fields.length) return true

  let message = 'Missing'
  const length = _fields.length

  for (let i = 0; i < length; i++) {
    if (i && i === length - 1) message = `${message} and`
    else if (i && i < length - 1) message = `${message},`

    message = `${message} ${_fields[i]}`
  }

  message = `${message}. Are you sure you want to proceed?`

  return new Promise(resolve => {
    _resolve = resolve
    _setDialog({ show: true, message })
  })
}

export { SoftValidatorAlertComponent, SoftValidatorService }
