import React from 'react'
import { Provider } from 'react-redux'
import { Switch, Route } from 'react-router-dom'

// Pages of Claims Details Encoder
import Dashboard from './Pages/Dashboard/Dashboard'
import BatchesPage from './Pages/BatchesPage/BatchesPage'
import BatchPage from './Pages/BatchPage/BatchPage'
import ClaimPage from './Pages/ClaimPage/ClaimPage'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Store
import Store from './Store'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/'
  },
  {
    label: 'Batches',
    icon: 'box-open',
    to: '/batches'
  }
]

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  batches: {
    text: 'Batches',
    to: '/batches'
  },
  specificBatch: {
    text: data => data,
    to: data => `/batches/${data}`
  },
  addClaim: {
    text: 'Add new claim',
    to: ''
  },
  updateClaim: {
    text: 'Update Claim',
    to: ''
  }
}

function ClaimsDetailsEncoderRoutes () {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <Provider store={Store}>
      <Switch>
        <Route path='/' exact component={Dashboard} />
        <Route path='/batches' exact component={BatchesPage} />
        <Route path='/batches/:_batchNumber' exact component={BatchPage} />
        <Route path='/batches/:_batchNumber/claims/add' exact render={props => <ClaimPage {...props} isAdd />} />
        <Route path='/batches/:_batchNumber/claims/:_claimNumber' exact component={ClaimPage} />
      </Switch>
    </Provider>
  )
}

export default ClaimsDetailsEncoderRoutes
