import ClaimsAdjudicationServices from './ClaimsAdjudicationServices'
import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'

const axios = instance('/c/cr')

export default class ClaimsReconServices extends ClaimsAdjudicationServices {
  static getClaims (params) {
    return axios({
      url: '/claims',
      method: 'GET',
      params
    })
  }

  static getClaimCount (params) {
    return axios({
      url: `/claims/count`,
      method: 'GET',
      params
    })
  }

  static getClaim ({ claim_number }) {
    return axios({
      url: `/claims/${claim_number}`,
      method: 'GET'
    })
  }

  static lookups ({ on, params }) {
    return axios({
      url: `/lookups/${on}`,
      method: 'GET',
      params: { limit: 10, ...params },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }
  static getComments ({ claim_number }) {
    return axios({
      url: `/claims/${claim_number}/comments`,
      method: 'GET'
    })
  }

  static getNotes ({ claim_number }) {
    return axios({
      url: `/claims/${claim_number}/notes`,
      method: 'GET'
    })
  }

  static comment ({ claim_number, data }) {
    return axios({
      url: `/claims/${claim_number}/comment`,
      method: 'POST',
      params: { claim_number },
      data
    })
  }

  static reassign ({ claim_number, data }) {
    return axios({
      url: `/claims/${claim_number}/reassign`,
      method: 'POST',
      data
    })
  }

  static getUtilizations ({ member_account_no, type, icd_code }) {
    return axios({
      url: `/claims/${member_account_no}/utilizations`,
      method: 'GET',
      params: { type, icd_code }
    })
  }

  static getPartners ({ member_account_no, params }) {
    return axios({
      url: `/claims/${member_account_no}/partners`,
      method: 'GET',
      params,
      data: { member_account_no }
    })
  }

  static getDoctors ({ member_account_no, facility_id, filters, params }) {
    return axios({
      url: `/claims/${member_account_no}/${facility_id}/doctors`,
      method: 'GET',
      params,
      data: { member_account_no, facility_id, filters }
    })
  }

  // MOCK ENDPOINT
  static getUsers (params) {
    return axios({
      url: `/lookups/users`,
      method: 'GET',
      params,
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }
}
