import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  doctor_id: Joi.string()
    .trim()
    .label('Medical Partner ID')
    .required(),
  doctor_name: Joi.string()
    .trim()
    .label('Doctor Name')
    .required(),
  specialization: Joi.string()
    .trim()
    .label('Specialization')
    .required(),
  gross_amount: Joi.number()
    .label('Gross Amount')
    .required(),
  vat_amount: Joi.number()
    .label('Vat Amount')
    .max(Joi.ref('gross_amount'))
    .message('"VAT Amount" must be equal or less than "Gross Amount"')
    .required(),
  amount_paid_by_member: Joi.number()
    .label('Amount Paid By Member')
    .required(),
  philhealth_amount_paid: Joi.number()
    .label('PhilHealth Amount Paid')
    .required(),
  philhealth_remarks: Joi.string()
    .trim()
    .label('PhilHealth Remarks')
    .allow('', null),
  amount_not_covered: Joi.number()
    .label('Amount Not Covered')
    .required(),
  reason: Joi.string()
    .trim()
    .label('Reason')
    .when('amount_not_covered', {
      not: 0,
      then: Joi.required(),
      otherwise: Joi.allow('', null)
    }),
  amount_due_from_member: Joi.number()
    .label('Amount Due from Member')
    .required(),
  reason_for_charging_member: Joi.string()
    .trim()
    .label('Reason For Charging Member')
    .when('amount_due_from_member', {
      not: 0,
      then: Joi.required(),
      otherwise: Joi.allow('', null)
    })
}).options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
