import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object()
  .keys({
    bundle_number: Joi.number()
      .label('Bundle Number')
      .integer()
      .greater(0)
      .required(),
    total_rcs: Joi.number()
      .label('Total No. of RCS')
      .required()
      .integer()
      .min(0),
    date_received: Joi.date()
      .label('Date Received')
      .required()
      .max('now'),
    due_date: Joi.date()
      .label('Due Date')
      .required()
      .min(Joi.ref('date_received'))
      .messages({
        'date.min': `"Due Date" should have a maximum length of Date Received`
      }),
    soa_number: Joi.string()
      .trim()
      .label('SOA No')
      .allow('')
      .required(),
    total_soa_amt: Joi.number()
      .label('Total SOA Amount')
      .greater(0)
      .less(100000000)
      .required(),
    facility_id: Joi.string()
      .trim()
      .label('Medical Partner ID')
      .required(),
    facility_name: Joi.string()
      .trim()
      .label('Medical Partner Name (Facility)')
      .required(),
    doctor_id: Joi.string()
      .trim()
      .label('Medical Partner ID')
      .allow('')
      .required(),
    doctor_name: Joi.string()
      .trim()
      .label('Medical Partner Name (Doctor)')
      .allow('')
      .required()
  })
  .options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
