import React from 'react'

// Components
import Alert from 'SharedComponents/Alert/Alert'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TextField from 'SharedComponents/TextField/CustomTextField'
import DropDown from 'SharedComponents/DropDown/DropDown'
import Button from '@material-ui/core/Button'

import { SafeAccess } from 'Utilities/SafeAccess'
// Styles
import classes from './ClaimFooter.module.scss'

// Validations
import EscalateValidator from './EscalateValidator'
import ReconValidator from './ReconValidator'

// Store
import { useSelector, useDispatch } from 'react-redux'
import {
  resetEscalation,
  setEscalationMessage,
  setEscalationAssignee,
  resetReassign,
  setReassignMessage,
  setReassignAssignee
} from '../../Store/actions'

// lodash
import { sortBy } from 'lodash'

export default function ClaimFooter (props) {
  // Events
  const {
    doValidate,
    doReject,
    doViewAnalysisSheet,
    doViewLetterOfAdvice,
    doReassign,
    doSave,
    doEscalate,
    doApprove,
    doCancel,
    isReimbursement
  } = props
  // Variables
  const { disabled, permissions } = props
  // Services
  const { grandTotal, claimType, userManagementService = () => {} } = props

  const dispatch = useDispatch()
  const escalations = useSelector(state => state.escalations)
  const reassign = useSelector(state => state.reassign)
  const rejectStatus = useSelector(state => state.rejectStatus)

  const [escalateModal, setEscalateModal] = React.useState({
    dialog: false,
    confirm: false
  })
  const [reassignModal, setReassignModal] = React.useState({
    dialog: false,
    confirm: false
  })
  const [rejectModal, setRejectModal] = React.useState(false)
  const [approveModal, setApproveModal] = React.useState({ message: '', show: false })
  const [saveModal, setSaveModal] = React.useState(false)
  const [cancelModal, setCancelModal] = React.useState(false)

  const [dropDownUsers, setDropDownUsers] = React.useState([])

  const [escalationErrors, setEscalationErrors] = React.useState({
    message: '',
    assignee: ''
  })

  const [reassignErrors, setReassignErrors] = React.useState({
    message: '',
    assignee: ''
  })

  // Init functions
  const initDropdownUsers = async ({ roles }) => {
    userManagementService.getUsersByRole(roles[0]).then(({ data }) => {
      let users = data.map(user => ({ text: `${user.first_name} ${user.last_name}`, value: user._id }))
      setDropDownUsers(sortBy(users, ['text']))
    })
  }

  // Validators
  const doValidateEscalation = () => {
    const { error } = EscalateValidator({
      message: escalations.message,
      assignee: escalations.assignee.id
    })

    setEscalationErrors({ ...error })
    return !!error
  }

  const doValidateReassign = () => {
    const { error } = ReconValidator({
      message: reassign.message,
      assignee: reassign.assignee.id
    })

    setReassignErrors({ ...error })
    return !!error
  }

  // UI Functions
  const handleEscalateDialog = () => {
    dispatch(resetEscalation())
    initDropdownUsers({ roles: permissions.escalate.options.roles })
    setEscalationErrors({ message: '', assignee: '' })
    dispatch(setEscalationMessage(''))
    setEscalateModal(state => ({ ...state, dialog: true }))
  }

  const handleReassignDialog = () => {
    dispatch(resetReassign())
    initDropdownUsers({ roles: permissions.reassign.options.roles })
    setReassignErrors({ message: '', assignee: '' })
    dispatch(setReassignMessage(''))
    setReassignModal(state => ({ ...state, dialog: true }))
  }

  const handleOnShowModal = () => {
    const { AuthorityMatrix } = permissions.approve.options

    const modalProps = { show: true }

    if (AuthorityMatrix) {
      const { other_types } = AuthorityMatrix

      const selected_amount = AuthorityMatrix[claimType] === undefined ? other_types : AuthorityMatrix[claimType]

      if (grandTotal > selected_amount) {
        modalProps.message =
          'The billed amount to be approved is higher than the threshold. Are you sure you want to Submit the Claim?'
      }
    }

    setApproveModal(modalProps)
    return true
  }

  const handleOnClickReject = () => {
    if (!doValidate()) {
      if (isReimbursement) doReject()
      else setRejectModal(true)
    }
  }

  return (
    <div className={classes.root}>
      {permissions.visible && (
        <React.Fragment>
          {SafeAccess(permissions.analysis_sheet, ['visible']) && (
            <Button
              variant='contained'
              color='secondary'
              className='button-positive'
              onClick={() => !doValidate() && doViewAnalysisSheet({ goBack: false })}
              disabled={disabled && permissions.analysis_sheet.enabled}
            >
              View Analysis Sheet
            </Button>
          )}
          {SafeAccess(permissions.letter_of_advice, ['visible']) && (
            <Button
              variant='contained'
              color='secondary'
              className='button-positive'
              onClick={() => !doValidate() && doViewLetterOfAdvice({ goBack: false })}
              disabled={disabled && permissions.letter_of_advice.enabled}
            >
              View Letter of Advice
            </Button>
          )}
          {SafeAccess(permissions.escalate, ['visible']) && (
            <Button
              variant='contained'
              color='secondary'
              className='button-positive'
              onClick={() => !doValidate() && handleEscalateDialog()}
              disabled={disabled && permissions.escalate.enabled}
            >
              Escalate
            </Button>
          )}
          {SafeAccess(permissions.reject, ['visible']) && (
            <Button
              variant='contained'
              color='secondary'
              onClick={handleOnClickReject}
              disabled={rejectStatus && !isReimbursement}
            >
              Reject
            </Button>
          )}

          {SafeAccess(permissions.return, ['visible']) && (
            <Button
              id='btn-return'
              variant='contained'
              color='primary'
              className='button-positive'
              // onClick={() => setApproveModal(true)}
              // disabled={disabled && permissions.return.enabled}
            >
              Return
            </Button>
          )}

          {SafeAccess(permissions.approve, ['visible']) && (
            <Button
              id='approve'
              variant='contained'
              color='primary'
              className='button-positive'
              onClick={() => !doValidate() && handleOnShowModal()}
              disabled={disabled && permissions.approve.enabled}
            >
              Approve
            </Button>
          )}
          {SafeAccess(permissions.save, ['visible']) && (
            <Button
              id='save-for-now'
              variant='contained'
              color='primary'
              className='button-positive'
              onClick={() => !doValidate() && setSaveModal(true)}
              disabled={disabled && permissions.save.enabled}
            >
              Save for now
            </Button>
          )}
        </React.Fragment>
      )}
      <Button id='cancel' variant='contained' color='secondary' onClick={() => setCancelModal(true)}>
        Cancel
      </Button>
      {permissions.visible && (
        <React.Fragment>
          {SafeAccess(permissions.reassign, ['visible']) && (
            <Button
              variant='contained'
              color='secondary'
              className='button-positive'
              onClick={() => !doValidate() && handleReassignDialog()}
              disabled={disabled && permissions.reassign.enabled}
            >
              Reassign
            </Button>
          )}
        </React.Fragment>
      )}
      {SafeAccess(permissions.escalate, ['enabled']) && (
        <React.Fragment>
          <Dialog
            fullWidth
            maxWidth='md'
            onClose={() => setEscalateModal(state => ({ ...state, dialog: false }))}
            aria-labelledby='customized-dialog-title'
            open={escalateModal.dialog}
            className={classes.escalate}
            PaperProps={{ style: { height: 'auto', display: 'flex' } }}
          >
            <DialogTitle disableTypography className={classes['header-container']}>
              <div className={classes.header}>
                <Typography variant='h6' className={classes.title}>
                  Reason For Escalation
                </Typography>
                <IconButton
                  aria-label='close'
                  className={classes.button}
                  onClick={() => setEscalateModal(state => ({ ...state, dialog: false }))}
                >
                  <CloseIcon />
                </IconButton>
              </div>
            </DialogTitle>

            <DialogContent dividers className={classes.content}>
              <div className={classes.input}>
                <TextField
                  className={classes.comment}
                  multiline
                  rows={5}
                  id=''
                  value={escalations.message}
                  autoFocus
                  fullWidth
                  placeholder='Compose a Message'
                  onChange={event => dispatch(setEscalationMessage(event.target.value))}
                  error={escalationErrors.message}
                />
              </div>
              <div className={classes.footer}>
                <div className={classes.assign}>
                  <Typography variant='subtitle2' className={classes.to}>
                    {dropDownUsers && dropDownUsers.length > 0 ? 'Assign To' : 'No Users'}
                  </Typography>
                  <DropDown
                    id='escalate-to'
                    items={dropDownUsers}
                    value={escalations.assignee.id}
                    onChange={event =>
                      dispatch(
                        setEscalationAssignee({
                          id: event.target.value,
                          name: event.target.value
                        })
                      )
                    }
                    error={escalationErrors.assignee}
                    disableComponents={!!(dropDownUsers && dropDownUsers.length === 0)}
                  />
                </div>

                <div className={classes.action}>
                  <Button
                    onClick={() => setEscalateModal(state => ({ ...state, dialog: false }))}
                    variant='contained'
                    color='primary'
                    className='button-negative'
                  >
                    Cancel
                  </Button>
                  <Button
                    onClick={() => !doValidateEscalation() && setEscalateModal(state => ({ ...state, confirm: true }))}
                    variant='contained'
                    color='primary'
                    className='button-positive'
                  >
                    Submit
                  </Button>
                </div>
              </div>
            </DialogContent>
          </Dialog>
          <Alert
            open={escalateModal.confirm}
            message='Are you sure you want to escalate claim?'
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setEscalateModal(state => ({ ...state, confirm: false }))}
            onOk={() => {
              setEscalateModal(state => ({ ...state, dialog: false, confirm: false }))
              doEscalate({ goBack: true })
            }}
          />
        </React.Fragment>
      )}

      {SafeAccess(permissions.reassign, ['enabled']) && (
        <React.Fragment>
          <Dialog
            fullWidth
            maxWidth='md'
            onClose={() => setReassignModal(state => ({ ...state, dialog: false }))}
            aria-labelledby='customized-dialog-title'
            open={reassignModal.dialog}
            className={classes.reassign}
            PaperProps={{ style: { height: 'auto', display: 'flex' } }}
          >
            <DialogTitle disableTypography className={classes['header-container']}>
              <div className={classes.header}>
                <Typography variant='h6' className={classes.title}>
                  Reassign
                </Typography>
                <IconButton
                  aria-label='close'
                  className={classes.button}
                  onClick={() => setReassignModal(state => ({ ...state, dialog: false }))}
                >
                  <CloseIcon />
                </IconButton>
              </div>
            </DialogTitle>

            <DialogContent dividers className={classes.content}>
              <div className={classes.input}>
                <TextField
                  className={classes.comment}
                  multiline
                  rows={5}
                  id=''
                  value={reassign.message}
                  autoFocus
                  fullWidth
                  placeholder='Compose a Message'
                  onChange={event => dispatch(setReassignMessage(event.target.value))}
                  error={reassignErrors.message}
                />
              </div>
              <div className={classes.footer}>
                <div className={classes.assign}>
                  <Typography variant='subtitle2' className={classes.to}>
                    Assign To:
                  </Typography>
                  <DropDown
                    id='reassign-to'
                    items={dropDownUsers}
                    value={reassign.assignee.id}
                    onChange={event =>
                      dispatch(
                        setReassignAssignee({
                          id: event.target.value,
                          name: event.target.value
                        })
                      )
                    }
                    error={reassignErrors.assignee}
                  />
                </div>

                <div className={classes.action}>
                  <Button
                    onClick={() => setReassignModal(state => ({ ...state, dialog: false }))}
                    variant='contained'
                    color='primary'
                    className='button-negative'
                  >
                    Cancel
                  </Button>
                  <Button
                    onClick={() => !doValidateReassign() && setReassignModal(state => ({ ...state, confirm: true }))}
                    variant='contained'
                    color='primary'
                    className='button-positive'
                  >
                    Submit
                  </Button>
                </div>
              </div>
            </DialogContent>
          </Dialog>
          <Alert
            open={reassignModal.confirm}
            message='Are you sure you want to re-assign claim?'
            cancelLabel='No'
            okLabel='Yes'
            onCancel={() => setReassignModal(state => ({ ...state, confirm: false }))}
            onOk={() => {
              setReassignModal(state => ({ ...state, dialog: false, confirm: false }))
              doReassign({ goBack: true })
            }}
          />
        </React.Fragment>
      )}

      {SafeAccess(permissions.reject, ['enabled']) && (
        <Alert
          open={rejectModal}
          message='Are you sure you want to reject this claim?'
          cancelLabel='No'
          okLabel='Yes'
          onCancel={() => setRejectModal(false)}
          onOk={() => {
            setRejectModal(false)
            doReject({ goBack: true })
          }}
        />
      )}

      {SafeAccess(permissions.approve, ['enabled']) && (
        <Alert
          open={approveModal.show}
          message={approveModal.message || 'Are you sure you want to approve the claim?'}
          cancelLabel='No'
          okLabel='Yes'
          onCancel={() => setApproveModal({ show: false })}
          onOk={() => {
            setApproveModal({ show: false })
            doApprove({ goBack: false })
          }}
        />
      )}

      <Alert
        open={saveModal}
        message='Are you sure you want to save the changes?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setSaveModal(false)}
        onOk={() => {
          setSaveModal(false)
          doSave(true)
        }}
      />

      <Alert
        open={cancelModal}
        message='Are you sure you want to cancel?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setCancelModal(false)}
        onOk={() => {
          setCancelModal(false)
          doCancel({ goBack: true })
        }}
      />
    </div>
  )
}
