import React from 'react'
import Typography from '@material-ui/core/Typography'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Popover from '@material-ui/core/Popover'

// Components

// Styles
import classes from './DiscrepancyBase.module.scss'

export default function DiscrepancyBase (props) {
  // INIT
  const { discrepancy, id = '', children, iconStyle = {}, ...others } = props

  // Breaking down props
  const { result } = discrepancy
  const { retainSpace = false, noMargin = false } = others

  // VARIABLES
  const [anchorEl, setAnchorEl] = React.useState(null)
  const [message, setMessage] = React.useState('')

  // FUNCTIONS
  const spreadResult = result => {
    if (result) {
      let message = ''
      let list = []
      if (typeof result === 'object') {
        if (Array.isArray(result)) {
          list = result.flat(Infinity)
        } else {
          for (const field in result) {
            if (result[field]) list.push(result[field].flat(Infinity))
          }
          list = list.flat(Infinity)
        }
      }

      // const uniqOnly = Array.from(new Set(list))
      // uniqOnly.map(m => m && (message += `\u2022 ${m} \n`))

      list.map(m => m && (message += `\u2022 ${m} \n`))
      setMessage(message)
    }
  }

  React.useEffect(() => {
    spreadResult(result)
  }, [result])

  const open = Boolean(anchorEl)

  const handleClick = event => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const icon = ({ iconStyle }) => {
    return (
      <div
        className={`${classes['discrepancy-icon']} ${
          message === '' ? (retainSpace ? classes['display-hidden'] : classes['display-none']) : ''
        } ${noMargin ? classes['no-margin'] : classes['with-margin']}`}
      >
        <FontAwesomeIcon
          id={`exc-circle-${id}`}
          style={iconStyle}
          icon='exclamation-circle'
          className={`${classes['icon']}`}
          onClick={handleClick}
        />
        <Popover
          id={`simple-popover`}
          open={open}
          anchorEl={anchorEl}
          onClose={handleClose}
          anchorOrigin={{
            vertical: 'center',
            horizontal: 'right'
          }}
          transformOrigin={{
            vertical: 'center',
            horizontal: 'left'
          }}
        >
          <Typography style={{ whiteSpace: 'pre-line' }} className={classes['message']}>
            {message}
          </Typography>
        </Popover>
      </div>
    )
  }

  return (
    <div id={id} className={`${classes[`root`]}`}>
      <div className={`${classes[`input-wrapper`]} ${message !== '' ? classes['discrepancy'] : ''}`}>{children}</div>
      {icon({ iconStyle })}
      {/* {message !== '' && icon({ iconStyle })} */}
    </div>
  )
}
