import React from 'react'

// Styles
import classes from './IcdCptStatus.module.scss'

let reset = null
let set = null
const IcdCptStatusComponent = () => {
  const [data, setData] = React.useState({})

  React.useEffect(() => {
    reset = () => setData({})
    set = values => {
      if (typeof values === 'function') setData(values)
      else setData(p => ({ ...p, ...values }))
    }
  }, [])

  const success = () => {
    const _success = data.cpt_fetched === data.cpt_total && data.icd10_fetched === data.icd10_total

    if (_success) setTimeout(() => setData({}), 2000)

    return _success
  }

  return (
    <React.Fragment>
      {data.show && (
        <div className={classes.root}>
          <p className={`${classes.fetch} ${success() && classes.success}`}>
            <span>Status: </span>
            {success() ? 'Success' : 'Fetching'}
          </p>

          <p>
            <span>CPT: </span>
            {data.cpt_fetched}/{data.cpt_total}
          </p>
          <p>
            <span>ICD: </span>
            {data.icd10_fetched}/{data.icd10_total}
          </p>
        </div>
      )}
    </React.Fragment>
  )
}

const IcdCptStatusReset = () => {
  reset()
}

const IcdCptStatusSet = values => {
  set(values)
}

export { IcdCptStatusComponent, IcdCptStatusReset, IcdCptStatusSet }
