const initialValue = {
  diagnosis: {},
  procedure: {},
  hospital_item: {},
  professional_fee: {}
}

const discrepancyReducer = (state = initialValue, action) => {
  switch (action.type) {
    case 'INIT_DISCREPANCY':
      return initialValue

    case 'SET_DISCREPANCY':
      return { ...initialValue, ...state, ...action.payload }

    default:
      return state
  }
}

export default discrepancyReducer
