import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/cc')

export default class CustomerCareServices {
  static getMember ({ member_account_no }) {
    return axios({
      url: `/members`,
      method: 'GET',
      params: { member_account_number: member_account_no },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getContractPDF ({ member_oid, contract_oid }) {
    return axios({
      url: `/member/${member_oid}/contract/pdf`,
      method: 'GET',
      params: { contract_oid },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getMemberContractBenefit ({ member_oid, contract_oid }) {
    return axios({
      url: `/member/${member_oid}/contract/${contract_oid}/benefit`,
      method: 'GET',
      params: { member_oid, contract_oid },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getMemberContract ({ member_oid, contract_oid }) {
    return axios({
      url: `/member/${member_oid}/contract`,
      method: 'GET',
      params: { contract_oid },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getBaseBenefitPlan () {
    return axios({
      url: `/benefit-plan/base`,
      method: 'GET',
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getBenefitPlanTree ({ contract_oid }) {
    return axios({
      url: `/benefit-plan/tree/${contract_oid}`,
      method: 'GET',
      params: { contract_oid },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getMemberBenefitLimit ({ member_oid, contract_oid }) {
    return axios({
      url: `/member/${member_oid}/contract/limit`,
      method: 'GET',
      params: { contract_oid },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getMemberFindBenefit ({ member_oid, contract_oid, tree_oid, is_exact }) {
    return axios({
      url: `/member/${member_oid}/contract/limit`,
      method: 'GET',
      params: { contract_oid, tree_oid, is_exact },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  static getMemberUtilizationLoa ({ member_account_no }) {
    return axios({
      url: `/member/utilization-loa`,
      method: 'GET',
      params: { member_account_number: member_account_no },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }

  // PRO User lookup
  static proUsersLookup ({ value, field }) {
    return axios({
      url: '/users/pro',
      method: 'GET',
      params: { [field]: value }
    })
  }
}
