import React from 'react'

// Components
import Typography from '@material-ui/core/Typography'
import SideBar from './Components/SideBar/SideBar'
import LimitTable from './Components/LimitTable/LimitTable'
import Header from './Components/Header/Header'
import Footer from './Components/Footer/Footer'

// Utilities
import { compare, includesAny } from 'Utilities/ArrayCompare'
// import { BenefitNode } from 'Utilities/BenefitNode'
import { BenefitNode2 } from 'Utilities/BenefitNode2'

// Styles
import classes from './Benefits.module.scss'

export default function Benefits (props) {
  // Events

  // Variables
  const { contractType, member, client } = props
  // Service

  // Local Variables
  const [selectedPlanType, setSelectedPlanType] = React.useState('BASE')
  const [selectedPlanPackage, setSelectedPlanPackage] = React.useState('RB')
  const [selectedBenefitCategory, setSelectedBenefitCategory] = React.useState('RBC')

  const [showAll, setShowAll] = React.useState(true)

  const [benefitRows, setBenefitRows] = React.useState([])

  const [exclusionList, setExclusionList] = React.useState([])
  const [contractNodes, setContractNodes] = React.useState([])
  const [planTypeNodes, setPlanTypeNodes] = React.useState([])
  const [planPackageNodes, setPlanPackageNodes] = React.useState([])
  const [benefitCategoryNodes, setBenefitCategoryNodes] = React.useState([])
  const [benefitTypeNodes, setBenefitTypeNodes] = React.useState([])
  const [mainBenefitNodes, setMainBenefitNodes] = React.useState([])
  const [subBenefitNodes, setSubBenefitNodes] = React.useState([])

  const [contractId, setContractId] = React.useState('')

  React.useEffect(() => {
    if (contractType === 'client') {
      setContractId('---')
      if (client.contract.base.length <= 0 || client.contract.benefits.length <= 0) return

      console.log(client)

      const baseNodes = BenefitNode2.convertNodeData(client.contract.base) // temporary handling if no result from endpoint
      const contractNodes = BenefitNode2.convertNodeData(client.contract.benefits, true) // temporary handling if member have no benefit
      const nodes = BenefitNode2.combineBaseBenefitNodes(baseNodes, contractNodes)

      const eList = BenefitNode2.createExclusionList(nodes)

      setExclusionList(eList)

      setContractNodes(BenefitNode2.getFilteredNodes(nodes, 2))
      setPlanTypeNodes(BenefitNode2.getFilteredNodes(nodes, 3))
      setPlanPackageNodes(BenefitNode2.getFilteredNodes(nodes, 4))
      setBenefitCategoryNodes(BenefitNode2.getFilteredNodes(nodes, 5))
      setBenefitTypeNodes(BenefitNode2.getFilteredNodes(nodes, 6))
      setMainBenefitNodes(BenefitNode2.getFilteredNodes(nodes, 7))
      setSubBenefitNodes(BenefitNode2.getFilteredNodes(nodes, 8))

      setSelectedPlanType('BASE')
      setSelectedPlanPackage('RB')
      setSelectedBenefitCategory('RBC')
    } else if (contractType === 'member') {
      if (member.info.contracts) setContractId(member.contracts.slice().pop())
      // ALGO HERE
    }
  }, [contractType, member, client])

  React.useEffect(() => {
    setBenefitRows(getRows(selectedPlanPackage, selectedBenefitCategory))
  }, [selectedPlanType, selectedPlanPackage, selectedBenefitCategory])

  const getContractNode = () => {
    return BenefitNode2.getContractNode(contractNodes)
  }

  const getCategoryNode = () => {
    return BenefitNode2.getCategoryNode(
      planPackageNodes,
      benefitCategoryNodes,
      selectedPlanType,
      selectedPlanPackage,
      selectedBenefitCategory
    )
  }

  const getPlanTypeList = () => {
    return BenefitNode2.getPlanTypeList(planTypeNodes, mainBenefitNodes, benefitTypeNodes, subBenefitNodes)
  }

  const getRows = (planPackage, benefitCategory) => {
    return BenefitNode2.getRows(
      exclusionList,
      benefitCategoryNodes,
      benefitTypeNodes,
      mainBenefitNodes,
      subBenefitNodes,
      selectedPlanType,
      planPackage,
      benefitCategory
    )
  }

  const getRoomAndBoardList = () => {
    return BenefitNode2.getSidebarList(benefitCategoryNodes, selectedPlanType, 'RB')
  }

  const getCoverageList = () => {
    return BenefitNode2.getSidebarList(benefitCategoryNodes, selectedPlanType, 'CV')
  }

  const getRiderList = () => {
    return BenefitNode2.getSidebarList(benefitCategoryNodes, selectedPlanType, 'RI')
  }

  const handleItemClick = args => {
    const { planPackage, benefitCategory } = args
    setSelectedPlanPackage(planPackage)
    setSelectedBenefitCategory(benefitCategory)
  }

  return (
    <div className={classes.root}>
      <div className={classes.wrapper}>
        <div className={classes['left-side']}>
          <Typography className={classes.title}>Member Benefits</Typography>
          <SideBar
            contractId={contractId}
            planTypeList={getPlanTypeList()}
            contractNode={getContractNode()}
            categoryNode={getCategoryNode()}
            roomAndBoardList={getRoomAndBoardList()}
            coverageList={getCoverageList()}
            riderList={getRiderList()}
            onItemClick={handleItemClick}
          />
        </div>

        <div className={classes['right-side']}>
          <Header
            contractType={contractType}
            showAll={showAll}
            benefitType={selectedBenefitCategory}
            categoryNode={getContractNode()}
          />

          <LimitTable
            contractType={contractType}
            benefitType={selectedBenefitCategory}
            rows={benefitRows}
            contractId={contractId}
            planTypeList={getPlanTypeList()}
            contractNode={getContractNode()}
            categoryNode={getCategoryNode()}
            roomAndBoardList={getRoomAndBoardList()}
            coverageList={getCoverageList()}
            riderList={getRiderList()}
          />

          <Footer
            contractType={contractType}
            showAll={showAll}
            benefitType={selectedBenefitCategory}
            categoryNode={getContractNode()}
          />
        </div>
      </div>
    </div>
  )
}
