import React from 'react'
import { Provider } from 'react-redux'
import { Switch, Route } from 'react-router-dom'

// Pages of Medical Consultant
import Dashboard from './Pages/Dashboard/Dashboard'
import ClaimsViewer from '../ClaimsViewer'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import MedicalConsultantServices from 'Services/Api/MedicalConsultantServices'

// Store
import Store from '../ClaimsViewer/Store'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/'
  },
  {
    label: 'Claims',
    icon: 'file-alt',
    to: '/claims'
  }
]

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  claims: {
    text: 'Claims',
    to: '/claims'
  },
  specificClaim: {
    text: data => data,
    to: data => `/claims/${data}`
  },
  review: {
    text: 'Review',
    to: ''
  }
}

const permissions = {
  base: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  diagnosis: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  procedures: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  hospital_items: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  professional_fees: {
    create: false,
    read: true,
    update: false,
    delete: false,
    options: {}
  },
  tabs: {
    benefits: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    partner: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    utilization: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    contact: {
      create: false,
      read: true,
      update: false,
      delete: false,
      options: {}
    },
    comments: {
      create: true,
      read: true,
      update: false,
      delete: false,
      options: {
        confirm: true,
        confirmMessage: 'Are you sure you want to Reply to the Escalated Claim?',
        okLabel: 'Yes',
        cancelLabel: 'No'
      }
    }
  },
  footer: {
    visible: true,
    enabled: true
  }
}

function MedicalConsultantRoutes () {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <Switch>
      <Route path='/' exact component={Dashboard} />
      <Provider store={Store}>
        <Route
          path='/claims/review/:claim_number'
          exact
          render={props => (
            <ClaimsViewer
              breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'review']}
              {...props}
              permissions={permissions}
              ApiService={MedicalConsultantServices}
            />
          )}
        />
      </Provider>
    </Switch>
  )
}

export default MedicalConsultantRoutes
