import React from 'react'

import ValueField from '../ValueField/ValueField'
import { Typography } from '@material-ui/core'

import { DigitFormatter, UpperWord } from 'Utilities/TextFormatter'
import { SafeAccess } from 'Utilities/SafeAccess'

import { UnitLimit } from '../../Utilities/FieldFormatter'

import classes from './Footer.module.scss'

const populateClient = args => {
  const { showAll, benefitType, categoryNode } = args

  const metadata = SafeAccess(categoryNode, ['custom_metadata'], {})

  let billingArrangement = {}
  let amountLimit = {}

  switch (benefitType) {
    case 'APE':
      return (
        <React.Fragment>
          <div className={classes.groups}>
            {(metadata['mobile_ape_availees'] || showAll) && (
              <React.Fragment>
                <Typography variant='overline' display='block' gutterBottom>
                  Mobile APE
                </Typography>
                <ValueField
                  label={'Min no. of availees in a shift (8 hrs)'}
                  value={`${metadata['mobile_ape_availees']}`}
                />
              </React.Fragment>
            )}
          </div>
        </React.Fragment>
      )

    case 'CC':
      billingArrangement = {
        premium: 'Imputed in premiums',
        fixed_rate_rider: 'Scheduled fixed rate for the entire rider',
        fixed_rate_personnel: 'Scheduled fixed rate per personnel deployed',
        soa_rate: 'Scheduled SOA based on fixed rate/hour/personnel deployed',
        soa_actual_cost: 'Scheduled SOA based on actual cost of personnel deployed',
        rider_fee: '1-time rider fee to be upon start of contract'
      }
      return (
        <React.Fragment>
          <div className={classes.groups}>
            {(metadata['billing_arangement'] || showAll) && (
              <ValueField label={'BILLING ARRANGEMENT'} value={billingArrangement[metadata['billing_arangement']]} />
            )}
          </div>
        </React.Fragment>
      )

    case 'DNT':
      return (
        <React.Fragment>
          <div className={classes.groups}>
            {((metadata['waiting_period_unit'] && metadata['waiting_period_type']) || showAll) && (
              <ValueField
                label={'Activation'}
                value={`${metadata['waiting_period_type']} ${metadata['waiting_period_unit']}`}
              />
            )}
          </div>
        </React.Fragment>
      )

    case 'EC':
      billingArrangement = {
        imputed_in_premiums: 'Imputed in premiums',
        billback: 'Bill back with admin fee',
        billback_net: 'Bill back, net of APE cost with admin fee',
        rider_fee: '1-time rider fee to be upon enrollment'
      }

      return (
        <React.Fragment>
          <div className={classes.groups}>
            {((metadata['waiting_period_unit'] && metadata['waiting_period_type']) || showAll) && (
              <ValueField
                label={'Activation'}
                value={`${metadata['waiting_period_type']} ${metadata['waiting_period_unit']}`}
              />
            )}
            {([metadata['billing_arangement']], showAll) && (
              <ValueField label={'BILLING ARRANGEMENT'} value={billingArrangement[metadata['billing_arangement']]} />
            )}
          </div>
        </React.Fragment>
      )

    case 'EM':
      return <React.Fragment />

    case 'IP':
      amountLimit = {
        amt_limit: 'Amount Limit',
        max_session: 'Max Session',
        max_amount: 'Max Amount',
        percent_mbl: 'Percent of MBL',
        percent_abl: 'Percent of ABL',
        no_visits: 'No. of Visits'
      }

      return (
        <React.Fragment>
          <div className={classes.groups}>
            {((metadata['amt_limit'] && metadata['amt_limit_unit']) || showAll) && (
              <Typography variant='overline' display='block' gutterBottom>
                Limit
              </Typography>
            )}
            {([metadata['mobile_ape_availees']], showAll) && (
              <ValueField
                label={'Amount Limit for all procedures'}
                value={`${metadata['amt_limit']} ${amountLimit[metadata['amt_limit_unit']]}`}
              />
            )}
          </div>
        </React.Fragment>
      )

    case 'MT':
      billingArrangement = {
        premium: 'Imputed in premiums',
        billback: 'Bill back with admin fee (15%)',
        rider_fee: '1-time rider fee to be upon enrollment'
      }

      return (
        <React.Fragment>
          <div className={classes.groups}>
            {(metadata['availment_per_year'] || showAll) && (
              <ValueField label={'AVAILMENT PER YEAR'} value={`${metadata['availment_per_year'] || ''}`} />
            )}
            {([metadata['waiting_period_type'], metadata['waiting_period_unit']], showAll) && (
              <ValueField
                label={'Activation'}
                value={`${metadata['waiting_period_type']} ${metadata['waiting_period_unit']}`}
              />
            )}
            {([billingArrangement[metadata['billing_arangement']]], showAll) && (
              <ValueField label={'BILLING ARRANGEMENT'} value={billingArrangement[metadata['billing_arangement']]} />
            )}
          </div>
        </React.Fragment>
      )

    case 'OP':
      amountLimit = {
        amt_limit: 'Amount Limit',
        max_session: 'Max Session',
        max_amount: 'Max Amount',
        percent_mbl: 'Percent of MBL',
        percent_abl: 'Percent of ABL',
        no_visits: 'No. of Visits'
      }

      return (
        <React.Fragment>
          <div className={classes.groups}>
            {((metadata['amt_limit'] && metadata['amt_limit_unit']) || showAll) && (
              <React.Fragment>
                <Typography variant='overline' display='block' gutterBottom>
                  Limit
                </Typography>
                <ValueField
                  label={'Amount Limit for all procedures'}
                  value={`${metadata['amt_limit']} ${amountLimit[metadata['amt_limit_unit']]}`}
                />
              </React.Fragment>
            )}
          </div>
        </React.Fragment>
      )

    case 'OM':
      return (
        <React.Fragment>
          <div className={classes.groups}>
            {((metadata['limit_for_all'] && metadata['limit_for_all_unit']) || showAll) && (
              <ValueField
                label={'Limit for All'}
                value={`${metadata['limit_for_all']} ${metadata['limit_for_all_unit']}`}
              />
            )}
          </div>
          <div className={classes.groups}>
            {((metadata['waiting_period'] && metadata['waiting_period_unit']) || showAll) && (
              <ValueField
                label={'Waiting period after effectivity of member'}
                value={`${metadata['waiting_period']} ${metadata['waiting_period_unit']}`}
              />
            )}
          </div>
        </React.Fragment>
      )

    case 'NT':
      return <React.Fragment />

    case 'PPE':
      return <React.Fragment />

    case 'PV':
      return <React.Fragment />

    case 'RBC':
      let voluntaryValue = ''
      // let involuntaryValue = ''
      if (metadata['voluntary_upgrading_type']) {
        if (metadata['voluntary_upgrading_type'] === 'paid_by_member') {
          voluntaryValue = 'Room and Board difference is paid by the Member'
        } else if (
          metadata['voluntary_upgrading_type'] === 'first_hours_free' &&
          metadata['voluntary_upgrading_amount']
        ) {
          voluntaryValue = metadata['voluntary_upgrading_amount'] + ' hrs'
        } else if (metadata['voluntary_upgrading_type'] === 'rate' && metadata['voluntary_upgrading_amount']) {
          voluntaryValue = metadata['voluntary_upgrading_amount'] + ' %'
        }
      }
      if (metadata['involuntary_upgrading_type']) {
        if (metadata['involuntary_upgrading_type'] === 'paid_by_member') {
          // involuntaryValue = 'Room and Board difference is paid by the Member'
        } else if (
          metadata['involuntary_upgrading_type'] === 'first_hours_free' &&
          metadata['involuntary_upgrading_amount']
        ) {
          // involuntaryValue = metadata['involuntary_upgrading_amount'] + ' hrs'
        }
      }
      return (
        <React.Fragment>
          <div className={classes.groups}>
            {(metadata['voluntary_upgrading_type'] || showAll) && (
              <React.Fragment>
                <ValueField label={'Voluntary Room Upgrading'} value={voluntaryValue} />
              </React.Fragment>
            )}
          </div>
        </React.Fragment>
      )

    case 'SD':
      return (
        <React.Fragment>
          <div className={classes.groups}>
            {((metadata['limit_for_all'] && metadata['limit_for_all_unit']) || showAll) && (
              <React.Fragment>
                <Typography variant='overline' display='block' gutterBottom>
                  Limit
                </Typography>
                <ValueField
                  label={'Peso Limit for all procedures'}
                  value={`${metadata['limit_for_all']} ${metadata['limit_for_all_unit']}`}
                />
              </React.Fragment>
            )}
          </div>
        </React.Fragment>
      )

    case 'SH':
      return <React.Fragment />
  }
}

const populateMember = args => {}

export default function Footer (props) {
  const { contractType } = props
  const [elemFields, setElemFields] = React.useState([])

  React.useEffect(() => {
    if (contractType === 'client') {
      setElemFields(populateClient(props))
    } else if (contractType === 'member') {
      setElemFields(populateMember(props))
    }
  }, [props])

  return (
    <div className={classes.root}>
      <div className={classes.wrapper}>{elemFields}</div>
    </div>
  )
}
