const initialState = {
  items: [],
  selected: null
}
const bundlesReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SET_BUNDLE_ITEMS':
      return {
        ...state,
        items: action.payload
      }

    case 'ADD_BUNDLE':
      return {
        ...state,
        items: [action.payload, ...state.items]
      }

    case 'SET_SELECTED_BUNDLE':
      return {
        ...state,
        selected: action.payload
      }

    case 'SET_BUNDLE_BATCHES':
      return {
        ...state,
        selected: { ...state.selected, batches: action.payload }
      }

    case 'ADD_BATCH':
      const { batches } = state.selected
      const { items } = batches

      return {
        ...state,
        selected: { ...state.selected, batches: { ...batches, items: [action.payload, ...items] } }
      }

    default:
      return state
  }
}

export default bundlesReducer
