import React from 'react'
import Tooltip from '@material-ui/core/Tooltip'

// Compoents
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'

// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Typography from '@material-ui/core/Typography'
// import Popover from '@material-ui/core/Popover'

import classes from './HospitalItemTextField.module.scss'

const HospitalItemTextFields = ({ children }) => {
  return (
    <div className={classes.root}>
      {/* Headers */}
      <div className={classes.header}>
        <Typography variant='subtitle2' className={classes.description}>
          Item Name *
        </Typography>

        <Typography variant='subtitle2' className={classes.code}>
          Item Code *
        </Typography>
      </div>

      {children}
    </div>
  )
}

const HospitalItemTextField = props => {
  const {
    id = '',
    codeId = '',
    name = '',
    item_code = '',
    errors,
    suggestions = [],
    onChangeName,
    onChangeCode = () => {}
  } = props

  const [tooltipOpen, setTooltipOpen] = React.useState(false)
  const handleTooltipOpen = () => {
    setTooltipOpen(!!name.trim())
  }

  return (
    <div className={classes.wrapper}>
      <div className={classes['sub-wrapper']}>
        <Tooltip onClose={() => setTooltipOpen(false)} onOpen={handleTooltipOpen} open={tooltipOpen} title={name}>
          <div className={`${classes['description']}`}>
            <AutoComplete
              id={id}
              required
              value={name}
              suggestions={suggestions.name}
              onChange={onChangeName}
              error={errors.name}
            />
          </div>
        </Tooltip>

        <div className={`${classes['code']}`}>
          <AutoComplete
            id={codeId}
            required
            value={item_code}
            suggestions={suggestions.item_code}
            onChange={onChangeCode}
            error={errors.item_code}
          />
        </div>
      </div>
    </div>
  )
}

export { HospitalItemTextFields, HospitalItemTextField }
