import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import { useHistory } from 'react-router-dom'

// Styles
import classes from './Error.module.scss'

let history = null

const DashboardRedirect = props => {
  const { title = `Page not found`, body = `We cant seem to find the page you're looking for.` } = props
  const [counter, setCounter] = React.useState()

  React.useEffect(() => {
    let _counter = 10
    setCounter(_counter)
    let interval = setInterval(() => {
      if (_counter) {
        _counter--
        setCounter(_counter)
      } else {
        clearInterval(interval)
        history.push('/')
      }
    }, 1000)

    return () => clearInterval(interval)
  }, [])

  return (
    <div className={classes.root}>
      <Typography variant='h4' className={classes.title}>
        {title}
      </Typography>
      <Typography variant='body1' className={classes.message}>
        {body}
      </Typography>

      <div className={classes['action-messages']}>
        <Typography variant='body2' className={classes.message}>
          You will be redirected to Dashboard in {counter}
        </Typography>
        <Typography variant='body2' className={classes.message}>
          Click the button below to go to that page.
        </Typography>
      </div>

      <Button variant='contained' color='primary' className='button-neutral' onClick={() => history.push('/')}>
        Go to Dashboard
      </Button>
    </div>
  )
}

const DashboardRedirectInit = props => {
  history = useHistory()
  return ''
}

const DashboardRedirectService = {
  redirect: () => {
    history.push('/404')
  }
}

export { DashboardRedirectInit, DashboardRedirect, DashboardRedirectService }
