import React from 'react'
import Radio from '@material-ui/core/Radio'
import Tooltip from '@material-ui/core/Tooltip'
import IconButton from '@material-ui/core/IconButton'
import DeleteIcon from '@material-ui/icons/Delete'
import Alert from 'SharedComponents/Alert/Alert'

// Compoents
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'

import Typography from '@material-ui/core/Typography'

// Styles
import classes from './ICDTextField.module.scss'

const ICDTextFields = ({ children }) => {
  return (
    <div className={classes.root}>
      {/* Headers */}
      <div className={classes.header}>
        <div className={classes.actions} />

        <Typography variant='subtitle2' className={classes.description}>
          ICD10 Description *
        </Typography>

        <Typography variant='subtitle2' className={classes.code}>
          ICD10 Code *
        </Typography>

        <Typography variant='subtitle2' className={classes.primary}>
          Primary
        </Typography>
      </div>

      {children}
    </div>
  )
}

const ICDTextField = props => {
  const {
    id = '',
    codeId = '',
    description = '',
    code = '',
    primary = false,
    suggestions = { description: [], code: [] },
    onChangeDescription,
    onChangeCode,
    onChangePrimary,
    errors = { description: '', code: '' },
    onDeleteDiagnosis
  } = props

  const [tooltipOpen, setTooltipOpen] = React.useState(false)
  const handleTooltipOpen = () => {
    setTooltipOpen(!!description.trim())
  }

  const [openDialog, setOpenDialog] = React.useState(false)

  return (
    <React.Fragment>
      <div className={classes.wrapper}>
        <div className={classes['sub-wrapper']}>
          <div className={classes.actions}>
            <IconButton id='delete-icd' onClick={() => setOpenDialog(true)}>
              <DeleteIcon fontSize='small' />
            </IconButton>
          </div>

          <Tooltip
            onClose={() => setTooltipOpen(false)}
            onOpen={handleTooltipOpen}
            open={tooltipOpen}
            title={description}
          >
            {/* {console.log('error ', errors)} */}
            <div className={`${classes['description']}`}>
              <AutoComplete
                id={id}
                value={description}
                suggestions={suggestions.description}
                onChange={(value, isSuggestionClick) => onChangeDescription({ value, isSuggestionClick })}
                error={errors.description}
              />
            </div>
          </Tooltip>

          <div className={`${classes['code']}`}>
            <AutoComplete
              id={codeId}
              value={code}
              suggestions={suggestions.code}
              onChange={(value, isSuggestionClick) => onChangeCode({ value, isSuggestionClick })}
              error={errors.code}
            />
          </div>

          <div className={classes.primary}>
            <Radio color='primary' checked={primary} onClick={onChangePrimary} />
          </div>
        </div>
      </div>

      <Alert
        open={openDialog}
        message='Are you sure you want to delete this diagnosis?'
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setOpenDialog(false)}
        onOk={() => {
          setOpenDialog(false)
          onDeleteDiagnosis()
        }}
      />
    </React.Fragment>
  )
}

export { ICDTextFields, ICDTextField }
