import { setClaimBaseDetails, setDropDown, setClaimFacility, addSuggestionToArray } from '../Store/actions'

export default function InPatient ({ common, loa, dispatch }) {
  let details = {
    ...common,
    date_from: loa.loa_details[0].date.from,
    date_to: loa.loa_details[0].date.to,
    room_type: loa.loa_details[0].room.type,
    room_rate: loa.loa_details[0].room.rate,
    pro_id: loa.loa_details[0].pro.id,
    pro_name: loa.loa_details[0].pro.name,
    claim_sub_type: loa.loa_details[0].claim_sub_type ? loa.loa_details[0].claim_sub_type : '',
    claim_sub_sub_type: loa.loa_details[0].claim_sub_sub_type ? loa.loa_details[0].claim_sub_sub_type : '',
    with_corporate_guarantee: loa.has_corp_guarantee ? 'Yes' : 'No',
    diagnosis: loa.loa_details[0].diagnosis,
    loa_notes: loa.remarks,
    procedures: loa.loa_details[0].procedures,
    hospital_items: loa.loa_details[0].hospital_items,
    professional_fees: loa.loa_details[0].professional_fees
  }

  for (let i in details) dispatch(setClaimBaseDetails({ field: i, value: details[i] }))

  dispatch(setClaimFacility({ facility_id: loa.loa_details[0].medical_partner_id }))
  dispatch(setClaimFacility({ facility_name: loa.loa_details[0].medical_partner_name }))
  console.log(loa.rooms)
  dispatch(setDropDown({ rooms: loa.rooms }))

  // Set Diagnosis Suggestions
  details.diagnosis.map(i => dispatch(addSuggestionToArray({ field: 'icd10' })))
}
