export const initSuggestions = payload => {
  return {
    type: 'INIT_SUGGESTIONS',
    payload
  }
}

export const setSuggestionObject = payload => {
  return {
    type: 'SET_SUGGESTION_OBJECT',
    payload
  }
}

export const setSuggestionOnArray = payload => {
  return {
    type: 'SET_SUGGESTION_ON_ARRAY',
    payload
  }
}

export const addSuggestionToArray = payload => {
  return {
    type: 'ADD_SUGGESTION_TO_ARRAY',
    payload
  }
}

export const removeSuggestionToArray = payload => {
  return {
    type: 'REMOVE_SUGGESTION_TO_ARRAY',
    payload
  }
}

export const resetSuggestionObject = payload => {
  return {
    type: 'RESET_SUGGESTION_OBJECT',
    payload
  }
}

export const resetSuggestionArray = payload => {
  return {
    type: 'RESET_SUGGESTION_ARRAY',
    payload
  }
}
