export const setSelectedBatch = payload => {
  return {
    type: 'SET_SELECTED_BATCH',
    payload
  }
}

export const setBatchAction = payload => {
  return {
    type: 'SET_BATCH_ACTION',
    payload
  }
}

export const setShowBatchForm = payload => {
  return {
    type: 'SET_SHOW_BATCH_FORM',
    payload
  }
}

export const setSaveBatchControl = payload => {
  return {
    type: 'SET_SAVE_BATCH_CONTROL',
    payload
  }
}
