import React from 'react'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import startCase from 'lodash/startCase'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import TaskTray from 'SharedComponents/TaskTrayComponent/TaskTrayComponent'
import TicketList from 'SharedComponents/TicketListComponent/TicketListComponent'
import ViewList from '../../Components/ViewList/ViewList'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsEncodingServices from 'Services/Api/ClaimsEncodingServices'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './Dashboard.module.scss'

export default function Dashboard (props) {
  const [batches, setBatches] = React.useState({
    open: { count: 0, data: [] },
    my: { count: 0, data: [] },
    completed: { count: 0, data: [] }
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  const [openTickets] = React.useState({ count: 0, data: [] })

  React.useEffect(() => {
    PageTitleService('Dashboard')
    BreadcrumbsService.setSelectedItems(['dashboard'])

    //* Open Batches
    Promise.all([
      ClaimsEncodingServices.getBatches({
        category: 'open',
        limit: 10,
        sort: 'date.due|desc',
        range: 'this-week'
      }),
      ClaimsEncodingServices.getBatchCount({ category: 'open' })
    ]).then(values => {
      const [batchResponse, batchCountResponse] = values
      const openBatches = batchResponse.data.data.results
      const openBatchCount = batchCountResponse.data.data
      setBatches(prevState => ({ ...prevState, open: { data: openBatches, count: openBatchCount } }))
    })

    //* My Batches
    Promise.all([
      ClaimsEncodingServices.getBatches({
        category: 'my',
        limit: 10,
        sort: 'date.due|desc',
        range: 'this-week'
      }),
      ClaimsEncodingServices.getBatchCount({ category: 'my' })
    ]).then(values => {
      const [batchResponse, batchCountResponse] = values
      const myBatches = batchResponse.data.data.results
      const myBatchCount = batchCountResponse.data.data
      setBatches(prevState => ({ ...prevState, my: { data: myBatches, count: myBatchCount } }))
    })

    //* Completed Batches
    Promise.all([
      ClaimsEncodingServices.getBatches({
        category: 'completed',
        limit: 10,
        sort: 'date.due|desc',
        range: 'this-week'
      }),
      ClaimsEncodingServices.getBatchCount({ category: 'completed' })
    ]).then(values => {
      const [batchResponse, batchCountResponse] = values
      const completedBatches = batchResponse.data.data.results
      const completedBatchCount = batchCountResponse.data.data
      setBatches(prevState => ({ ...prevState, completed: { data: completedBatches, count: completedBatchCount } }))
    })
  }, [])

  const handleFilterChange = category => value => {
    ClaimsEncodingServices.getBatches({ category, range: value, limit: 10, sort: 'date.due|desc' }).then(
      ({ data: res }) => {
        const { data } = res
        setBatches(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
      }
    )
  }

  const handleOnEncode = batch_number => {
    ClaimsEncodingServices.assignBatch({ batch_number }).then(({ data: res }) => {
      props.history.push(`/batches/${batch_number}`)
    })
  }

  const handleOnShowViewList = category => () => {
    setViewList({ ...initialValue, show: true, title: startCase(`${category} Batches`), category })
    ClaimsEncodingServices.getBatches({ category, limit: 10, sort: 'date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: viewList.category,
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    ClaimsEncodingServices.getBatches(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnClickItemViewList = ({ category, batch_number }) => {
    if (category === 'open') handleOnEncode(batch_number)
    else if (category === 'my' || category === 'completed') props.history.push(`/batches/${batch_number}`)
  }

  return (
    <div className={classes.root}>
      <Grid container className={classes['tiles']} spacing={3} direction='row'>
        <Grid item className={classes['task-tray']}>
          <TaskTray sizes={{ xs: 12 }}>
            <DashboardTile
              id='open-batches'
              count={{ color: 3, value: batches.open.count, id: 'open-batches-count' }}
              title='Open Batches'
              viewList={
                <Typography
                  id='ob-view-list'
                  variant='caption'
                  className='bold text-link'
                  onClick={handleOnShowViewList('open')}
                >
                  View List
                </Typography>
              }
              onFilterChange={handleFilterChange('open')}
            >
              <Table size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell id='ob-batch-no'>Batch No.</TableCell>
                    <TableCell id='ob-due-date'>Due Date</TableCell>
                    <TableCell id='ob-facility-name'>Facility Name</TableCell>
                    <TableCell />
                  </TableRow>
                </TableHead>
                <TableBody>
                  {batches.open.data.map((item, index) => (
                    <TableRow key={item.batch_number}>
                      <TableCell id={`ob-batch-no-${index}`}>{item.batch_number}</TableCell>
                      <TableCell id={`ob-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                      <TableCell id={`ob-facility-name-${index}`}>{item.facility.name}</TableCell>
                      <TableCell>
                        <Typography
                          id={`encode-batch-${index}`}
                          variant='caption'
                          className={`text-link underline`}
                          onClick={() => handleOnEncode(item.batch_number)}
                        >
                          Encode
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </DashboardTile>

            <DashboardTile
              id='my-batches'
              count={{ color: 8, value: batches.my.count, id: 'my-batches-count' }}
              title='My Batches'
              viewList={
                <Typography
                  id='mb-view-list'
                  variant='caption'
                  className='bold text-link'
                  onClick={handleOnShowViewList('my')}
                >
                  View List
                </Typography>
              }
              onFilterChange={handleFilterChange('my')}
            >
              <Table size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell id='mb-batch-no'>Batch No.</TableCell>
                    <TableCell id='mb-due-date'>Due Date</TableCell>
                    <TableCell id='mb-facility-name'>Facility Name</TableCell>
                    <TableCell id='mb-status'>Status</TableCell>
                    <TableCell />
                  </TableRow>
                </TableHead>
                <TableBody>
                  {batches.my.data.map((item, index) => (
                    <TableRow key={item.batch_number}>
                      <TableCell id={`mb-batch-no-${index}`}>{item.batch_number}</TableCell>
                      <TableCell id={`mb-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                      <TableCell id={`mb-facility-name-${index}`}>{item.facility.name}</TableCell>
                      <TableCell id={`mb-status-${index}`}>In Progress</TableCell>
                      <TableCell>
                        <Typography
                          id={`update-batch-${index}`}
                          variant='caption'
                          className={`text-link underline`}
                          onClick={() => props.history.push(`/batches/${item.batch_number}`)}
                        >
                          Update
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </DashboardTile>

            <DashboardTile
              id='completed-batches'
              count={{ color: 5, value: batches.completed.count, id: 'completed-batches-count' }}
              title='Completed Batches'
              viewList={
                <Typography
                  id='cb-view-list'
                  variant='caption'
                  className='bold text-link'
                  onClick={handleOnShowViewList('completed')}
                >
                  View List
                </Typography>
              }
              onFilterChange={handleFilterChange('completed')}
            >
              <Table size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell id='cb-batch-no'>Batch No.</TableCell>
                    <TableCell id='cb-due-date'>Due Date</TableCell>
                    <TableCell id='cb-facility-name'>Facility Name</TableCell>
                    <TableCell />
                  </TableRow>
                </TableHead>
                <TableBody>
                  {batches.completed.data.map((item, index) => (
                    <TableRow key={item.batch_number}>
                      <TableCell id={`cb-batch-no-${index}`}>{item.batch_number}</TableCell>
                      <TableCell id={`cb-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                      <TableCell id={`cb-facility-name-${index}`}>{item.facility.name}</TableCell>
                      <TableCell>
                        <Typography
                          id={`view-batch-${index}`}
                          variant='caption'
                          className={`text-link underline`}
                          onClick={() => props.history.push(`/batches/${item.batch_number}`)}
                        >
                          View
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </DashboardTile>
          </TaskTray>
        </Grid>

        <Grid item className={classes['ticket-list']}>
          <TicketList
            count={openTickets.count}
            title='Open Tickets'
            viewAllLink={{ to: '/batches', caption: 'View all' }}
          >
            <Table size='small'>
              <TableHead>
                <TableRow>
                  <TableCell>Ticket #</TableCell>
                  <TableCell>Date</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {openTickets.data.map((item, index) => (
                  <TableRow key={index}>
                    <TableCell>2334324343</TableCell>
                    <TableCell>08/12/2019</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TicketList>
        </Grid>
      </Grid>
      <ViewList
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        onItemClick={handleOnClickItemViewList}
      />
    </div>
  )
}
