import React from 'react'
import Grid from '@material-ui/core/Grid'

import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'

//  Components
import BundleDetails from '../../Components/BundleDetails/BundleDetails'
import BatchList from '../../Components/BatchList/BatchList'
import BatchForm from '../../Components/BatchForm/BatchForm'
import SearchTextComponent from 'SharedComponents/SearchTextComponent'
import Alert from 'SharedComponents/Alert/Alert'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import BatchEncodingServices from 'Services/Api/BatchEncodingServices'

// Style
import classes from './BundlePage.module.scss'
import Typography from '@material-ui/core/Typography'

// Store
import { useSelector, useDispatch } from 'react-redux'
import {
  setShowBatchForm,
  setSelectedBatch,
  setBatchAction,
  initSuggestions,
  resetSuggestionObject,
  setSuggestionObject,
  setSaveBatchControl
} from '../../Store/actions'

// Validator
import BundleValidator from './BundleValidator'

export default function BundlePage (props) {
  const dispatch = useDispatch()

  const suggestions = useSelector(state => state.suggestions)

  const [originalState, setOriginalState] = React.useState({})
  const [bundle, setBundle] = React.useState(null)
  const [batches, setBatches] = React.useState([])
  const [isReadOnly, setIsReadOnly] = React.useState(true)
  const [openDialog, setOpenDialog] = React.useState(false)
  const [openBundleDialog, setOpenBundleDialog] = React.useState({})
  const [openError, setOpenError] = React.useState(false)
  const [submitStatus, setsubmitStatus] = React.useState(false)
  const { bundle_number } = props.match.params
  const [disableComponents, setDisableComponents] = React.useState(false)
  const [errors, setErrors] = React.useState({})
  const [disableControls] = React.useState(false)
  const [forDeletion, setForDeletion] = React.useState({ batch_numbers: [] })
  const [disabledSubmit, setDisabledSubmit] = React.useState(true)

  React.useEffect(() => {
    LoadingService.loading(true)
    getBundleDetails(bundle_number)
    dispatch(initSuggestions())
  }, [props.match.params.bundle_number])

  React.useEffect(() => {
    setDisabledSubmit(batches.length <= 0)
  }, [batches])

  const getBundleDetails = bundle_number => {
    BatchEncodingServices.getBundle({ bundle_number }).then(({ data: res }) => {
      const { data } = res

      let _status = {
        text: 'In Progress',
        color: 'blue'
      }

      if (data.status === 'SUBMITTED') {
        _status = {
          text: 'Submitted',
          color: 'green'
        }
        setsubmitStatus(true)
      }

      PageTitleService(`Bundle No. ${bundle_number}`, {
        text: _status.text,
        color: _status.color
      })
      BreadcrumbsService.setSelectedItems(['dashboard', `specificBundle:${bundle_number}`])

      setBundle({
        bundle_number,
        total_rcs: data.total_rcs,
        date_received: data.date.received,
        due_date: data.date.due,
        soa_number: data.soa.number,
        total_soa_amt: data.soa.amount,
        facility_id: data.facility.id,
        facility_name: data.facility.name,
        doctor_id: data.doctor.id,
        doctor_name: data.doctor.name,
        status: data.status
      })

      getBatches(bundle_number)
    })
  }

  const getBatches = bundle_number => {
    BatchEncodingServices.getBatches({ bundle_number }).then(({ data: res }) => {
      const { data } = res
      setBatches(data)
      LoadingService.loading(false)
    })
  }

  const createBundle = () => {
    BatchEncodingServices.createBundle().then(res => {
      const { data } = res.data
      props.history.push(`/bundles/${data}/create`)
    })
  }

  const cancelBundle = () => props.history.push(`/`)

  const handleOnSubmit = () => {
    LoadingService.loading(true)

    setOpenBundleDialog(state => ({
      ...state,
      show: false
    }))

    BatchEncodingServices.submitBundle({
      bundle_number: bundle.bundle_number
    }).then(({ data: res }) => {
      LoadingService.loading(false)
      props.history.push(`/`)
    })
  }

  const [debounce, setDebounce] = React.useState({ doctor: null })

  //* Doctor
  const handleOnChangeDoctor = ({ value, field, isSuggestionClick }) => {
    dispatch(setSelectedBatch({ [field]: value }))
    if (isSuggestionClick) {
      for (let i of suggestions.doctor.data) {
        if (value === i.id || value === i.full_name) {
          dispatch(setSelectedBatch({ doctor_id: i.id, doctor_name: i.full_name }))
          dispatch(resetSuggestionObject({ field: 'doctor' }))
          return
        }
      }
    } else {
      dispatch(resetSuggestionObject({ field: 'doctor' }))
      const { 1: by } = field.split('_')
      let _by = by === 'id' ? 'id' : 'full_name'
      getDoctorSuggestions({ value, by: _by })
    }
  }

  const getDoctorSuggestions = ({ value = '', by }) => {
    if (value.trim().length < 3) return

    clearTimeout(debounce.doctor)
    setDebounce(state => ({
      ...state,
      doctor: setTimeout(() => {
        if (value) {
          BatchEncodingServices.lookups({
            on: 'doctor-partner',
            params: { query: value, by }
          }).then(res => {
            const { data } = res.data
            let _items = []
            for (let d of data) _items.push(d[by])
            dispatch(
              setSuggestionObject({
                field: 'doctor',
                suggestion: { [by]: _items, data }
              })
            )
          })
        }
      }, 500)
    }))
  }

  const handleOnSaveBatch = newBatch => {
    const { batch_number, ...data } = newBatch
    dispatch(setSaveBatchControl(true))
    if (batch_number) {
      BatchEncodingServices.updateBatch({
        bundle_number: bundle.bundle_number,
        batch_number,
        data
      }).then(
        ({ data: res }) => {
          const { data } = res
          setBatches(state =>
            state.map(i => {
              if (i.batch_number === batch_number) {
                for (let f in data) i[f] = data[f]
                i.batch_number = data.batch_number
              }
              return i
            })
          )
          dispatch(setSelectedBatch({}))
          dispatch(setShowBatchForm(false))
        },
        () => dispatch(setSaveBatchControl(false))
      )
    } else {
      BatchEncodingServices.createBatch({
        data,
        bundle_number: bundle.bundle_number
      }).then(
        ({ data: res }) => {
          const { data } = res
          setBatches(state => [...state, data])
          dispatch(setShowBatchForm(false))
        },
        () => dispatch(setSaveBatchControl(false))
      )
    }
  }

  const handleOnConfirmDeleteBatch = () => {
    BatchEncodingServices.deleteMultipleBatch({
      bundle_number: bundle_number,
      data: forDeletion
    })
      .then(({ data: res }) => {
        getBatches(bundle_number)
        setOpenDialog(false)
        setForDeletion({ batch_numbers: [] })
      })
      .catch(() => {
        setOpenDialog(false)
        setOpenError(true)
      })
  }

  const handleOnCancelDeleteBatch = () => {
    setOpenDialog(false)
    setOpenError(false)
  }

  const handleOnDeleteBatch = batch => {
    setOpenDialog(true)
  }

  const handleOnEditBatch = batch => {
    dispatch(setSaveBatchControl(false))
    dispatch(setShowBatchForm(true))
    setDisableComponents(true)
    dispatch(setBatchAction('Update Batch'))
    dispatch(
      setSelectedBatch({
        batch_number: batch.batch_number,
        doctor_id: batch.doctor.id,
        doctor_name: batch.doctor.name,
        claim_type: batch.claim.type,
        claim_source: batch.claim.source,
        total_rcs: batch.total_rcs,
        amount_billed: batch.amount_billed,
        notes: batch.notes
      })
    )
  }

  const handleOnchangeBundleDetails = ({ fieldName, fieldValue }) => {
    setBundle(state => ({ ...state, [fieldName]: fieldValue }))
  }

  const handleOnSaveBundleDetails = () => {
    let data = { ...bundle }

    const hasError = validate({
      bundle_number: data.bundle_number,
      total_rcs: data.total_rcs,
      date_received: data.date_received,
      due_date: data.due_date,
      soa_number: data.soa_number,
      total_soa_amt: data.total_soa_amt,
      facility_id: data.facility_id,
      facility_name: data.facility_name,
      doctor_id: data.doctor_id,
      doctor_name: data.doctor_name
    })

    if (hasError) return

    const onOk = () => {
      setOpenBundleDialog(state => ({
        ...state,
        show: false
      }))

      LoadingService.loading(true)

      delete data.status
      BatchEncodingServices.updateBundle({
        data,
        bundle_number: bundle.bundle_number
      }).then(() => {
        LoadingService.loading(false)
        setIsReadOnly(true)
      })
    }

    setOpenBundleDialog(state => ({
      ...state,
      show: true,
      message: 'Are you sure you want to update?',
      handleOnOk: onOk
    }))
  }

  const handleOnCancelBundleDetails = () => {
    setErrors({})
    setBundle(originalState)
    setIsReadOnly(true)
  }

  const handleOnNewBatch = () => {
    dispatch(setSaveBatchControl(false))
    dispatch(setShowBatchForm(true))
    setDisableComponents(false)
    dispatch(setBatchAction('New Batch'))
    dispatch(
      setSelectedBatch({
        batch_number: null,
        doctor_id: bundle.doctor_id,
        doctor_name: bundle.doctor_name,
        claim_type: '',
        claim_source: '',
        total_rcs: 0,
        amount_billed: 0,
        notes: ''
      })
    )
  }

  const validate = value => {
    const { error } = BundleValidator(value)
    setErrors(error || {})
    console.log(error)
    return !!error
  }

  return (
    <React.Fragment>
      {bundle && (
        <div className={classes.root}>
          <BatchForm
            bundle={bundle}
            disableControls={disableControls}
            onSave={handleOnSaveBatch}
            onChangeDoctor={handleOnChangeDoctor}
            disableComponents={disableComponents}
          />

          <Paper className={classes['bundle-details']}>
            <BundleDetails
              details={bundle}
              errors={errors}
              isReadOnly={isReadOnly}
              onChange={handleOnchangeBundleDetails}
            />
            {!submitStatus && (
              <Grid item className={classes['actions']}>
                {isReadOnly && (
                  <React.Fragment>
                    <Button
                      id='add-new-bundle'
                      variant='contained'
                      color='primary'
                      className='button-neutral'
                      onClick={createBundle}
                    >
                      Add New Bundle
                    </Button>
                    <Button
                      id='update-details'
                      variant='contained'
                      color='primary'
                      className='button-neutral'
                      onClick={() => {
                        setOriginalState(bundle)
                        setIsReadOnly(false)
                      }}
                    >
                      Update Details
                    </Button>
                  </React.Fragment>
                )}

                {!isReadOnly && (
                  <React.Fragment>
                    <Button
                      id='cancel-update'
                      variant='contained'
                      color='primary'
                      className='button-negative'
                      onClick={handleOnCancelBundleDetails}
                    >
                      Cancel
                    </Button>

                    <Button
                      id='save'
                      variant='contained'
                      color='primary'
                      className='button-neutral'
                      onClick={handleOnSaveBundleDetails}
                    >
                      Save
                    </Button>
                  </React.Fragment>
                )}
              </Grid>
            )}
          </Paper>
          <Typography className={classes['list-header']} variant='h6'>
            Batches
          </Typography>
          <Paper className={classes['table-container']}>
            <Grid item className={classes['actions']}>
              <SearchTextComponent />
              {bundle.status === 'IN_PROGRESS' && (
                <Button
                  variant='contained'
                  color='primary'
                  className='button-neutral'
                  id='add-new-batch'
                  onClick={handleOnNewBatch}
                >
                  Add New Batch
                </Button>
              )}
            </Grid>
            <BatchList
              batches={batches}
              bundle_number={bundle_number}
              onDelete={handleOnConfirmDeleteBatch}
              openDialog={openDialog}
              onCancel={handleOnCancelDeleteBatch}
              onOpen={handleOnDeleteBatch}
              openError={openError}
              deleteStatus={submitStatus}
              onEdit={handleOnEditBatch}
              forDeletion={forDeletion}
              setForDeletion={setForDeletion}
              isBundleCompleted={bundle && bundle.status && bundle.status === 'SUBMITTED' ? 1 : false}
            />
          </Paper>
          <div align='right' className={classes.buttonWrapper}>
            {bundle && bundle.status && bundle.status !== 'SUBMITTED' && forDeletion.batch_numbers.length > 0 ? (
              <Button
                variant='contained'
                color='primary'
                className={`${'button-negative'} ${classes['delete-button']}`}
                onClick={() => {
                  handleOnDeleteBatch(true)
                }}
                id='cancel'
              >
                Delete
              </Button>
            ) : (
              ''
            )}
            <Button variant='contained' color='primary' className='button-negative' id='cancel' onClick={cancelBundle}>
              Cancel
            </Button>

            {bundle.status === 'IN_PROGRESS' && (
              <Button
                id='submit'
                variant='contained'
                color='primary'
                className='button-positive'
                disabled={disabledSubmit}
                onClick={() =>
                  setOpenBundleDialog(state => ({
                    ...state,
                    show: true,
                    message: 'Are you sure you want to submit?',
                    handleOnOk: handleOnSubmit
                  }))
                }
              >
                Submit
              </Button>
            )}
          </div>
        </div>
      )}

      <Alert
        open={openBundleDialog.show}
        message={openBundleDialog.message}
        cancelLabel='No'
        okLabel='Yes'
        onCancel={() => setOpenBundleDialog(state => ({ ...state, show: false }))}
        onOk={openBundleDialog.handleOnOk}
      />
    </React.Fragment>
  )
}
