const initialValue = {
  show: false,
  mode: null,
  index: null,
  details: {
    description: '',
    code: '',
    tooth_number: '',
    gross_amount: 0,
    vat_amount: 0,
    amount_paid_by_member: 0
  }
}

const dentalProcedureReducer = (state = initialValue, action) => {
  switch (action.type) {
    case 'SET_DENTAL_PROCEDURE':
      return (() => {
        const { details = {}, ...others } = action.payload
        return {
          ...initialValue,
          ...state,
          ...others,
          details: others.mode === 'add' ? initialValue.details : { ...state.details, ...details }
        }
      })()

    default:
      return state
  }
}

export default dentalProcedureReducer
